    'description': 'ההוסטל ״אבני דרך״ לצעירים להט״ב בחולון, הוקם בשנת 2018 בשיתוף פעולה בין עמותת ״אותות - דלת לחיים חדשים״ למשרד העבודה והרווחה, במסגרת סדרת פרויקטים יעודיים למציאת פתרונות דיור לצעירים במצבי סיכון מהקהילה הלהט״בית. מדובר בצעירים חסרי קורת גג ועורף משפחתי. בהוסטל מקום לעד 12 צעירות וצעירים והוא פתוח 24/7 ומיועד לשהייה של חצי שנה עד שנה וחצי. ההוסטל מספק ליווי טיפולי, פעילויות פנאי וחוגים, כמו גם דמי כיס של 100 ש״ח לשבוע בחודשי השהות הראשונים. בנוסף, מותאמת לכל צעירה תוכנית אישית של לימודים ועבודה, הכשרה כלכלית וחברתית.',
