    'created': {
      'by': 'יעל בר צדק',
      'on': '17/05/2018'
    },
    'description': 'אד\'אר - פורום אנשי ונשות מקצוע למאבק בפשעי רצח נשים (ע.ר. 580628055) היא עמותה שהוקמה על ידי קבוצה של אנשי ונשות מקצוע בתחום העבודה הסוציאלית, בריאות הנפש וההנחיה החינוכית בשנת 2011 בעקבות תהליך למידה קבוצתי על רצח נשים. מטרת הקבוצה היתה לחקור ולגבש תפיסה לגבי התפקיד של אנשי מקצוע בתחום האבחון, המניעה וההתערבות בנושא. בעקבות כך, נוסדה העמותה בשנת 2016 בחיפה ומטרותיה הן: ״הקניית ידע, יכולות וכלים לאנשי המקצוע הרלוונטיים להתמודדות יומיומית ושוטפת עם האלימות ורצח הנשים בחברה הערבית וזאת באמצעות הכשרות, ליווי והדרכות על ידי מומחים לטיפול בנושא בין היתר מתחומי העבודה הסוציאלית, הפסיכולוגיה והייעוץ החינוכי. פיתוח שיח מקצועי אלטרנטיבי בחברה הערבית בהתמודדות עם רצח הנשים והאלימות כלפיהן וזאת באופן המספק מענה לצרכים ולרגשות של הפרט ותוך התחשבות בנסיבות חייו וסביבתו החברתית. יצירת מערך תמיכה לאנשי המקצוע, המבוסס בין היתר על העברת ידע וקידום שותפויות ושיתופי פעולה עם המסגרות והגופים המקצועיים הרלוונטיים בחברה.״',
    'description_ar': 'اذار- منتدى المهنيات والمهنيين لمكافحة جرائم قتل النساء (ج.م 580628055) تأسست الجمعية، في العام 2016 ومقرّها في حيفا، بمبادرة من مهنين ومهنيات في مجال العمل نحن مجموعة من المتخصصات والمتخصصين في العمل الاجتماعي، الصحة النفسية والاستشارة التربوية، بدأت جمعية آذار في العام 2011، بتنظيم حوار فكري عميق حول قتل النساء والقراءة الاجتماعية العميقة لهم لاستباق وتعزيز الدور المهني للعاملات والعاملين في المجال والتشخيص والوقاية والتدخل، حيث كانت هذه المبادرة تجسيدًا للقلق الإنساني والمهني الخطير بشأن انتشار هذه الجريمة ونفاقمها بشكل خاص وانتشار العنف في العائلة بشكل عام. تسعى الجمعية جاهدة للمساهمة في القضاء على ظاهرة قتل النساء من منظور التحليل النفسي الاجتماعي ولاثراء وتوسيع هذا السياق الاجتماعي وتقديم الحلول المناسبة والمهنية للمهنيات والمهنيين العاملات والعاملين في هذا المجال وتوفير أدوات مهنية وتنمية المعرفة والقدرات والآليات لدى المهنيات والمهنيين في مجالات العمل المتعددة في العمل الاجتماعي والصحة النفسية والمتخصصات والمتخصصين في مجال علاج العنف، للتعامل مع ظاهرة العنف وقتل النساء على أساس جندري. كما تعمل الجمعية على تأهيل ملائم لعلاج العنف الجندري في مجال ״ تقييم الخطورة״ وتشدّد في عملها على أهمية علاج المعالج/ة مرافقته/ها ودعمه/ها وتعزيزه/ها.',
