    'description': 'פרויקט ״אופק נשי״ הוקם ב-2008 כשיתוף פעולה בין משרד הרווחה, משרד הבריאות, עיריית חיפה והרשות למעמד האישה. הפרויקט מופעל על ידי עיריית חיפה במטרה להעניק עזרה לנשים שורדות רחוב הלכודות במעגל הזנות והסמים, ולסייע בשיקומן. לשם כך מפעילה אופק נשי לנשים בזנות דירת חירום, מרכז יום והוסטל שבו מקבלות המטופלות ליווי צמוד, סיוע במציאת תעסוקה, רכישת מקצוע ולימודים, תיווך מול מוסדות, טיפול פסיכיאטרי ועוד.',
