    'description': '״אות הזדהות למען נפגעי תקיפה מינית״ (ע״ר 580601771) היא עמותה שהוקמה בשנת 2015 על ידי שני שטלריד במטרה לפעול למען: ״הקמת קו-חירום למען נפגעי תקיפה מינית. מתן עזרה נפשית וייצוג משפטי חינם לקורבנות תקיפה מינית״. בשנת 2014, שטלריד פרסמה פוסט אישי בפייסבוק שהיכה גלים ושינה את חייה. היא עזבה את עבודתה כמחנכת ומורה לאנגלית והחלה להקדיש את זמנה לפעול נגד תקיפות מיניות בפעילות ציבורית, הרצאות וקמפיינים. שמה של העמותה נקשר לפעילות למען החמרה בענישה וליווי נפגעות ושורדות של תקיפה מינית מול עורכי דין, עובדים סוציאליים ופסיכולוגיים המתמחים בנפגעי עבירה.',
