    'description': 'עמותת אחת אחת (ע״ר 580559664) נוסדה בשנת 2012 על ידי תושבות בקעת אונו, במטרה לפעול לקידומן של נשות בקעת אונו (קריית אונו, גני תקווה, גבעת שמואל, אור יהודה, יהוד-נווה מונוסון, סביון-גני יהודה) ונשים בכלל. פעילות העמותה כוללת מתן ייעוץ משפטי בדיני עבודה (ע״י נציגת שדולת הנשים), יעוץ בכתיבת קורות חיים ובהשתלבות בשוק העבודה, ייעוץ בפתיחת עסק עצמאי. בנוסף משתפת פעולה עם עמותת ״מחשב לכל ילד״ על מנת לספק, ללא עלות, מחשבים מחודשים לאמהות חד-הוריות.',
