    'description': 'מועצת הנשים בגבעתיים הוקמה ב-1996 על ידי הרבנית ד״ר איילה גליקסברג. מרבית שנות פעילותה כיהנה בראשה חברת מועצת העיר איריס אברהם. בשנת 2017 הוקמה המועצה מחדש תחת השם ״אחת אחת הצד הנשי של גבעתיים״ על ידי חברת המועצה אור-לי ניב, מחזיקת תיק הנשים במועצה. מועצת הנשים פעלה לאורך השנים בין היתר בתחומי: ספורט, שירותים לאמהות ולאמהות חד-הוריות ובנושא תכנון עירוני ממבט מגדרי. המועצה פועלת כיום בעיקר באמצעות הרצאות והכשרות לנשות העיר וכן הרצאות והכשרות בבתי ספר ובתיכונים בנושאים מדעיים וטכנולוגיים.',
