    'description': 'עמותת ״אל הלב״ (ע״ר 580416634) הוקמה ב-2003 במטרה להעצים נערות ונשים המעוניינות לעסוק באומנויות לחימה ולהסיר את שלל החסמים העומדים בפניהן, מתוך כוונה לתרום ליצירת אקלים חברתי של שוויון הזדמנויות. מטרות העמותה המעודכנות הן: ״הקניית מיומנויות בטיחות אישית והגנה עצמית מעצימה לילדות/ים, בני/ות נוער, צעירות/ים, נשים ומבוגרות/ים, קשישות/ים ואנשים בעלי צרכים מיוחדים, תוך שאיפה למניעת אלימות לסוגיה וצמצום השלכותיה, בקרב כל מגזרי החברה ובפריסה ארצית, לרבות הפריפריה החברתית-גיאוגרפית, כמובילת התנועה העולמית להגנה עצמית מעצימה בישראל״. העמותה נוסדה במטרה להנגיש חוגים, אימונים והכשרות לנשות מקצוע תוך שיתוף פעולה עם בתי ספר ומסגרות חינוכיות במטרה לעודד קורסים של הגנה עצמית לנשים ונערות. עם השנים התפתחו מטרותיה המקוריות של ״אל הלב״ והארגון יצר מערך ארצי של הכשרה, הדרכה, פיתוח תכניות, חינוך והסברה. התכניות השונות שמפעיל הארגון עוסקות בצמצום ומניעת אלימות פיזית, מינית, רגשית וכלכלית המופנית כלפי ילדים, בני נוער, נשים, אנשים עם מוגבלויות וקשישים מכל מגזרי החברה הישראלית. כיום פועלת העמותה בקרב מאות ארגונים, רשויות מוניציפאליות, חברות עסקיות, עמותות ומוסדות חינוך, בריאות ורווחה.',
