    'description': '״אלאסאלה לפעילות חברתית-קהילתית״ היא עמותה (ע״ר 580571719) שנוסדה בשנת 2013 בוואדי סלמה. מטרות העמותה הן: ״הפעלת מעונות יום רגילים ושיקומיים. הפעלת צהרונים, גני ילדים, משפחתונים. טיפול בילד ובאישה. הפעלת מסגרות התנדבות חברתית קהילתית. הפעלת שירות לאומי. קידום מעמד האישה בחברה הבדואית בפרט והערבית בכלל. הפעלת מסגרות לקשישים. טיפוח דו קיום ופעילות חברתית וקהילתית בין הנשים באזור הצפון.״ העמותה מפעילה מרכז לגיל הרך ועורכת פעילות חברתית וימי כיף ואירועים לאמהות וילדים. יש תיעוד לפעילות העמותה בפייסבוק ובאתר רשם העמותות עד לשנת 2016.',
    'description_ar': 'مركز الاصالة للنشاطات الاجتماعية-الجماهيرية״- هي جمعية (ج.م 580571719) التي تأسست عام 2013 في وادي سلامة. أهدافها: تشغيل مراكز الرعاية النهارية العادية والتأهيلية. تشغيل رياض الأطفال والمنازل العائلية. رعاية طفل وامرأة. تمكين أطر التطوع الاجتماعي المجتمعي. تشغيل خدمة وطنية. تعزيز مكانة المرأة في المجتمع البدوي بشكل خاص والعربية بشكل عام. تشغيل إطارات للمسنين. تعزيز التعايش والأنشطة الاجتماعية والمجتمعية بين النساء في منطقة الشمال״. تدير الجمعية مركزًا لمرحلة ما قبل المدرسة وتجري أنشطة اجتماعية وأيامًا وفعاليات ممتعة للأمهات والأطفال. تم توثيق أنشطته على الفيسبوك وعلى الموقع الخاص بالمنظمات غير الربحية حتى عام 2016.',
