    'description': '״אל-בדיל״ (״האלטרנטיבה״) נוסדה בשנת 1994 כקואליציה של ארגונים ופעילים למאבק בפשע ״כבוד המשפחה״. הקואליציה פעילה בעיקר בצפון ובמשולש ועסקה בתיעוד מקרי רצח של רצח נשים ערביות על רקע כבוד המשפחה ובהעלאת מודעות לתופעה בקרב הרשויות בישראל. הקואליציה דרשה שינויי חקיקה ואכיפה לשם הגברת ההגנה על נשים ונערות מפני רצח והשתתפה בדיונים בכנסת, בהכשרות שוטרים ובהעלאת מודעות ציבורית לנושא זה. בין הארגונים שנטלו חלק בפעילות הקואליציה היו: נשים נגד אלימות, אל-סיואר, האגודה הערבית לזכויות האדם, אל-טופולה, כייאן, מעון לנשים חיפה, תנד״י ועוד.',
    'description_ar': 'الائتلاف البديل لمحاربة جريمة شرف العائلة. تأسست عام 1994 كائتلاف بين المنظمات والناشطين لمكافحة الجريمة ״شرف العائلة״. ينشط التحالف بشكل اساسي في الشمال وفي المثلث، ويتعامل مع توثيق حالات قتل النساء العربيات على أساس شرف العائلة وزيادة الوعي حول الظاهرة لدى السلطات الإسرائيلية. طالب التحالف بتغييرات تشريعية وتطبيقية لزيادة حماية النساء والفتيات من القتل والمشاركة في مناقشات الكنيست وتدريب الشرطة وتوعية الجمهور بهذه القضية. بين المنظات المشاركة في الائتلاف: نساء ضد العنف، السوار، الاتحاد العربي لحقوق الانسان، الطفولة، كيان، ملجأ نساء حيفا، نساء ديمقراطيات وغيرهم.',
