    'description': 'עמותת ״אלואפא ואל אמל״ נוסדה בבאקה אל-גרבייה בשנת 1983 ונרשמה כעמותה (ע״ר 580056331) שמטרתה לפעול למען רווחה וחברת הציבור, לרבות קידום מעמד האשה. העמותה פועלת לגיוס כספים והענקת מלגות לסטודנטיות וסטודנטים הנוטלים חלק בפעילויות קהילתיות וחינוכיות. בנוסף, העמותה מסייעת בפעילויות צדקה ודת, עזרה לנזקקים, שיעורי לימוד וקורסים שונים לנשים (תפירה, רקמה וכדומה).',
    'description_ar': 'جمعية الوفاء والامل تأسست في باقة الغربية عام 1983 وسجلت كجمعية (ج.م 580056331) وأهدافها العمل من اجل الرفاه والمجتمع المدني ولزيادة تعزيز مكانة المرأة. تعمل الجمعية بهدف تجنيد الأموال وتقديم منح للطالبات والطلاب الذين يتطوعون في المشاريع الجماهيرية والتربوية، إضافة الى ذلك تساعد الجمعية في الأنشطة الخيرية والدينية، وتساعد المحتاجين، دروس تعليمية ودورات مختلفة للنساء (الخياطة والتطريز وما شابه).',
