    'description': '״אלזהראא - עמותה לקידום מעמד האשה״ (ע.ר.580307817) היא עמותה שנוסדה בשנת 1997 בסח\'נין. מטרות העמותה הן ״קידום מעמד האישה הערביה בחברה ובמשפחה הישראלית בכל התחומים. מניעת אלימות על כל אופניה - פיזית, נפשית, מילולית ומינית. קידום זכויות האשה הן בחברה והן במשפחה.״ פעילות העמותה מתמקדת בסח\'נין ואזור הגליל. העמותה מפעילה מרכז לנשים בעיר ומקיימת סדנאות, הרצאות וקורסים בתחום של העצמת נשים, בריאות, הכנת נשים ליציאה לשוק העבודה ומעורבות בקהילה.',
    'description_ar': 'الزهراء- جمعية للنهوض بمكانة المرأة (ج.م .580307817) هي جمعية تأسست عام 1997 في سخنين. أهداف الجمعية هي ״تعزيز مكانة المرأة العربية في المجتمع والأسرة الإسرائيلية في جميع المجالات. منع العنف بجميع أشكاله – الجسدية، النفسية، اللفظية والجنسية. تعزيز حقوق المرأة في المجتمع والأسرة״. تتركز أنشطة الجمعية في سخنين ومنطقة الجليل. تدير الجمعية مركزًا نسائيًا في المدينة وتجري ورش عمل ومحاضرات ودورات في مجال تمكين المرأة، الصحة وتجهيز النساء للخروج إلى سوق العمل والتداخل الاجتماعي.',
