    'description': 'עמותת ״אליסקוד -בנות לומדות תכנות״ (ע״ר 580633733) הוקמה בשנת 2016 למען קידום ילדות ונערות, להשתלבותן במקצועות טכנולוגיים ולהעלאת המודעות לחשיבות לימודי טכנולוגיה בכלל האוכלוסיה והטמעת לימוד טכנולוגיה בגילאים צעירים. העמותה מפעילה את המיזם ״בנות לומדות תכנות״ בשיתוף פעולה עם עיריית ראשון לציון שנועדה לקדם תלמידות בחינוך היסודי (כיתות ד\'-ו\') בתחום הטכנולוגי. העמותה מכשירה מדריכות - תלמידות תיכון ממגמות מדעי המחשב - שעברו הכשרה מקיפה על נושאים מקצועיים ונושאי הדרכה בליווי מהנדסות תוכנה ומקיימת פעילויות העשרה שכוללות סיורים בחברות הי-טק והאקאתונים.',
