    'description': 'עמותת אלמנהאל (ע.ר. 580342228) הוקמה בשנת 1999 באבו-סנאן במטרה לפעול לשם ״עידוד השכלה גבוהה אצל נשים דרוזיות. שיפור וקידום מעמד האישה הדרוזית ועידוד מעורבותה בחברה. עידוד וסיוע למשפחות שכולות, אלמנות ונשים מוכות.״ פעילות העמותה כוללת ארגון של כנסים, ימי עיון, הרצאות וסיורים בישראל לנשים דרוזיות לשם העמקת המורשת והדת דרוזית. ככל הנראה עיקר סיורי העמותה מכוונים לביקור במקומות קדושים לעדה הדרוזית.',
