    'created': {
      'by': 'יעל בר צדק',
      'on': '20/05/2018'
    },
    'description': '״אלסיואר- תנועה פמיניסטית ערבית״ היא עמותה (ע.ר. 580296283) שמטרותיה הן: ״מתן תמיכה לנפגעות תקיפה מינית. מאבק נגד פשעי האלימות המינית נגד נשים. הגברת המודעות בחברה בנושא האלימות המינית לנשים.״ העמותה פועלת בחיפה משנת 1997 ומשרתת את הציבור הערבי-פלסטיני, במיוחד באזור הצפון והמרכז באמצעות הפעלת קו תמיכה וסיוע טלפוני המספק מענה 24 שעות, הפעלת סדנאות חינוכיות בבתי ספר, הכשרת אנשי מקצוע, יצירת קמפיינים תקשורתיים ופרסום מידע בערוצים שונים. אלסיואר נאבקת נגד כל סוגי הדיכוי והניצול הפטריארכלי, הכלכלי, הלאומי והאתני ומחויבת לשחרר ולהעצים נשים מקבוצות שוליות ופגיעות בחברה. בהתאם לכך, אל-סיואר רואה בפמיניזם תנועה מהפכנית חברתית-פוליטית המבקשת לשנות את יחסי הכוח והשליטה השוררים בחברה ולתמוך בקידום סוגיות הנוגעות למעמד האשה הפלסטינית.',
    'description_ar': 'السوار- حركة نسوية عربية״ هي جمعية (ج.م. 580296283) التي أهدافها هن: ״إعطاء دعم لضحايا التحرش الجنسي. مكافحة جرائم العنف الجنسي ضد النساء. رفع المعرفة في المجتمع حول موضوع العنف الجنسي عند النساء. ״الجمعية تعمل في حيفا منذ عام 1997 وتخدم المجتمع العربي – الفلسطيني، وخصوصا منطقة الشمال والمركز. من خلال تشغيل خط دعم ومساعدة هاتفية الذي يعطي رد على مدار 24 ساعة، تشغيل ورشات تربوية بالمدارس، تدريب أصحاب مهن، انشاء حملات إعلامية ونشر معلومات بقنوات مختلفة. السوار تعمل لمكافحة ضد كل أنواع الظلم والاستغلال الأبوي، الاقتصادي، القومي والعرقي وتلتزم بتحرير وتعزيز نساء من المجموعات المستضعفة والمتضررة في المجتمع. بملائمة لذلك، ترى ״السوار״ بالنسوية حركة ثورية اجتماعية- سياسية التي تطالب بتغيير علاقات القوة والتحكم المستشريين في المجتمع ولدعم بتطوير القضايا المتعلقة بمكانة المرأة الفلسطينية.',
