    'description': 'ארגון אל״ף הוקם במאי 1978, בכנס הפמיניסטי בבאר שבע ופעל במשך שנה וחצי. הוא איחד נשים משלושת המרכזים הפמיניסטיים בירושלים, חיפה ותל אביב. פעילותו כללה ארגון ערבי תרבות ומסיבות, דיונים ומפגשים, מעורבות בהפגנת ״הנעימים והנעימות״ ואף קורס על פמיניזם בהנחיית חברת הכנסת לשעבר מרשה פרידמן. הארגון התפרק כתוצאה ממתחים בין חברות שרצו להדגיש את הפעילות הפמיניסטית הפוליטית לבין חברות שהעדיפו שעיקר הדגש בקבוצה יהיה על הקמת מסגרת חברתית לסבית.',
