    'description': '״אמא יקרה״ נוסדה בשנת 2014 על ידי ימית אורבין בעקבות דיכאון אחרי לידה שחוותה. המיזם נרשם כעמותה (ע״ר 580643625) ב-2017. מטרת העמותה - העמותה להעלות למודעות הציבורית את נושא הדיכאון והקשיים הרגשיים שעוברות נשים במהלך ההריון ובתקופה שלאחר הלידה, ולמצוא פתרונות בכדי להקל על הנשים ובני המשפחה הקרובה. העמותה פועלת באמצעות הנגשת ידע עדכני והקמת קבוצות תמיכה לנשים.',
