    'description': '״אמהות למען עתיד פורח״ היא עמותה (ע״ר 580378925) שנוסדה בשנת 2001 באבו סנאן. מטרות העמותה הן: ״עידוד הקמת מעונות יום במגזר הערבי, כפוף לחוק הפיקוח על מעונות תשכ״ה-1965 ו/או כל חוק אחר. שיפור וקידום מעמד האשה ועידודה ליציאה לעבודה. טיפוח וסיוע לאמהות עובדות ולילדים בסיכון. שיפור השירותים החברתיים לטף במגזר הערבי. חינוך, ניהול ופתיחת גני ילדים ע״פי חוק הפיקוח תשכ״ט״. העמותה מפעילה שלושה גני ילדים ומעון יום באבו סנאן ובכפר יאסיף ומקיימת פעילויות תרבותיות לאמהות וילדים.',
    'description_ar': 'أمهات من اجل مستقبل مزهر- هي (ج.م 580378925) تأسست عام 2001 في أبو سنان. اهداف الجمعية هي: التشجيع على انشاء مراكز للرعاية في المجتمع العربي، مع مراعاة قانون المراقبة الملاجئ لعام 1965 و\\أو أي قانون اخر. تحسين وتعزيز وضع المرأة وتشجيعها للخروج لسوق العمل. رعاية ومساعدة الأمهات العاملات المعرضات للخطر. تحسين الخدمات الاجتماعية للاستفادة من المجتمع العربي، التعليم، تدير الجمعية ثلاثة رياض أطفال ومراكز للرعاية النهارية في أبو سنان وكفر ياسيف وتجري أنشطة ثقافية للأمهات والأطفال.',
