    'description': 'תנועת ״הורים נגד שתיקה״ שנקראה גם ״אמהות נגד שתיקה״ הוקמה ב-3.5.1983 והתפרקה ב-5.6.1985 עם סיומה הרשמי של מלחמת לבנון. התנועה הוקמה על ידי הורים לחיילים למעלה משני-שליש מחברותיה היו נשים ולכן נחשבת לאחת מתנועות השלום הנשיות הראשונות בישראל. מטרת התנועה היתה ״הוצאת צה״ל מלבנון מיד, תוך הבטחת סדרי הביטחון הטובים ביותר שאפשר ליישובי הצפון״. התנועה היתה בלתי-מפלגתית ורוב חברותיה היו תושבות מרכז הארץ והיא פעלה באמצעות הפגנות, מכתבים ועצומות. המנהיגה המרכזית היתה שושנה שמואלי מתל אביב.',
