    'description': 'ארגון מיילדות הבית בישראל – אמה״י (ע״ר 580456564) הוקם בשנת 2006 על ידי מיילדות מוסמכות בעלות רישיון המומחיות ללוות וליילד בלידה מסגרת ביתית נשים. מטרותיו העיקריות של הארגון היא לאסוף נתונים ולספק מידע על לידות בית, לאגד מיילדות בית ולקדם את מיסוד לידות הבית במסגרת שירותי הבריאות בישראל כולל הגדרת פרוטוקולים המסדירים את הטיפול בנשים ויילודים בסיכון נמוך במסגרת ביתית. בשנת 2016 הארגון היה מעורב (ביחד עם קבוצות נוספות) בתיקון החקיקה להשוואת זכויות לקבלת מענק לידה לנשים שילדו בבית. לא נמצאו דוחות שנתיים של העמותה ובשנת 2021 היא נמצאת בהליכי מחיקה.',
