    'description': '‏״תנועת האישה הדתית לאומית״ נוסדה בשנת 1959 כמיזוג בין ארגון נשי מזרחי ״אמן״ לבין ארגון ‏הפועלות של הפועל המזרחי, בראשות הרבנית שרה הרצוג. בשנת 1977 התווסף לשם הארגון השם ‏‏״אמונה״ ובאותה שנה הוקמה התנועה העולמית של ״אמונה״. בשנת 1982 נרשמה התנועה כעמותה. ‏כיום, מפעילה ״אמונה״ 37 סניפים ובתי מדרש לנשים ללימודי יהדות ומחשבת ישראל ברחבי הארץ, ‏‏140 מעונות יום ומעונות רב-תכליתיים, 4 אולפנות תיכוניות, מכללה אקדמית להכשרת עובדי הוראה ‏בתחומי אמנות, תיאטרון ועיצוב, 5 בתי ילדים, פנימיות וכפרי נוער, מרכז חירום וקלט חירום לילדים ‏במצבי סיכון, 13 מרכזים לייעוץ וטיפול במשפחה, בת עמי – אגודת השרות הלאומי בשיתוף עם ״אלומה״, מכון באר אמונה להכשרת מנחים ומטפלים במשפחה, מרכז להב״ה – ללימודי הבית היהודי, ‏מועדון נשות עסקים וקריירה, חוגים ארציים לדוברות אנגלית, ו-28 סניפי אמונה עולמית בתפוצות.‏',
