    'description': 'ב-1996 קמה ביוזמתה של הסופרת אורה עשהאל קבוצה של נשים יוצרות במסגרת ״איגוד הנשים האקדמאיות״ שנקראה ״סניף נשים יוצרות״. ב-2003 נרשמה הקבוצה כעמותה בשם אנ״י - איגוד נשים יוצרות ישראל (ע״ר 580406783). המטרה - מתן ביטוי ובמה ליוצרות ואמניות: סופרות, משוררות ואמניות. האיגוד פרסם שתי אנתולוגיות בשם ״מעגלים״ בשנת 2000 ובשנת 2002. העמותה ככל הנראה פעלה עד 2013 ולא פעילה כיום.',
