    'created': {
      'by': 'שרי אהרוני',
      'on': '06/12/2018'
    },
    'description': '״אסוואת - מרכז פמיניסטי פלסטיני לזכויות מיניות ומגדריות״ היא עמותה של נשים פלסטיניות גאות שהוקמה בשנת 2003 על ידי שלוש פעילות לסביות פלסטיניות וביניהן ראודה מרקוס. בין השנים 2003-2017 פעל המרכז באופן עצמאי במסגרת הארגון הפמיניסטי ״כייאן״ בבית קואליציית נשים חיפה ובשנת 2018 נרשם כעמותה עצמאית (ע״ר 580645489). ״אסוואת״, אשר משמעות שמה בערבית הינה ״קולות״, פונה ללסביות, ביסקסואליות, טרנסיות, אינטרסקס וקוויריות. מטרות העמותה הן לפעול ״בתוך ולמען החברה הערבית. לפרסם ולהוציא לאור מאמרים, חוברות, מחקרים ופרסומים אחרים בתחום הזהות המינית והמגדרית. להעביר הרצאות ולתכנן ימי עיון לצורך הגברת המודעות בחברה הערבית בישראל בנושאים הקשורים לזהות מינית ומגדרית. לארגן פעילויות חברתיות ותרבותיות בנושאים הקשורים לזהויות מיניות ומגדריות. לתת תמיכה אישית וקבוצתית ללהט״בים על ידי עבודה עם אנשי מקצוע רלוונטיים״. בנוסף, אסוואת מקדמת נושאים הקשורים בזכויות להט״ב, דיכוי נשים פלסטיניות הן במרחב הפטריארכלי והן במרחב הישראלי-ציוני, וקושרות בין מאבקים להט״בים, פמיניסטיים ופלסטינים. פעילויות המרכז כוללות מפגשים חודשיים ברחבי הארץ, קבוצה לטרנסג׳נדריות, פעילויות חברתיות, מסיבות, אירועי ספורט, טיולים והפעלת קו תמיכה ויעוץ. בנוסף, מפעיל הארגון קבוצת לימוד לאנשי ונשות מקצוע בתחומי החינוך והטיפול על מגון מיני ומגדרי, ומפרסם חומרים ומידע בערבית על הקהילה הלהט״בית וצרכיה בחברה הערבית.',
    'description_ar': '״أصوات- مركز نسوي فلسطيني من اجل الحقوق الجنسانية والجندرية״- هي جمعية للنساء الفلسطينيات المثليات والتي أقيمت عام 2003، بمبادرة ثلاث نساء فلسطينيات مثليات. وواحدة منهن هي روضة مرقص. بين السنوات 2003-2017، عمل المركز بشكل مستقل داخل المركز النسوي ״كيان״ في بيت ائتلاف النساء في حيفا. وفي عام 2008 سجلت الجمعية بشكل رسمي ومستقل (ج.م 580645489). ״أصوات״ والتي تتوجه للنساء المثليات، مزدوجة الجنس، المتحولات، المتوسطات والكويريات.״ تهدف الجمعية لنشاط داخل ومن اجل المجتمع العربي. لنشر مقالات، كتيبات وابحاث وغيرها من المنشورات حول الهوية الجنسية والجندرية. تمرير محاضرات وبناء أيام دراسية بهدف زيادة الوعي، إقامة نشاطات اجتماعية وتربوية حول مواضيع الهوية الجنسية والجندرية للمجتمع العربي في إسرائيل. تقديم الدعم الشخصي والجماعي للمثليين من خلال العمل مع مهنيين ذو صلة״. إضافة الى ذلك تقوم ״أصوات״ بتعزيز القضايا المتعلقة بحقوق المثليين، قمع النساء الفلسطينيات في كلا المساحتين الابوية والإسرائيلية-الصهيونية، والروابط بين المثليين، الصراعات النسوية والفلسطينية. يشمل نشاط المنظمة لقاءات شهرية في كل انحاء المنطقة. مجموعة متحولات، نشاطات اجتماعية، حفلات، فعاليات رياضية، رحلات وتفعيل خط مساعدة واستشارة. بالإضافة الى هذا تدير المنظمة مجموعة دراسة لنساء ورجال من مجال التربية حول التنوع الجنسي والجنسانية، وتنشر المواد والمعلومات باللغة العربية عن مجتمع المثليين واحتياجاتهم في المجتمع العربي.',
