    'created': {
      'by': 'יעל בר צדק',
      'on': '19/05/2018'
    },
    'description': 'אסל״י (ארגון \'סרט לבן\' ישראל) היא עמותה ישראלית (ע״ר 580619799) והסניף המקומי של תנועת ה\'סרט הלבן\' (White Ribbon) הבינלאומית. \'סרט לבן\' היא התנועה הגדולה בעולם של גברים ונערים שפועלים לשים קץ לאלימות נגד נשים ונערות ולפעול לקידום שוויון מגדרי, יחסים בריאים בין המינים וחזון חדש של גבריות. התנועה הוקמה בקנדה בשנת 1991 ופועלת כיום ביותר מ- 60 מדינות ברחבי העולם. התנועה מקדמת את חזונה ורעיונותיה באמצעות פעילויות חינוכיות, העלאת מודעות, הפצת חומרי הסברה, פעולות שטח, הקמת שותפויות וקמפיינים יצירתיים במטרה ליצור שינוי חברתי ותרבותי שיסייע לצמצם ולמגר את תופעת האלימות נגד נשים. מטרות העמותה הישראלית הן: ״לפעול להעלאת המודעות לכל סוג של אלימות כלפי נשים, בדגש על העלאת מודעותם של גברים. אופן הפעולה לקידום מטרה זו יכלול כתיבת מאמרים בעיתונים וברשתות החברתיות, הפצת עלוני מידע, מתן הרצאות, הפקת סרטים, מסעי פרסום, וכל אמצעי אחר להפצת מידע בקרב הציבור; לפעול למניעת אלימות כלפי נשים. על מנת לקדם מטרה זו תפעל העמותה בשני ערוצים עיקריים: 1. השפעה על דעת הקהל ומקבלי ההחלטות במטרה להגביר את מדיניות האכיפה כנגד גברים אלימים ועברייני מין מחד, והטיפול בקורבנות מאידך. במקביל תפעל העמותה מול הגורמים הרלוונטיים להכנסת שיעורי מגדר ושיעורי חינוך מיני למערכת החינוך ולקידום יוזמות שפונות לגברים בוגרים במטרה למנוע אלימות כלפי נשים ולעודד התערבות של ״העומד מן הצד״ במקרי אלימות. 2. עיסוק ישיר של העמותה בחינוך ילדים ונוער לגבריות בלתי אלימה, העברת סדנאות והרצאות לבני נוער ולמבוגרים בנושא זה וקריאה לגברים להצטרף למאבק באלימות כלפי נשים באמצעות דף הפייסבוק של העמותה, באמצעות קמפיינים תקשורתיים ובדרכי פירסום והסברה נוספים; לבצע כל פעולה שתהיה דרושה או מועילה או רצויה לשם ביצוע מטרות העמותה וכל פעולה שיוחלט עליה במוסדות העמותה.״ בין מייסדי העמותה הישראלית אביב יהלום, יותם קונסטנטיני וירון שוורץ.',
