    'description': '״אפאק אלמסתקבל להעצמת האדם״ (ע״ר 580590867) נוסדה בשנת 2014 במטרה לפעול ל״קידום החינוך והתעסוקה בדרום, קיום הדרכות העצמה במיוחד לנשים מהמגזר הבדואי, מתן מלגות לסטודנטים הלומדים במוסדות להכשרה גבוהה, הדרכה והכוונה לתלמידי י״ב בבחירת מקצוע הלימודים באוניברסיטה״.העמותה מקיימת קייטנות, אירועי תרבות וטיולים בטבע. קהל היעד הוא תלמידים ותלמידות בחינוך היסודי, בנים ובנות כאחד.',
