    'description': 'ארגון האמהות החד-הוריות בדימונה הוקם בסוף 2003 על ידי קבוצת נשים מקומיות בראשות אילנית לוגסי שייסדה קייטנה לאמהות חד-הוריות בעיר בשנת 1997. הארגון הוקם כקבוצת מחאה בעקבות מדיניות קיצוץ הקצבאות שהנהיג שר האוצר בנימין נתניהו. קיים מעט מאוד מידע על פעילות הקבוצה שלא נרשמה כעמותה, אך נראה כי בין פעילויותיה נכללו הקמת מפעל הזנה בבתי ספר שנועד לספק תעסוקה לאמהות חד-הוריות (בתמיכת משרד התמ״ת והביטוח הלאומי), ארגון קייטנה שנתית למשפחות חד-הוריות ופעילויות חברתיות אחרות. בשנת 2010 קוצץ תקציב הקייטנות וככל הנראה הסתיימה פעילות הקבוצה. לא נמצא מידע נוסף.',
