    'description': '״ארגון המילדות- בישראל״ (ע״ר 580171056) הוקם בשנת 1998 במטרה: ״לעדכן את הידע וההבנה בכל הקשור לפריון ילודה ותכנון משפחה ולהשתתף בפעולות לקידום בריאות המשפחה ושיפור הטיפול ביולדת ובילוד.״ חברות הארגון בוחרות נציגה מכל חדר לידה שמייצגת אותן במועצת הארגון. הארגון פועל בתחום החינוך (הוצאה לאור של העיתון ״מילדות״ וארגון כנסים וימי עיון) וקידום של מחקר ואיסוף נתונים ופרוטוקולים שונים בתחום הלידה.',
