    'description': '״ארגון המניפה של מקצועות הליווי וההדרכה מסביב ללידה ובמעגלי החיים״ הוקם בשנת 2007 בתל אביב על ידי קבוצה של מדריכות הכנה ללידה ותומכות לידה (דולות) בישראל. נרשם כעמותה (ע״ר 580494888) מטרות העמותה הן: ״הסדרה הכשרה וייצוג של עוסקים במקצועות הליווי וההדרכה מסביב ללידה ובמעגלי החיים. עדכון וההגדרה מעת לעת של המקצועות הנ״ל בהתאם לנסיבות. הפקת ימי עיון והשתלמויות. יידוע הציבור באשר לזכותו לליווי בלידה ולאחר לידה. ייצוג חברי הארגון הן כלפי חוץ והן כלפי פנים-כערכאה לבירורים בסוגיות שונות.״ העמותה הקימה אתר אינטרנט וקיימה מספר כנסים ומפגשים (כפי שעולה מהדוח הכספי של 2010) שעסקו בחקיקה, מדיניות ציבורית בנושאים הנוגעים למעמדן החוקי והתעסוקתי של תומכות לידה בישראל. בשנת 2017 הוגש הדוח האחרון של העמותה ולאחר מכן היא החלה בהליכי מחיקה.',
