    'description': 'ארגון נשות-אורט נוסד בשנת 1950 בסיוע ארגון נשות אורט באמריקה במטרה לשמש כזרוע הנשים של ״אורט ישראל״ (1949, חל״צ). הארגון החל לפעול בתחילה בשלוש הערים הגדולות: ירושלים, חיפה ותל-אביב, ולימים הרחיב את פעילותו במרבית היישובים שבהם ממוקמים מוסדות אורט. הארגון פועל על בסיס התנדבותי, רשומות בו 45,000 נשים, מתוכן פעילות 400. הארגון פועל למען קידום רווחתם של התלמידים ברשת אורט– החל מצרכים פיזיים (כמו מזון ולבוש לנזקקים, מזנון/קפיטריה בבתיה״ס וכו\'), וכלה בצרכים לימודיים (ספריות, מלגות לנזקקים ולמצטיינים בתחום החברתי ועוד). בין המטרות המעודכנות של הארגון - עידוד בנות ללימודי טכנולוגיה ומדע.',
