    'description': '״ארגון נשי עכו״ הוא אחד הארגונים הותיקים ביותר שהוקמו על ידי נשים ערביות בישראל. הוא נרשם כאגודה עותומנית בשנת 1975 וכעמותה בשנת 1990. מטרת העמותה (ע״ר 580105963) היא לפעול למען ״ארגון הנשים הערביות וקיום פעילות למען קידום מעמדן ופתרון בעיותיהן החברתיות, החינוכיות התרבותיות.״ החל ממשנת 1976 מפעיל הארגון גן ומעון יום המוכרים על ידי משרד העבודה והרווחה. בנוסף, הוא מפעיל מועדון בעיר עכו שבו מתקיימים קורסים לנשים, כולל הכשרת מטפלות ונשות חינוך וספריה לקהל הרחב. העמותה גם מחזיקה קרן הלוואות קטנות (עד 15,000 ₪ ללא ריבית) לנשים מעכו והסביבה שזקוקות לכך.',
    'description_ar': 'جمعية النساء العكيات: وهو أحد أقدم الجمعيات التي أقمتها نساء عربيات في إسرائيل. وقد سجل كسجل عثماني عام 1975 وكجمعية عام 1990. تهدف الجمعية للعمل من أجل ״ تنظيم النساء العربيات وتحقيق نشاطات من اجل تعزيز مكانة النساء وحل مشاكلهن الاجتماعية التربوية والثقافية.״ منذ مطلع عام 1976 تدير الجمعية حضانة ونادي يومي المعترف بهم من قبل وزارة الرفاه الاجتماعي، كما وانها تدير نويدية في عكا التي توفر مساقات للنساء تشمل: تدريب حاضنات ومربيات ومكتبة عامة. تقوم الجمعية بتمويل صندوق قروض صغيرة حتى 15,000 لا يشمل ضرائب. لنساء محتاجات من عكا والمنطقة.',
