    'description': '״ארגון תומכות הלידה בישראל״ (ע״ר 580475614) הוקם בשנת 2007 בכפר סבא על ידי רוני חטב מבית הספר ״קשת בענן״ כאיגוד מקצועי של דולות במטרה: ״לאגד את כל תומכות הלידה תחת קורת גג אחת וליצור הידברות חוזית סדירה והוגנת מול בתי החולים בארץ והצוותים הרפואיים. לרבות ציבור המיילדות. להעלות את המודעות של נשים הרות למקצועיותן של תומכות הלידה בישראל. לאפשר לנשים הרות לבחור ללדת בדרכן, כל עוד לא קיימת סכנה להן או לילוד״. לעמותה אין דוחות פעילות ונראה כי היא פעלה במשך זמן מועט. בשנת 2008 הוקם ״ארגון המניפה של מקצועות הליווי וההדרכה מסביב ללידה״ אשר פועל למטרות דומות.',
