    'description': 'באלעטאא נחיא (ע״ר 580593135) היא עמותה שהוקמה בשנת 2014 באכסאל. מטרות העמותה הן ״לסייע להרחבת המודעות בקרב הנשים בעיקר בכל הנוגע לזכויותיהן בחברה, מתן הדרכות והרצאות בכל הנוגע לקידום הפן הנשי ושילובו בתוך האוכלוסייה, תוך הגברת המודעות והפניית שימת הלב לדרכים ולשיטות חדשניות שיגרמו לשיפור מעמדן של הנשים בראי החברה הישראלית בכלל ובראי החברה הערבית בפרט״. פעילויות העמותה מתרכזות באכסאל וכוללות הדרכות, הרצאות וחוגי העשרה והכשרה לנשים בנושאים הנוגעים לזכויות נשים ומעמדן בחברה',
    'description_ar': 'بالعطاء نحيا (ج.م 580593135) هي مؤسسة غير ربحية تأسست عام 2014 في إكسال. تتمثل أهداف الجمعية في ״المساعدة على زيادة الوعي بين النساء، خاصة فيما يتعلق بحقوقهن في المجتمع، توفير التدريب والمحاضرات حول تعزيز وإدماج الوجه الأنثوي داخل المجتمع، مع زيادة الوعي وتوجيه انتباههم إلى طرق وأساليب مبتكرة من شأنها تحسين وضع المرأة في وجه المجتمع الإسرائيلي بشكل عام والعربي بشكل خاص. تتركز أنشطة الجمعية في اكسال وتشمل دورات تدريبية، محاضرات ودورات إثراء والتأهيل النساء في القضايا المتعلقة بحقوق المرأة ووضعها في المجتمع.',
