    'description': '״באשר תלכי״ היא עמותה (ע״ר 580581585) המספקת עזרה מיידית לנשים חרדיות ודתיות הנמצאות בהליכי גירושין ופרידה. הארגון הוקם על ידי פייני סוקניק בשנת 2013 לאחר שהשתתפה בתכנית מנהיגות נשים של ויצו. הארגון רשום בירושלים אך פועל ברחבי הארץ ועוסק בתחומים הבאים: הפעלת קו חם לפניות ושאלות בנושאים הקשורים בתהליך הגירושין; מתן ייעוץ משפטי וליווי של טוענות רבניות ועובדות סוציאליות; הפעלת רשת תמיכה טלפונית; סדנאות להעצמה אישית וקבוצות תמיכה לנשים ברחבי הארץ; ייעוץ וליווי כלכלי לנשים בתהליכי גירושין, כולל תכניות לעזרה כלכלית וליווי עסקי.',
