    'description': 'בוש - BUSH הן קולקטיב אומנות ותרבות פמיניסטי, להט״בי וקווירי. מאז 2015 הן פועלות למען שינוי חברתי דרך יצירת במות ומרחבים של אמנות, תרבות ואקטיביזם קווירי ופמיניסטי. הן עוסקות בעיקר בחקר זהות מגדרית ומינית, כמו גם סוגיות אקטואליות. הקולקטיב שעיקר פעילותו מרוכזת באזור תל אביב, פועל במגוון פלטפורמות כולל הדפסת פנזין, הקרנת סרטים, יצירת תערוכות ופסטיבלי אומנות, מיצגים והרצאות. הוא גם מייצר חממת אומנות לאמניות מתחומים שונים המבקשות להתבטא בתחומים אותם הוא מבקש לחקור. היצירות של האומניות בחממה הוצגו בשני לוחות שנה ואסופת גלויות שהפיק הקולקטיב.',
