    'description': 'בזכות נשים (ע״ר 580505493) נוסדה בשנת 2009 למתן תמיכה כלכלית, הלוואות, ויעוץ לנשים חרדיות עובדות ובעלות עסקים; וזאת על מנת לתמוך במשפחות חרדיות. אחת ממקימות העמותה היא אביגיל שקוביצקי – רו״ח חרדית עם משרד משלה ״קבוצת מישור״ (סניף בירושלים, סניף בבני ברק, ‏ואביגיל ‏שקוביצקי עצמה גרה בבית״ר עילית), והיא מאוד מוכרת בציבור החרדי ובמעגלי ממשל בשל ‏העיסוק הרב ‏מאוד שלה לטובת הציבור החרדי, בפרט בנושא מימון מוסדות חינוך, וגם מתמקצעת ‏בחשבונאות למלכ״רים ועמותות. למרות שהיא מאורע מאוד מדינית וחברתית, והיא בעצמה אשה ‏חרדית מצליחה ופורצת דרך בתחום העסקי ‏והציבורי.',
