    'description': 'הארגון ״ביסקסואלים בישראל״ נוסד על ידי דניאל הופמן בשנת 2004. הוא נועד להלחם בביפוביה בתוך קהילת הלהט״ב ומחוצה לה ולהעלות את המודעות לצרכיהם של ביסקסואלים. הוא פעל בעיקר בירושלים וארגן מפגשים לקהילה הביסקוסאלית בעיר. בנוסף, הוא ארגן גושים ביסקסואלים במצעדי גאווה, הפעיל אתר עם תכנים לקהילה הביסקסואלית והוציא הודעות לתקשורת בנושאים הקשורים לקהילה. הארגון הפסיק לפעול בשנת 2007, בהחלטתו של הופמן, אשר הרגיש תחושה של מיצוי הפעילות והשלמה חלקית של מטרות הארגון.',
