    'description': '״בית נתן״ (ע״ר 580273746) הוא מרכז תמיכה הממוקם בירושלים לנשים המתמודדות עם מחלת הסרטן, לרבות סרטן השד, המיועד בעיקרו לקהל הדתי. המרכז מפעיל בין השאר קו תמיכה טלפוני בשם \'לב רחל\' המאויש על ידי מתנדבות מחלימות או נשים שהתמודדו עם בן משפחה חולה סרטן, קבוצות תמיכה וסדנאות, ביקורי בית לחולים קשים, ליווי נשים בטיפול יום, סבסוד ימי נופש והבראה לזוגות שאחד מהם מתמודד עם סרטן וקבוצות תמיכה ייחודיות לאימהות חולות סרטן ובנותיהן ולנערות שחוו אובדן של אחד ההורים על רקע מחלת הסרטן.',
