    'description': '״בית של״ב- בית שהייה טיפולי לגברים אלימים מורחקים- עירית חיפה והסביבה״ הוקם בשנת 2010 בכדי לספק מענה לגברים אלימים אשר הורחקו מביתם על ידי צו בית משפט לענייני משפחה או ע״י המשטרה ובכדי למנוע את הסכנה הנשקפת לחיי האישה בעקבות תביעתה לצו הרחקה. בדומה לדירת השל״ב בבאר שבע, הבית נועד לטיפול בגברים מאזור חיפה והפריפריה ונערך לקלוט עד 15 גברים בו בזמן במתכונת של הוסטל עם צוות מטפלים 24 שעות ביממה. הבית סיפק סיוע בתחומים הבאים: (1) טיפול ממוקד ואינטנסיבי בשלב המשבר; (2) תמיכה והתחלת תהליך טיפולי להפסקת האלימות; (3) הפחתת רמת הסיכון של הגבר המורחק כלפי האישה והילד על ידי פתרון הדיור. ההפנייה לבית נעשתה בהסכמה של הגבר האלים ותקופת השהייה בה היתה תלוייה באורך צו ההרחקה. בשנת 2011 הופנו 25 אנשים לדירת של״ב בחיפה ע״י קציני מבחן, מתוכם נקלטו לטיפול 7 אנשים. הפרויקט הובל על ידי ועדת היגוי שכללה נציגי משרד הרווחה, השרות לרווחת הפרט ומשפחה ומערכות שרותים חברתיים- עיריית חיפה. על פי דוח הממונה על יישום חוק חופש המידע במשרד הרווחה (2012) השירות נסגר בראשית שנת 2013, ככל הנראה בשל חילוקי דעות בין משרד הרווחה ועיריית חיפה בנוגע למימון פעילותו.',
