    'description': '״בצדק״ היא תכנית צדק מאחה במקרים של פגיעה מינית, אשר פועלת במסגרת ״המרכז הישראלי לגישות מאחות״ [ע״ר], מפתחת ומיישמת תהליכי צדק מאחה בפגיעות מיניות בישראל. התכנית מבוססת על מודלים של תכניות צדק מאחה מקבילות מרחבי העולם. היא ייחודית וחדשנית בשל התאמתה לתחום פגיעות מיניות, לתרבות, לנורמות, לתהליכי העבודה ולמבנה המערכות הקיימות בישראל. התכנית שמה לה מטרה לפתח, לשפר ולקדם את מודל העבודה, להיות הגוף המקצועי המוביל בישראל בתחום צדק מאחה בפגיעה מינית ומהתכניות המובילות בעולם בתחום זה.',
