    'description': '״בראעם אל רביע כפר יאסיף״ (ע״ר 580490746) היא עמותה שנוסדה בשנת 2008. מטרותיה הן: ״יסוד וניהול מסגרות לילדים בגיל הרך מגיל 3 עד 5 שנים לרבות גני ילדים. שיפור מעמד האישה באמצעות הרצאות וסדנאות בתחומים שונים. קידום מעמדו של הילד. עידוד וטיפוח יכולות אישיות. קיום מסגרות להגנה על ילדים מקיפוח. קיום וניהול מסגרות לילדים עם צרכים מיוחדים.״  העמותה מפעילה גני ילדים בכפר יאסיף.',
