    'description': 'בשבילך היא תכנית שהוקמה בשיתוף משרד הרווחה, עיריית באר שבע ובהפעלת עמותת יחדיו כחלק מהתכנית לאומית לטיפול בזנות. התכנית הנותנת מענים טיפוליים ותמיכתיים לנשים במעגל הזנות. התכנית מפעילה מרכז טיפולי הפועל בשעות היום המציע טיפול, ליווי, תמיכה, תיווך מול רשויות, מקום להפוגה ולפסק זמן , ארוחות חמות, מסגרת חברתית. התכנית מבוססת על התפיסה שהקשר עם הנשים צריך להתחיל במקום בו הן נמצאות ולכן מרכיב הישוג בתכנית הינו מרכזי.',
