    'description': '״בשלה״ – קבוצת לסביות בנות 45 ומעלה שהוקמה בשנת 2007, שמה לעצמה למטרה ליצור קהילה שתיתן מענה למגוון צרכיהן הייחודיים של לסביות בוגרות. הרעיון של הקמת קבוצה או קהילה תומכת לנשים לסביות בגיל השלישי, עלה והתבשל כבר בסוף שנות ה-90 של המאה הקודמת אצל ד״ר רותי ליטוין, פסיכותרפיסטית חיפאית ולסבית גאה ופעילה בקהילה המקומית והארצית. בכנס הלסבי הארצי שנערך בדצמבר 2004 במלון כרמל בנתניה, נזרע הזרע הראשון להקמת הקבוצה ובמרץ 2007 נפגש הגרעין המייסד של בשלה בתובל שבגליל. הקבוצה כוללת כ כ- 200 חברות מכל רחבי הארץ ומקיימת ארועים קהילתיים ועוסקת בסוגיות נוספות הנוגעות להזדקנות בקהילת הלהט״ב. בשנת 2018, החלה לפעול בפייסבוק גם קבוצת ״דורותיאה״ שמקיימת פעילויות חברתיות מגוונות לנשים לסביות מבוגרות. הקשר בין שתי ההתארגנויות אינו ברור.',
