    'description': '״בת המדבר - עמותה לקידום מעמד האישה הבדואית בחורה״ (ע.ר. 580405934) הוקמה בשנת 2003 ומטרותיה הן: ״קידום מעמד האישה הבדואית בתחומי חינוך, בריאות ותעסוקה. קידום השכלת נשים ושיבוצן במסגרות תעסוקה מתאימה. תמיכה וחיזוק במשפחות חד-הוריות והעלאת מודעות בקרב נשים לנושא בריאות״. העמותה עוסקת בהעצמת נשים בדואיות, בעיקר חד הוריות כתוצאה מפוליגמיה, באמצעות פרויקטים של הכשרה מקצועית בתחום הרקמה והתפירה ומכירתן במרכז המבקרים. את העמותה הקימה נהאדה אל-עוקבי שהיא גם היועצת למעמד האישה בח\'ורה.',
    'description_ar': 'تأسست ״ابنة الصحراء - جمعية للنهوض بمكانة المرأة البدوية في حورة״ (ج. م580405934) في عام 2003 وأهدافها هن: ״تعزيز مكانة النساء البدويات في التعليم والصحة والتوظيف. تطوير تعلم النساء ومرافقتهن في اطر تشغيلية مناسبة. دعم وتقوية الأسر ذات العائل الواحد وزيادة الوعي بين النساء حول القضايا الصحة ״. تعمل الجمعية بهدف تمكين النساء البدويات، خاصة العائلات الوحيدات نتيجة لتعدد الزوجات في العائلة الواحدة، من خلال مشاريع التدريب المهني في مجال التطريز والخياطة وبيعها في مركز الزوار. تأسست الجمعية بمبادرة من ناهدة العقبي التي تعمل أيضا كمستشارة لمكانة المرأة في حورة.',
