        'title': 'ספרן, חנה, הרטל, רחלי וששון-לוי, אורנה (2016). הי[א]סטוריה לסבית מקומית: פעילות, מאבקים והישגים. בתוך: הראל, אלון, לושינסקי, יניב ומורגנשטרן, עינב (עורכים), <b>זכויות הקהילה הגאה בישראל: משפט, נטייה מינית וזהות מגדרית</b> (עמ\' 45–80). האוניברסיטה העברית, המכון למחקרי חקיקה ולמשפט השוואתי. צפרירים: נבו.'
