    'description': 'גאווה בליכוד, התא הגאה של המפלגה, הוקם בשלהי 2011, על ידי עו״ד אוון כהן. התא פועל לקידום זכויות הקהילה הגאה במפלגה ומחוצה לה. התא הוביל לשינוי חוקת הליכוד כך שתכלול התנגדות לאפלייה על רקע נטייה מינית, והיה מעורב בבחירתו של חבר כנסת אמיר אוחנה (אשר מונה בהמשך לשר המשפטים והשר הגאה הראשון בישראל, בממשלת המעבר של קיץ 2019) לרשימת המפלגה לכנסת בבחירות 2015. התא מארגן חוגי בית ומפגשים בנושאים הנוגעים לקהילה הגאה כגון: הומופוביה, פונדקאות ועוד. גאווה בליכוד זכה לביקורות קשות מצד חלקים ניכרים מהקהילה הלהט״בית, הן סביב הצבעותיה של המפלגה נגד חוקים שתומכים בזכויות הקהילה, והן סביב בחירותיה הפוליטיות של המפלגה בכלל, ומצא את עצמו לא פעם בעימות עם ארגוני קהילה אחרים.',
