    'description': 'בשנת 2000 הוקם בחיפה ביוזמת ובמימון הרשות למלחמה בסמים בית טיפולי לנשים בקהילה. המסגרת, שנקראה ״מב״ט נשי״ היתה התכנית היחידה בישראל לטיפול בהתמכרויות שיועדה לנשים בלבד והופעלה על ידי המרכז לטיפול בנפגעי סמים ברמב״ם בשיתוף משרד הרווחה והאגודה לבריאות הציבור (חברת בת של משרד הבריאות). ב-2015, הוציא משרד הרווחה את השירות למכרז אשר בו זכתה עמותת ״הדרך״ אשר מפתחת ומפעילה שירותים למכורים. הבית נותר באותה הכתובת (מסדה 26) ושמו שונה ל״דרך אריאלה״.',
