    'description': 'האגודה למען הלהט״ב בישראל הוקמה בתל אביב בשנת 1975 תחת השם ״אגודה לזכויות הפרט״, והייתה חלוצת ארגוני הקהילה בארץ. בשנת 1982 נרשמה כעמותה. לעמותה היו סניפים מקומיים בחיפה ובבאר שבע, אשר נסגרו בתחילת שנות ה - 2000 והוחלפו על ידי קבוצות פעילות אזוריות. פעילויותה המגוונות של האגודה מכוונות לקדם את מעמדה החוקי, המשפטי והחברתי של הקהילה הגאה, להעניק שירותים ומענים מקצועיים, לתמוך בפיתוח יוזמות קהילתיות, להנגיש מידע לאוכלוסיות מודרות ומוחלשות בקהילה ולסייע לקהילות אלה לפתח נראות והכרה ציבורית. בנוסף, האגודה פועלת גם לשימור הזיכרון הקהילתי, לציון אירועים קהילתיים ופיתוח וקידום אירועי גאווה. כמו כן, האגודה מפעילה קו תמיכה טלפוני בשם ״הקו הלבן״ עבור חברי הקהילה.',
