    'description': 'עמותת ״האור שבקצה״ ממוקמת בירושלים ותקוע. מציעה ליווי ותמיכה נפשית וכספית לנשים מוכות וילדיהן לאחר צאתן ממקלט לנשים מוכות. מייסדת העמותה היא נאווה מלכיאל, מאמנת אישית. עיקרה של התמיכה היא במתכונת של ליווי אישי. בעמותה מצמידים לכל אישה מלווה התומכת בהשתלבותה בעבודה ובחיי הקהילה, בעזרה לילדים, במימוש זכויות מול מוסדות המדינה וכדומה. כמו כן העמותה מסייעת, בהתאם ליכולת, בפרויקטים מיוחדים, מעבר לדירה חדשה, עזרה לילדים בקייטנות, ריהוט בסיסי, סלי מזון, רכישת מקצוע וכיו״ב.',
