    'description': 'קואליציית ״האקדח על שולחן המטבח״ נוסדה בשנת 2011 על ידי רלה מזלי וסמדר בן-נתן כקמפיין רב ארגוני שמתמקד בהשפעות המגדריות של תעשיית האבטחה והשמירה בישראל, כולל רצח בנות ובני משפחה בנשק חברות אבטחה שנישא מחוץ לשעות העבודה. הקואליצייה פועלת תחת ארגון ״אשה לאשה-מרכז פמיניסטי חיפה״ ומאגדת 13 ארגוני חברה אזרחית: אשה לאשה, איתך-מעכי, האגודה לזכויות האזרח בישראל, הכצעקתה, ל.א. לאלימות נגד נשים, מרכז נגה למען נפגעי עבירה, נשים לגופן, פסיכואקטיב-אנשי ונשות בריאות הנפש למען זכויות אדם, פרופיל חדש, קואליציית נשים לשלום, רופאים לזכויות אדם, שדולת הנשים ותמורה-המרכז המשפטי למניעת אפליה. פעילות הקואליצייה כוללת: (1) קמפיינים ציבוריים ותקשורתיים על ההשפעה של נשק קל במרחב הציבורי; (2) פנייה לחברי כנסת ושרים בנושאי חקיקה הנוגעים לכלי ירייה; (3) עתירות לבית המשפט על נזיקין (מוגבלות גופנית) כתוצאה משימוש בלתי תקין בכלי נשק; (4) תיעוד ומחקר על נשק קל במרחב האזרחי בישראל; (5) קבוצות למידה.',
