    'description': 'הבית הפתוח בירושלים לגאווה ולסובלנות הוקם ב - 1997 על ידי קבוצת מתנדבים ירושלמים. בשנתיים הראשונות לפעילותו פעל הארגון ללא מקום קבוע, ובמהלכן תוכנן המרכז הקהילתי שפעל בהתחלה במדרחוב ברחוב בן יהודה ובמהשך ברחוב הסורג. הארגון פועל בשני מישורים מרכזיים - הפעלת שירותים ותמיכה לחברי הקהילה הלהט״בית, ופעולה למען שינוי חברתי בנושאים הנוגעים לקהילה, ובעיקר להפיכתה של ירושלים לעיר שיוויונית ופלורליסטית יותר. מאז 2002 מארגן הבית הפתוח את מצעד הגאווה בירושלים וב - 2006 אירח בעיר את אירועי הגאווה העולמיים. הבית הפתוח פועל כמרכז קהילתי בו יש סלון, ספריה ותערוכות מתחלפות. הוא מפעיל קבוצת נוער וצעירים בשם בנ״צ, מרפאה פתוחה, שירותי יעוץ ותמיכה פסיכולוגים, מערכים חינוכיים בבתי ספר תיכונים ברחבי העיר וקבוצות פעילות יעודיות לנשים, טרנס*, משפחות ועוד.',
