    'description': 'הגג הורוד הוא מעון להלנת חירום לצעירים וצעירות להט״בים, בגילאי 18-25 שנפתח בתל אביב במרץ 2018 על ידי משרד העבודה והרווחה, המופעל על ידי עמותת ״אותות״. המעון יכול להכיל עד 12 צעירים וצעירות והשהות בו מוגבלת לארבעה חודשים לכל היותר, לאחריהן מופנים השוהים למסגרת המשך בקהילה. המקום מציע לפונים, לפני הכל מיטה חמה, מקלחת וארוחה, אולם בהמשך הוא מספק גם סיוע במיצוי זכויות, הנגשת שירותי בריאות; טיפול ותמיכה רגשית; סיוע בתחום התעסוקה; ליווי משפטי; גישור מול המשפחה; תיווך לשירותים ולמענים קיימים והשמה במסגרות המשך.',
