    'description': 'בנובמבר 1998 פרסמה הוועדה הבין-משרדית לטיפול באלימות במשפחה דוח והמלצות שאומצו על ידי הממשלה באפריל 1999. הממשלה קבעה שהוועדה הבינמשרדית תמשיך את תפקידה כצוות קבוע ותלווה את יישום ההמלצות. דו״ח מבקר המדינה ב-2001 הצביע על כך שהוועדה לא המשיכה לפעול וכן הצביע המבקר על היעדרו של מעקב קבוע לליבון מקרי רצח של נשים לשם בחינת טיב המעורבות הטיפולית של לשכת הרווחה במשפחה. בעקבות הדוח קיבלה ועדת השרים לתיאום, מינהל וביקורת המדינה את החלטה תמ/42 ,שבמאי 2002 קיבלה תוקף של החלטת ממשלה. בהחלטה נקבע, בין היתר: ״בדיקת כל מקרה רצח של אישה בידי בן זוגה תהיה באחריות מנכ״ל משרד העבודה והרווחה, בשיתוף עם המשרד לביטחון הפנים, משטרת ישראל, משרד החינוך, משרד הבריאות והרשות המקומית אשר בתחומה היה האירוע, במטרה להפיק לקחים לגבי טיפולה של לשכת הרווחה במשפחה, טרם הרצח״. בפועל, הוועדה החלה את עבודתה בשנת 2004. הוועדה מתכנסת לאחר כל רצח של אישה בידי בן זוגה אולם לעיתים היא דנה בכמה מקרים יחד. עו״ס איילה מאיר, מנהלת השירות לרווחת הפרט והמשפחה במשרד הרווחה, היא העומדת בראש הוועדה בשנים האחרונות. לוועדה אין מעמד סטטוטורי, אין לה סמכויות מוגדרות וברורות או נוהל עבודה כתוב ומוגדר.',
