    'created': {
      'by': 'יעל חסון',
      'on': '06/04/2018'
    },
    'description': '״הוועדה הציבורית לקידום נשים בשלטון המקומי״ הוקמה באפריל 2016 ברוח החלטת הממשלה מספר 481 מיום 30.06.2013 שבה אושר דו״ח הועדה לשיפור ניהול ההון האנושי בשירות המדינה ומעמדן של הנשים בשירות המדינה (ועדת שטאובר). בכתב המינוי של ועדה צויין כי מטרת הועדה היא ״לחולל שינוי ולהביא לקידום מעמדן של נשים בשלטון המקומי [...] הוועדה תמליץ על דרכים לקידום נשים הן בדרג הנבחר והן בדרגים המקצועיים, הן ברשויות המקומיות והן בתאגידים העירוניים״. בראש הוועדה עמדה גב\' ליזי דלריצ\'ה, ראש המועצה המקומית גני תקוה וחבריה כללו את מנהלת הרשות לקידום מעמד האשה ונציגים מהמרכז לשלטון מקומי; המשרד לפריפריה, הנגב והגליל; משרד הפנים; משרד הכלכלה; משרד האוצר; ונציבות שירות המדינה. \nהוועדה הציבורית בחנה את ייצוגן של נשים ברשויות המקומיות בין השנים 2003-2013. על פי דו״ח הועדה שהוגש לשר הפנים אריה מכלוף דרעי במרס 2018, רק ב-2.5% מהרשויות בישראל מכהנת ראשת עיר. בבחינת אחוזי הייצוג במועצות המקומיות נמצא כי ב-2003 היוו נשים רק 9% נשים מכלל החברים במועצות, זאת אל מול 13% בשנת 2013. בבדיקה זהה שכללה 113 רשויות, ללא רשויות חרדיות וערביות, היו אחוזי הייצוג גבוהים יותר, אך עדיין בלתי שוויוניים: 13% נשים במועצות בשנת 2003 , אל מול 21% בשנת 2013. מרבית העלייה באחוזי הייצוג ברשויות אלה התרחשה במחצית השנייה של העשור (2008-2013). על פי מסקנות הוועדה, על אף שניתן להבחין במגמת עלייה בייצוג הנשים במועצות, מדובר בעלייה מתונה מאוד שעדיין לא מביאה לייצוג שוויוני. הועדה גם עסקה בהגדרות התפקיד של היועצות לקידום מעמד האשה ברשויות המקומיות והמליצה להכין קורס הכשרה מחייב לתפקיד היועצות לקידום מעמד האישה, תוך מתן קדימות לרשויות במעמד סוציו-אקונומי נמוך.',
