    'created': {
      'by': 'יעל חסון',
      'on': '20/09/2018'
    },
    'description': 'הוועדה הוקמה בעקבות ״מקרים קשים של אלימות במשפחה״ שהועלו לדיון בנוכחות השר לביטחון פנים ושר הרווחה ב-10.9.2014. בדיון הוחלט על הקמת ועדה בין-משרדית בראשות המשנה למנכ״ל המשרד לביטחון הפנים, מר אליעזר רוזנבאום. מטרת הוועדה היתה לפעול ל״טיוב הטיפול המערכתי בהתמודדות עם תופעת האלימות במשפחה״. תפקידי הוועדה כללו: א) גיבוש תמונת מצב עדכנית ומתמשכת בנוגע לתופעת האלימות במשפחה (מאפייני התופעה, היקף התופעה, מענים וחסמים קיימים);  ב) גיבוש מדיניות ודרכי פעולה מומלצות וסנכרון מאמצים אופרטיביים בין משרדי הממשלה השונים והארגונים הפועלים בתחום. הוועדה כללה נציגים מכלל המשרדים העוסקים בצמצום תופעת האלימות במשפחה: משרד הרווחה והשירותים החברתיים, משרד המשפטים, משרד הבריאות, משרד העלייה והקליטה, משרד החינוך, משרד ראש הממשלה והמשרד לביטחון הפנים. כמו כן הופיעו לפני הוועדה נציגי ממשלה שאינם חברים בוועדה וארגונים חוץ ממשלתיים שהציגו את עשייתם בתחום הטיפול באלימות במשפחה. המלצות הוועדה שהוגשו ביוני 2016 עסקו בצורך לגבש מדיניות לאומית לטיפול בנושא על ידי: אימוץ הגדרה ממשלתית אחידה לתופעת האלימות במשפחה על כל גווניה; הסרת חסמים ארגוניים וחוקיים המקשים או מונעים העברת מידע על הפוגעים והנפגעים בין הגורמים המטפלים הרלוונטיים למקרה; יצירת מנגנונים וכלים לשיפור שיתוף הפעולה על הרצף הטיפולי; בניית כלים להערכת מסוכנות של פוגעים; הרחבת סל הטיפול והשיקום לנשים, גברים וקשישים נפגעי אלימות במשפחה ועוד. ביוני 2017 ועדת השרים למאבק באלימות בראשות השר ארדן אישרה את המלצות הצוות הבין-משרדי ליישום התכנית הלאומית למאבק באלימות במשפחה. נכון לספטמבר 2018 לא אושרו התקציבים הנדרשים ליישום התכנית.',
