    'description': 'ה״ועדה לקידום מעמד האישה״, הוקמה בכנסת ה-13 על פי החלטת הכנסת מיום 16 בנובמבר 1992 במטרה ליישם את אחת ההמלצות המרכזיות שהופיעו בדו״ח הוועדה הממלכתית לקידום מעמד האישה (1978). יושבת הראש הראשונה של הוועדה הייתה חברת הכנסת יעל דיין וכל יושבי ראש הוועדה מאז הקמתה היו נשים , למעט גדעון סער. תחום העיסוק העיקרי של הוועדה הוא קידום מעמד האשה לקראת שוויון בייצוג; בחינוך ובמעמד האישי, וכן למניעת אפליה בשל מין או נטייה מינית בכל התחומים; להקטנת פערים בכלכלה ובשוק העבודה ולמאבק באלימות כלפי נשים. בדצמבר 2013 שונה שם הוועדה לשמה הנוכחי, ביוזמת יושבת ראש הוועדה, עליזה לביא.',
