    'description': 'החברה לרפואת להט״ב הוקמה בשנת 2016 על ידי ד״ר רות גופן וד״ר גל וגנר-קוסקו, המשמשים גם כיו״רים שותפים שלה ואושרה על ידי ההסתדרות הרפואית בישראל. מטרת החברה היא להרחיב את הידע הקיים לשם מתן טיפול רפואי מיטבי לקהילת הלהט״ב, הסברה בנוגע לקהילה ולצרכיה ומאבק בלהט״בופוביה בקרב צוותים רפואיים, השפעה על קביעת מדיניות ונהלים הנוגעים לקהילה מול המדינה ומול קופות החולים ובתי החולים, קידום בריאות בתוך הקהילה הלהט״בית ועידוד ותמיכה במחקרים רפואיים על הקהילה, כמו גם שיתופי פעולה עם חברות מקבילות בחו״ל. פעילותה כוללת בין היתר: כתיבת ניירות עמדה בשיתוף ההסתדרות הרפואית ביחס לאימוץ ולטיפולי המרה; הרצאות לצוותי רפואה; ארגון כנסים בנושאים כמו טיפולים רפואיים בטרנס*; סמים וסקס ושיוויון בבריאות; העברת סדנאות לחברי וחברות הקהילה; פרויקט הכנסת נאמני קהילה לבתי החולים; והסברה בכנסת בנושאים הקשורים לקהילה.',
