    'description': 'החוג הגאה במפלגת העבודה הוקם ב-2011 על ידי דן סליפר וניב נוניס, בעידודה ותמיכתה של היו״ר דאז שלי יחימוביץ׳. מפגש הפעילים הראשון התקיים בינואר 2012. החוג פעל לקידום יוזמות חקיקה הנוגעות לקהילה והכנסת שינויים התומכים בקהילה במצע המפלגה. הוא עודד את ההתפקדות למפלגה והצליח להגדיל בצורה ניכרת את מספר הצירים הלהט״בים בוועידת המפלגה. החוג מקיים מפגשי פעילים ומפגשים עם ח״כים מטעם המפלגה. המפגשים מתרחשים בעיקר בתל אביב, במקרים רבים בדירות פרטיות. בנוסף פעילי החוג לוקחים חלק בפעילויות מחאה קהילתיות ומצעדי גאווה בכל רחבי הארץ.',
