    'description': 'החוג הפמיניסטית במפלגת העבודה הוקם בסוף שנת 2016 על ידי קבוצת פעילות שהחלו להתארגן לפני הבחירות לכנסת ה-20 (2015). מטרת החוג היא לפעול אל מול החוגים בתוך מפלגת העבודה כדי להטמיע נקודת השקפה מגדרית ולקדם סוגיות פמיניסטיות בתוך המפלגה. בנוסף, החוג מקיים שיתופי פעולה עם פורומים ותאים פמיניסטיים במפלגות אחרות. בין מייסדות החוג: אור סרי, עדי עזוז, יעל שרר ולילי פרגמניקוב.',
