    'description': 'עמותת ״החוט המשולש - בית חם לנוער״ הוקמה בירושלים בשנת 1999 על ידי יגאל ודנה גולדשטיין. העמותה מנסה לספק מענים הוליסטים לצעירים וצעירות בגילאי 14-25 במצבי סיכון, מחוסרי קורת גג ומסגרת. העמותה מפעילה שלושה מרחבים - 1. מרחב פתוח לגילאי 14-25 הפועל במרכז ירושלים מ - 10:00 - 22:00, אליו יכולים להגיע בני נוער וצעירים כדי לקבל ארוחה חמה, תמיכה נפשית, מרחב קהילתי, מקלחת וכביסה, תכניות העשרה והכשרה לתעסוקה. 2. בית לבנות בגילאי 18-25 שהוקם בשיתוף משרד הרווחה, בו הן יכולות להתגורר במשך שנתיים ולקבל תמיכה נפשית, הכשרה לתעסוקה וסיוע במציאת עבודה וסדנאות העצמה וכישורי חיים. הבית יכול להכיל עד 12 צעירות וב - 2016 היו לו 80 בוגרות. רבות מהמגיעות אליו נמצאות בשלבי הריון מתקדם, הן מקבלות הכשרה ללידה ולהורות, ליווי ותמיכה בהחלטה האם למסור את הילד לאימוץ או לגדלו והן יכולות להמשיך ולשהות במעון עד חצי שנה לאחר הלידה. 3. בית לבנים בגילאי 18-25 שמציע מענה לעד 18 בנים בו זמנית, המקבלים אף הם, תמיכה נפשית והכשרה תעסוקתית, בתוכנית של 6-18 חודשים. העמותה משתפת פעולה עם מסעדת ״אנה״ בבית אנה טיכו, המספק הכשרה ושילוב תעסוקתי לבני נוער בסיכון.',
