    'description': 'היחידה לחקר בריאות האישה והילד, בראשותה של פרופ\' ליאת לרנר-גבע הוקמה ביולי 2001 כאחת היחידות בחטיבת האפידמיולוגיה במכון גרטנר לאפידמיולוגיה וחקר מדיניות בריאות (חל״צ). מטרת היחידה: מחקרי איכותי שישמש בסיס לקבלת החלטות ומדיניות בתחום בריאות האישה והילד. פעילות העמותה - פיתוח מתודולוגיות וכלי מחקר, השתתפות בוועדות ומועצות לאומיות של משרד הבריאות, הוראה והדרכת סטודנטים לרפואה.',
