    'description': 'היחידה לקידום נשים הוקמה באוקטובר 2000 בוועידה שבה התאחדו תנועת הקיבוץ הארצי והתנועה הקיבוצית המאוחדת לתנועה אחת - ״התנועה הקיבוצית״. קדמו ליחידה המדורים לשוויון בין המינים בתק״מ ובקיבוץ הארצי שפעלו בין 1981-1990 במטרה לקדם תכנית אסטרטגית משותפת אך נסגרו בשל חוסר בתקציבים. היחידה פעלה לקידום נשים לתפקידי מנהיגות, מאבק בהטרדות מיניות (כולל קו חם שפעל כשנה ונסגר בשל מיעוט פניות), חינוך מגדרי ועוד.',
