    'description': 'המדורים נוסדו בשנת 1982 כשיתוף פעולה בין שתי התנועות הקיבוציות הגדולות - הקיבוץ הארצי והתנועה הקיבוצית המאוחדת. בראשם עמדו נשים בעלות עמדות פמיניסטיות מוצהרות. שיתוף הפעולה בין המדורים הביא לתכנית פעולה ואסטרטגיה משותפת. המדורים פעלו ברמת הפרט, ברמת הקיבוץ וברמה התנועתית. ערכו סדנאות להעלאת מודעות, הרצאות וכנסים, פרסמו כתב עת בשם ״המשווה״, אספו וסיפקו מידע אודות תופעות של אי שוויון בקיבוצים, ניהלו מאבק להגברת שיעורי נשים בתפקידי מנהיגות וניהול, הקימו קרן מענקים לנשים שלמדו ניהול וקידמו העלאת שיעורי הגברים בעבודות החינוך והטיפול בקיבוצים. לאורך כל הדרך ספגו התנגדויות מחברי וחברות קיבוץ, על רקע היותן פמיניסטיות. פורקו ב-1992 על רקע קיצוצים תקציביים בין היתר בעקבות משבר הקיבוצים.\nבאוקטובר 2000 אוחדו שתי התנועות ל״תנועה הקיבוצית״ ובוועדיה משותפת בקיבוץ כנרת הוקמה ״היחידה לקידום נשים״.',
