    'description': '״המועצה לקידום נשים במדע ובטכנולוגיה״, הוקמה מתוקף החלטת ממשלה 2660 מדצמבר 2000 במטרה ״להגדיל את התוצר הלאומי ולשפר את איכות החיים באמצעות הגדלת השתתפות נשים בתחומי מדע וטכנולוגיה״. המועצה מתאמת בין גורמים ממלכתיים, ציבוריים ופרטיים לצורך קידום נשים במדע ובטכנולוגיה: המועצה מתכננת ומבצעת פעילויות המעודדות קידום נשים במדע ובטכנולוגיה, מרכזת את הפעילות הישראלית מול האיחוד האירופי ופועלת להעלאת המודעות הציבורית לבעיות הקשורות בתחום. נוסף לכך, תומכת המועצה במיזמים ובפעולות לקידום נשים בחינוך, באקדמיה ובתעשייה. במועצה מכהנות: נציגת המדענים הראשיים במשרדי הממשלה, מנהלת מינהל המדע במשרד החינוך, מנהלת המכון להכשרה טכנולוגית בתמ״ת, נציגת הוועדה לתכנון ותקצוב, יו״ר פורום המנהלות בתעשייה ויועצת הרמטכ״ל לענייני מגדר.',
