    'description': 'המטה למאבק בסחר בנשים הוקם בשנת 2003, כשיתוף פעולה בין עמותת עצו״מ – עבודה צדקה ומשפט (ע״ר 580411775) ומשרד עו״ד כבירי-נבו-קידר-בלום ושות\'. המטה פועל בתחומי המשפט, קידום המדיניות והפעילות הציבורית במטרה למגר את תופעות הזנות והסחר בנשים לישראל. בתחילת דרכו קידם המטה את העלאת המודעות לתופעת הסחר בנשים וליצירת לחץ מקומי ובינלאומי על ישראל להביא למיגורה. לקראת סוף העשור הראשון של שנות ה-2000 החל המטה למקד את עיקר מאמציו במאבק בצריכת זנות. המטה הוביל, בשיתוף עם ארגוני חברה אזרחית וחברי כנסת מכלל הקשת הפוליטית, את המאבק להפללת לקוחות זנות בישראל. בנוסף, פועל המטה בזירה החינוכית במטרה להגביר את תודעת הציבור להשפעות הקשות והחמורות של תעשיית הזנות ומעודד שיח ציבורי בנושא, באמצעות ערוצי התקשורת, הרצאות ופאנלים. מאז שנת 2013 המטה מרכז את ״הקואליציה למאבק בזנות״ הכוללת 13 ארגונים ישראליים הפועלים בשותפות במאבק בצריכת זנות, בהם ארגונים ממשלתיים, חצי ממשלתיים ועמותות. מטרת הקואליציה היא לקדם את הרחבת שירותי הטיפול והשיקום לשורדות הזנות, להיאבק בזנות באמצעות קידום חקיקה לאיסור צריכת זנות ולפעול לשינוי תפיסות הציבור והעלאת מודעות ביחס לזנות.',
