    'description': 'המקלט לנשים מוכות הוקם על ידי ויצו בשנת 1995 בשיתוף ובמימון משרד הרווחה ופועל בדומה למקלט שייסדה ויצו באשדוד בשנת 1983. הוא אחד משלושת המקלטים הפועלים בירושלים. במהלך שנות פעילותו סייע המקלט ליותר מ- 1400 נשים מרקעים מגוונים ול-1800 ילדים שהגיעו עימן. במהלך השהייה במקלט, שנמשכת כחצי שנה, מקבלות הנשים חדר משפחתי וליווי לפי צורכיהן - כולל סיוע מצד עובדות סוציאליות ופסיכולוגים. הילדים זוכים להעשרה לימודית, חוגי העשרה, אבחונים, טיולים וקייטנות.',
