    'description': 'המרכז הגאה הוקם על ידי עיריית תל אביב בינואר 2008 כשירות עירוני ייחודי, בעקבות סקר בנוגע לצרכי הקהילה שהתקיים ב - 2004. הוא ממוקם במבנה בן שלוש קומות בגן מאיר. המרכז מספק מגוון שירותים לקהילה הגאה - מרכז תרבותי, מקום מפגש לקבוצות פעילות (כולל קבוצה ביסקסואלית, ״רדוגה״ - להט״ביות רוסיות ו״בשלה״ - לסביות מגיל 45 ומעלה), חוגים יעודיים כמו יוגה קווירית והגנה עצמית לטרנס*, קליניקה לבדיקות מחלות מין ומרפאה, גן ילדים גאה, בית קפה, שירותי יעוץ שונים, מרחב פעילות לארגונים כמו חוש״ן ואיג״י ועוד. המימון למרכז ולעובדים המועסקים בו מגיע ברובו מעיריית תל אביב, בשיתוף עם ארגוני חברה אזרחית. כמו כן, מספר רב של מתנדבים המעורבים בהפעלתו של המרכז.',
