    'description': 'המרכז לחקר הרפואה המגדרית במרכז הרפואי רבין, הוקם ב-2012 על ידי פרופ\' מרק גלזרמן שכיהן קודם לכן את מחלקות הנשים בבית החולים רבין (בלינסון) והשרון ואף היה מהמייסדים של החברה הישראלית לרפואה מגדרית בשנת 2009. המרכז המרכז פועל בשיתוף פעולה הדוק עם החברה הישראלית לרפואה מגדרית, יוזם מחקרים ומרכז את העשייה המחקרית בנושא. מטרותיו: לקדם את הרפואה המגדרית בישראל, ליזום מחקר ברפואה המגדרית, להעלות מודעות בקרב הצוות הרפואי והפארא-רפואי לגבי הרפואה המגדרית, ליזום יישום של עקרונות הרפואה המגדרית בפעילות הקלינית, באבחון ובטיפול.',
