    'description': 'המרכז למשפחה האלטרנטיבית (ע״ר 580448512) נרשם כעמותה ב- 2005, וככל הנראה הפסיק את פעילותו בסביבות 2009-2010. מטרות העמותה היו: ״לפעול למען המשפחה האלטרנטיבית, קרי: משפחה שאינה מורכבת בהכרח מגבר ואשה אשר קיימו ו/או מקיימים ביניהם קשר זוגי, ואשר הינם הורים לילדים משותפים. לפעול במיוחד למען המשפחה האלטרנטיבית בקרב חברי הקהילה ההומו-לסבית. מתן סיוע והדרכה לגברים ולנשים המעוניינים להיות הורים ללא יצירת קשר זוגי.״ המרכז פעל על מנת לסייע למשפחות אלטרנטיביות, בין היתר במתן סיוע והדרכה להומואים ולסביות המעוניינים להיות הורים במסגרות לא זוגיות.',
