    'description': '״המרכז למשפחה היהודית״ (ע״ר 580630325) נוסד ב-2016 על ידי הארגון ״יד שרה״ ובהובלת ד״ר שלומית להמן, עובדת סוציאלית שמתמחה באלימות במשפחה שהיתה גם חברה ב-״פורום תקנה״. מטרות העמותה הן ״לפעול למניעת אלימות במשפחה בישראל. ללמוד וללמד ניהול חיי משפחה הרמוניים ולסייע להשגת שלום בית בתוך המשפחה. לשמש כמרכז חינוכי והפעלת קורסים, הדרכה והרצאות. להעניק תמיכה נפשית וחומרית לילדים, נשים וגברים הסובלים מאלימות או התעללות במשפחה - פיזית או נפשית.״ העמותה איננה פעילה, אך המרכז עצמו פועל כחלק מ״יד שרה״ שיתוף פעולה עם מחלקת הרווחה בעיריית ירושלים ומעניק שירותים בתחום המניעה והטיפול באלימות במשפחה במגזר החרדי, בדגש על עבודה עם רבני קהילות לשבירת השתיקה סביב הנושא.',
