    'description': 'המרכז למשפט, מגדר ומדיניות חברתית, בפקולטה למשפטים באוניברסיטת חיפה הוקם ב-2014 על ידי פרופ\' שולמית אלמוג ופרופ\' נויה אימלט. מטרת המרכז הינה לקיים מסגרת אקדמית ראשונה מסוגה, שמשמשת מחד גיסא כמוקד של מחקר והוראה ומהווה מאידך גיסא גשר למקבלי ההחלטות תוך התמקדות במשפט ככלי מרכזי להנעת רפורמות, שתכליתן העצמת נשים וקידום השוויון המגדרי בישראל. לצד זיהוי שאלות של מדיניות ציבורית, עוסק המרכז בקידום הדיון בסוגיות של אי שוויון מגדרי במעגלי השפעה שונים: משרדי ממשלה, מחוקקים, שופטים, עורכי דין, אקדמיה, ארגוני שטח פמיניסטים בארץ ובעולם.',
