    'description': 'הפורום הוקם ב-1996 על ידי שתי״ל וב-1997 פרשו ממנו מספר ארגונים והקימו את הקואליציה הארצית למשפחות חד הוריות. שתי הקואליציות פעלו במקביל. במחקר של ענת הרבסט צויין שבפורום היו שתי נציגות מכל עיר. פעילות מרכזיות בפורום היו חנה מרגלית, שהקימה עמותה בשם ע.ל.מ.ה לחד הוריות ברמת גן וג\'ודי דון שהיתה אחר כך פעילה גם בארגון הנכים. לא ברור מתי הפסיק הפורום לפעול באופן פורמלי, אך לא נמצא תיעוד לפעילות אחרי שנת 2004.',
