    'description': '״הצוות הבין משרדי לבחינת הכלים לצמצום צריכת זנות בישראל״ הוקם ביוני 2016 על ידי שרת המשפטים איילת שקד במטרה לבחון את הכלים לצמצום צריכת הזנות בישראל, וכן לבחון את האפשרות לקביעת עבירה של צריכת זנות. בראש הצוות עמדה עו״ד אמי פלמור, מנכ״לית משרד המשפטים והשתתפו בו נציגות ונציגים ממשרד המשפטים, יחידת תיאום המאבק בסחר בבני אדם, הסניגוריה הציבורית, פרקליטות המדינה, המשרד לביטחון פנים, משטרת ישראל, משרד החינוך, משרד הרווחה, משרד האוצר, משרד הבריאות והמשרד לשוויון חברתי. הצוות הגיש דוח מסכם בדצמבר 2017 שכלל סקירה כללית של מאפייני הזנות בישראל ניתוח מודלים ממדינות אחרות לצמצום והתמודדות עם תופעת הזנות והתייחסות לתחום המשפט והאכיפה תוך דגש על הצעות החוק בדבר הפללת לקוחות זנות. המלצות הצוות עסקו בהיבטים חינוכיים (קיום הדרכות וקמפיינים ציבוריים להעלאת מודעות לצמצום צריכת זנות); שירותי רווחה (הרחבת השירותים המוצעים לעוסקים בזנות ולמבקשים לצאת ממעגל הזנות); הכשרות לאנשי מקצוע וסדנאות לקידום מין מוגן; גיבוש תכנית התערבות עבור גברים צרכני זנות; הגברת האמצעים לשיפור האכיפה של עבירות הנוגעות לזנות, זנות קטינים וסרסרות. בכל הנוגע להפללת לקוחות, הוצע לבחון ״מודל של אכיפה פלילית מדורגת, שייקבע במסגרת הנחיות אכיפה, במסגרתו ייעשה שימוש בסמכויות של סגירת תיק החקירה לאחר חקירה באזהרה ושל סגירת תיק בהסדר״. באוגוסט 2018 החליטה הממשלה על הקמת ״צוות לבחינת דרכי יישום של דו״ח הצוות הבין-משרדי לצמצום צריכת זנות ולבחינת האפשרות להפללת לקוחות זנות״ (החלטה מספר 4099) בראשות מנכ״לית משרד המשפטים ונציגי משרדים רלוונטיים. מטרת הצוות היא לבחון את המלצות הדוח ולנסח תכנית ליישומן, כולל מקור תקציבי ומועדי ביצוע.',
