    'description': 'ה״קואליציה נגד פורנוגרפיה״ הוקמה בשנת 2001 כחלק משיתוף פעולה בין ויקי שירן שהיתה חברה בקשת הדמוקרטית המזרחית ופרסמה מאמר במדור דעות ב״ידיעות אחרונות״ (זנות מצולמת, 23.07.2001) לבין אסתר הרצוג מתנועת ש.י.ן שפרסמה מאמר דומה במדור דעות ב״מעריב״ (נשים נגד פורנוגרפיה 22.07.2001). הקואליצייה התגבשה במחאה על מתן היתר לערוץ שידורים של ארוטיקה ופורנוגרפיה בטלוויזיה המסחרית והשקתם של שלושה ערוצים למבוגרים על ידי חברות הכבלים ביולי 2000 והתיקונים השונים ל״חוק התקשורת״ שהתרחשו בעקבותיו. על רקע זה החלה ההתארגנות של הקואליציה נגד הפורנוגרפיה שכללה כ-15 ארגונים (ש.י.ן, ויצו, אמונה, שדולת הנשים, ל.א. לחימה באלימות, אלמאנהל, אחותי, ה״ן הרצליה נשים, פרלמנט נשים, הקשר הפמיניסטי, מועצות נשים ברשויות המקומיות, התנועה לגבריות חדשה והקשת הדמוקרטית המזרחית) ומטרותיה היו: \nלהביא לחקיקה האוסרת באופן אפקטיבי שידורים פורנוגרפיים בערוצי השידור הציבוריים, מתוך השקפת עולם פמיניסטית חברתית; \nלהביא למודעות הציבור את נזקי הפורנוגרפיה, את הקשר בינה לבין דיכוי נשים ולהתנהגות אלימה כלפיהן; \nלשנות את התפיסה החברתית הרואה בפורנוגרפיה סממן של חברה חופשית ופתוחה מבחינה מינית ובמתנגדים לה, מייצגי תפישת עולם דתית שמרנית. \nעל מנת לקדם מטרות אלו יצרו חברות הקואליצייה שיתוף פעולה פרגמטי עם פוליטיקאים מהמפלגות הדתיות והחרדיות כדי לנסח במשותף חוק מרחיב יותר ולהפוך את המאבק בפורנוגרפיה לנחלת הציבור. ביולי 2001, בעקבות פעילותם של חברי כנסת מהמפד״ל וש״ס ביחד עם הקואליציה הפמיניסטית נוסף תיקון 25 ל״חוק התקשורת״ שאסר על שידורו של ערוץ שעיקר שידוריו הם משדרי מין. התיקון קבע כי: ״בעל רשיון לשידורי כבלים לא ישדר שידורים (2א)... שחלק מהותי מתוכם כולל תכנים העוסקים במין בדרך של הצגת יחסים מיניים או בדרך של הצגת מעשים המיועדים לעורר גירוי מיני או שיש בהם ביזוי או השפלה מיניים או שהם מציגים אדם כחפץ זמין לשימוש מיני או כנתון להתעללות גופנית או מינית״. \nשינוי חקיקתי זה הביא את המועצה לשידורי כבלים ולשידורי לוויין לקיים דיונים ציבוריים ולפרסם פרשנות מרחיבה לפיה החוק חל על ערוצים של משדרי מין אך לא על שידורים בשיטת תשלום לפי צפייה. בהתאם לכך, קבעה המועצה כי ניתן לקיים שידור ערוצי מבוגרים בשיטת תשלום לפי צפייה ומגבלות נוספות. אולם, באוקטובר 2001 עתרה חברת פלייבוי לבית המשפט נגד התיקון לחוק בשל טענה כי הוא פוגע בחופש הביטוי, בחופש העיסוק ובזכות הקניין ובראשית חודש מרץ 2002 הפסיק ערוץ פלייבוי את שידוריו עקב קשיים במכירת הערוצים לפי התנאים החדשים. הדיונים בבג״ץ על עתירת פלייבוי נמשכו עד 2003 כאשר הוחלט (בג״ץ 5432/03) לאפשר את שידורי ערוץ פלייבוי. במקביל, אישרה הכנסת את תיקון מס\' 27 ל״חוק התקשורת״ האוסר לחלוטין שידורי פורנוגרפיה בטלוויזיה ו״חומר תועבה״. פעילות הקואליציה עוררה התנגדות מצד ארגונים פמיניסטיים אשר ביקרו את שיתוף הפעולה עם חברי כנסת דתיים וחששו שהחקיקה עלולה לפגוע בקהילה ההומו-לסבית. הקמת הקואליציה נגד שידורי הפורנוגרפיה היתה המאבק החברתי-פמיניסטי האחרון שבו היתה מעורבת ויקי שירן.',
