    'description': 'ה״קשר הפמיניסטי״ שנרשמה כעמותה (ע״ר 580252047) בשנת 1994 היא גלגול מאוחר של ״התנועה הפמיניסטית בישראל״ שנוצר לאחר הפרידה ממרכז הסיוע בתל אביב. אסתר עילם כותבת כי היוזמה נועדה לפתוח מרכז פמיניסטי עצמאי במתנ״ס בשכונת נווה אליעזר בדרום מזרח תל אביב אך פעילותה גוועה בשנת 1997 והיא נסגרה סופית בשנת 2002. מטרות העמותה היו: ״הקמת מרכז (בית) פמיניסטי בעיר תל אביב. הפעלת מרכז מידע לנשים. הפעלת קורסים וחוגים לנשים. ארגון כנסים, משמרות מחאה בנושאים פמיניסטיים ובנושאי מעמד האשה.״',
