    'description': '״התא לשוויון מגדרי - פמיניזם בתל חי״ הוקם בשנת 2017. לגלגול הנוכחי של התא הפמיניסטי במכללת תל חי קדמו מספר ניסיונות התארגנות במכללה בשנים 2009-10 מטעם סטודנטיות מהמקבץ ללימודי מגדר שקיימו אסיפות, הפגנות וארועים בקמפוס. מאז קיומו של התא, הוא פועל בשלושה מישורים: מפגשי שיח ולמידה בנושאי מגדר; יזום ארועים לכלל המכללה שמטרתם להעלות מודעות בנושאים שונים; יצירת פלטפורמה לעשייה אקטיביסטית פמיניסטית ושינוי מדיניות. תחומי הפעילות העיקריים שבהם עוסק התא כוללים: הטמעת חשיבה מגדרית, מאבק באלימות מגדרית, מאבק באלימות מינית, פעילות הקשורה לדימוי גוף וסטריאוטיפים, מאבק בהדרת נשים ועוד.',
