    'description': 'בתחילת 2007 הוגשו שתי תוכניות בין-משרדיות הנוגעות לאלימות נגד נשים: האחת לטיפול בנפגעות תקיפה מינית והשנייה להוצאת נשים ממעגל הזנות. את התכניות קידמה מרית דנון שהיתה מנהלת הרשות לקידום מעמד האישה והן אושרו על ידי ראש הממשלה דאז, אהוד אולמרט שהקצה להן תקציב ייעודי של 30 מיליון ₪ למשך שנתיים כסכום חד פעמי. בהמשך הוחלט לוותר על חלק מהסכום ובתמורה להכניס 20 מיליון ₪ לבסיס התקציב להמשך מימון השירותים שנבנו והורחבו במסגרת התכנית. התכנית הבין משרדית לטיפול בנפגעות תקיפה מינית החלה לפעול בשנת 2008 והיתה אמורה להביא להרחבה של 3 מרכזים רב-תחומיים קיימים והקמת 3 מרכזים חדשים, להקמה של 6 דירות מגורים, לחלופת אשפוז, להכשרות והשתלמויות לאנשי/נשות מקצוע ולתקצוב 25 עו״סיות לאיתור נערות וצעירות נפגעות תקיפה מינית. התכנית יושמה באופן חלקי על ידי משרד הבריאות ומשרד הרווחה ולאחר שנת 2013 צומצמו התקציבים שהועברו לתכנית.',
