    'description': 'בתחילת 2007 הוגשו שתי תוכניות בין-משרדיות הנוגעות לאלימות נגד נשים: האחת לטיפול בנפגעות תקיפה מינית והשנייה להוצאת נשים ממעגל הזנות. את התכניות קידמה מרית דנון שהיתה מנהלת הרשות לקידום מעמד האישה והן אושרו על ידי ראש הממשלה דאז, אהוד אולמרט שהקצה להן תקציב ייעודי של 30 מיליון ₪ למשך שנתיים כסכום חד פעמי. בהמשך הוחלט לוותר על חלק מהסכום ובתמורה להכניס 20 מיליון ₪ לבסיס התקציב להמשך מימון השירותים שנבנו והורחבו במסגרת התכנית. לצורך הכנת התכנית להוצאת נשים ממעגל הזנות הוקמה ועדה בין-משרדית בראשות מרית דנון. הוועדה גיבשה מתווה, הכינה תכניות עבודה ואישרה את תקציב הפעילות של התכנית. התוכנית החלה לפעול באופן חלקי בשנים 2008-2009 בשיתוף פעולה של משרדי ממשלה, רשויות מקומיות, ארגונים ועמותות. מטרות התוכנית כללו בין היתר צמצום ומניעה של הזנות ונזקיה בחברה הישראלית, שיקום נשים העוסקות בזנות וקידום החלמתן של נשים שורדות זנות. התכנית הובילה להקמתם של שלושה פרויקטים עירוניים: ״סלעית״ בתל-אביב, ״אופק נשי״ בחיפה ו״בשבילך״ בבאר-שבע ובאזור הדרום. בשנים שחלפו מאז הקמתה הועברה האחריות על השירותים למשרד הרווחה ובשנת 2018 פעלו כ-13 מסגרות טיפול (כולל הוסטלים, דירות חירום, ניידות ומרכזי יום וערב) שונות ב-5 תכניות: סלעית, אופק נשי, ניידות, ערים בלילה (על״ם) ובשבילך. מימון התכנית מגיע בעיקר מתקציב משרד הרווחה (76%), משרד הבריאות (16%) והשתתפות של רשויות מקומיות (4%).',
