    'description': 'התנועה לשוויון ושלום בין המינים הוקמה ב-2004 כשיתוף פעולה בין התנועה לגבריות חדשה, שדולת הנשים ותנועת ש.י.ן לשויון ייצוג נשים. התנועה נרשמה כעמותה (ע״ר 580460798) בשנת 2008. המטרות המוצהרות של התנועה היו: גינוי כל ביטוי או פעולה של אפליה, דיכוי, השפלה, פגיעה בכבוד או הגבלת החופש האישי של אישה ואיש בשל מינם/ן. טיפוח היחס המכבד כלפי גופם/ן של נשים וגברים ומיניותם/ן. \nעל מייסדי העמותה נמנו חן נרדי, יונת קלר, ציון בודאי, חוה תדהר, יוכבד יהל, עודד אביגדורי ורנה שחר.\nאת הוועד המנהל הובילו אביב יהלום ועידית שחם.\nהתנועה פעלה על בסיס התנדבותי ולא היו לה עובדים בשכר.\nהעמותה נמצאת בהליכי פירוק וככל הנראה פעלה עד 2014.',
