    'description': 'הוועדה להגברת שילובן וקידומן של נשים בשירות הממשלתי, בראשותה של פרופ\' רות בן-ישראל, מונתה בשנת 1993 במשרד הכלכלה והתכנון בעקבות המלצות ועדת קוברסקי. בוועדה השתתפו נציגות של ארגוני נשים ועובדות מדינה בתפקידים בכירים. הועדה ניסחה המלצות אופרטיביות למיסוד מעמדה ותפקידיה של הממונה על מעמד האישה והמליצה להקים אגף מטה לנושא מעמד האישה בנציבות שירות המדינה. בעקבות המלצות הוועדה, הוקם ב-1995 אגף בנציבות שירות המדינה, שמטרתו לרכז את כל הפעולות הנוגעות לקידום ושילוב נשים בשירות המדינה (כיום - האגף לשוויון מגדרי).',
    'description_ar': 'مكانة المرأة الدرزية وتعزيز اندماجها في المجتمع. تشجيع ومساعدة العائلات الثكلى، الارامل والنساء الاتي يتعرضن للضرب. نشاط الجمعية يشمل تنظيم مؤتمرات، أيام دراسية، محاضرات وجولات في إسرائيل للنساء الدرزيات بهدف تعميق التراث الدرزي والدين. غالبا تهدف معظم جولات الجمعية إلى زيارة الأماكن المقدسة للطائفة الدرزية.',
