    'description': '״בית חולים הקריה״ היה שמו העממי של בית החולים לנשים (בית יולדות ומחלקות גינקולוגיות) אשר שכן בין השנים 1951–1997 בקריה בתל אביב. שמו הרשמי היה ״בית היולדות ע״ש יוסף סרלין״ והוא שימש במשך 46 שנים מקום משכנן של מחלקות הנשים והיולדות של העיר תל אביב עד שהועבר אל בית החולים ליס במתחם איכילוב. ועדת המשנה בנושא סגירת בית החולים ליולדות בתל אביב הוקמה במהלך הכנסת ה-9 ב-30/05/1978, ככל הנראה על מנת לבחון את המשך פעילות בית החולים. יו״ר הועדה היתה ח״כ שרה דורון. חברי הועדה היו: יעקב ז`אק אמיר (אמזלג), חייקה גרוסמן-אורקין, הלל זיידל, אורה נמיר ושרה שטרן-קטן.',
