    'description': 'ועדת המשנה לקידום מעמד האישה במגזר הערבי הוקמה בשנת 1992 בכנסת ה-13 תחת השם ״ועדת משנה לקידום מעמד האשה הערבייה״ כתת-ועדה של הועדה לקידום מעמד האשה. היו״ר הראשון של ועדת המשנה היה ח״כ עבד אלוהאב דראושה, אחריו כהנו בתפקיד ח״כ תמר גוזנסקי (כנסת ה-14) וח״כ חוסניה ג\'בארה וח״כ מוחמד כנעאן (כנסת ה-15). ועדת המשנה הפסיקה לפעול בשנת 2003. בין הנושאים שנידונו בוועדת המשנה היו: רצח נשים ערביות, נשים ערביות בשוק העבודה, פערי שכר ועידוד יזמות עסקית, ייצוג נשים ערביות ברשויות המקומיות, נשים חד הוריות במגזר הערבי ועוד.',
    'description_ar': 'اقيمت اللجنة الثانوية لتعزيز مكانة المرأة في المجتمع العربي عام 1992 في الكنيست ال 13 تحت اسم ״اللجنة الثانوية لتعزيز مكانة المرأة العربية״ كلجنة فرعية من لجنة تعزيز مكانة المرأة. رئيس اللجنة الأول كان عضو البرلمان عبد الوهاب دراوشة وبعدها في الكنيست ال 14 عضوة البرلمان تمار جوزنسكي وبعدها حسنية جبارة ومحمد كنعان في الكنيست ال 15. توقف نشاط اللجنة في عام 2003. ومن بين المواضيع التي طرحت كانت: قتل النساء العربيات، النساء العربيات في سوق العمل، الفجوات في الدخل، تشجيع ريادة الاعمال، تمثيل نسائي عربي في السلطات المحلية، العائلات أحادية الوالدية في الوسط العربي وغيره.',
