    'description': 'הוועדה לקידום נשים בשירות המדינה הוקמה בעקבות החלטת ממשלה מס\' 481 לאישור דוח הוועדה לשיפור ניהול ההון האנושי בשירות המדינה. לפי בקשת יו״ר ועדת השרים לקידום מעמד האישה, שרת התרבות והספורט, ח״כ לימור לבנת, בהחלטת הממשלה נקבע כי מעמדן של נשים בשירות המדינה, קידומן, תנאי עבודתן ושכרן ייבחנו בידי צוות ממשלתי, ומסקנותיו יובאו לפני הממשלה בתום עבודת הוועדה.  באוקטובר 2013, מונתה דלית שטאובר (שכיהנה אז כמנכ״לית משרד החינוך) לעמוד בראש הוועדה. התפקיד שהוגדר לוועדה: לבחון את מעמדן של הנשים בשירות המדינה, קידומן, תנאי עבודתן ושכרן, ולהביא את המלצותיה בפני הממשלה. המלצות הוועדה הוגשו ביוני 2014.',
