    'created': {
      'by': 'שרי אהרוני',
      'on': '01/07/2020'
    },
    'description': 'במסגרת החלטת ממשלה כוללת לפיתוח כלכלי חברתי של האוכלוסייה הבדואית בנגב, התקבלה החלטה 2345 מיום 17.1.29 שקבעה הקמת ״ועדת שרים לעניין התמודדות עם תופעת הפוליגמיה״. בהחלטת הממשלה נאמר כי הועדה הוקמה ״נוכח הצורך בהתמודדות מקיפה עם תופעת הפוליגמיה, ששורשיה עמוקים ונטועים במסורת רבת שנים ולאור השלכותיה הקשות על בני המשפחה הפוליגמית, ובראש ובראשונה על הנשים והילדים, ועל החברה בכללותה, ובניסיון ממשלתי ראשון להתמודדות כלל ממשלתית עם התופעה האמורה.״ סמכויות הועדה היו: א. לאשר תכנית הפעולה שיגבש צוות בין-משרדי שיוקם מתוקף החלטת הממשלה; ב. לעקוב ולפקח אחר יישום תכנית הפעולה שיגבש הצוות הבין-משרדי. יו״ר הועדה היתה שרת המשפטים איילת שקד ובין חבריה נכללו: שר החינוך, שר העבודה, הרווחה והשירותים החברתיים, שר הפנים, שר הבריאות, שר החקלאות ופיתוח הכפר, השר לביטחון הפנים, ושר הכלכלה והתעשייה (שצורף באוגוסט 2018). הצוות הבין-משרדי בראשות אמי פלמור, מנכ״לית משרד המשפטים, כלל נציגים ממשרדי הממשלה השונים, הרשויות המקומיות הרלוונטיות והחברה האזרחית הגיש דו״ח מסכם ביולי 2018. הדוח התבסס על פעילות מגוונת של הצוות אשר כללה סיורים בנגב ופגישות עם מגוון רחב של אנשים, מנהיגים מקומיים, אנשי דת ושייח\'ים, אנשי מקצוע, אנשי חינוך וטיפול, פעילות ופעילים חברתיים ועוד, בעלי זיקה לסוגיה. המסקנות העיקריות של הדו״ח העלו כי קיים חוסר במידע אחיד ומגובה בדבר היקף התופעה וכי ממשלות ישראל לדורותיהן נמנעו מפעילות אכיפה במכלול ההיבטים של פוליגמיה, במישור הפלילי ובמישור האזרחי והמנהלי כאחד. בנוסף, הדו״ח קבע כי הסתגרות והתבדלות החברה הבדואית בנגב משפיעה על הנישואין בתוך החברה הבדואית ועל תופעת הפוליגמיה. המלצות הדו״ח עסקו במספר סוגיות: (1) חשיבות מערכת החינוך ככלי לשינוי תודעה בקרב האוכלוסייה הבדואית, צמצום הנשירה, העלאת גיל הנישואין ועוד. (2) השפעת דפוסי התעסוקה של גברים ונשים על הפוליגמיה. (3) הצורך במתן מענה לצרכים הפיזיים והרווחתיים של נשים וילדים שהינם הקורבנות העיקריים של התופעה. (4) הטיפול בתופעה מצריך הגברת המשילות, טיפול רוחבי והתווית מדיניות אחידה של כלל משרדי הממשלה. הצוות אף המליץ לקיים מנגנון קבוע של תאום פעולות ומעקב שיכלול את כלל הגורמים הממשלתיים הפועלים בנושא.',
