    'description': '״חזון נשות עכו״ הוקמה בשנת 2004 על ידי קבוצת נשים ערביות ויהודיות מעכו בהובלת ד״ר ג\'אנן פראג\'-פלאח, מרצה בכירה לספרות עברית ומגדר במכללה הערבית לחינוך בחיפה. הקבוצה נרשמה בשנת 2008 כעמותה (ע״ר 580490670) שמטרותיה הן: ״ קידום מעמד האשה, חיזוק הקשר הבין עדתי למען שלום אמת. טיפוח וחיזוק הדו-קיום בין הערבים ליהודים בעיר עכו.״ פעילות העמותה משתרעת על פני העיר כולה, העתיקה והחדשה כאחד ומכוונת לצעירות ולמבוגרות, למשכילות וללא משכילות. העמותה חנכה ב-2014 את ״בית אישה לשלום״ בעיר - מרכז תרבות, מחקר וגלריה ומקיימת בה מפגשים עם נשים פלסטיניות ונשים מרחבי העולם לקידום נושאי השלום ומעמד האישה.',
    'description_ar': 'أقيمت ״رؤيا نساء عكا״ في عام 2000 بواسطة مجموعة نساء عربيات ويهوديات من عكا بقيادة د. جنان فرج- فلاح، محاضرة في الأدب العبري والجندر في الكلية العربية للتربية في حيفا. المجموعة سجلت عام 2008 كجمعية (ج.م 580490670). أهدافها هن:  ״ تطوير مكانة المرأة، تقوية العلاقة بين الطوائف من اجل سلام حقيقي. التطوير وتعزيز التعايش بين العرب واليهود في مدينة عكا״.    تنتشر اعمال هذه الجمعية في جميع انحاء المدينة، الجديدة والقديمة في آن واحد. وتتوجه للشابات والكبيرات، للمتعلمات والغير متعلمات. في عام 2014، افتتحت الجمعية ״بيت امرأة السلام״ في المدينة - وهو مركز ثقافي، أبحاث ومعرض. وعقدت اجتماعات مع النساء والفتيات الفلسطينيات من جميع أنحاء العالم لتعزيز قضايا السلام ومكانة المرأة.',
