    'created': {
      'by': 'שרי אהרוני',
      'on': '03/11/2020'
    },
    'description': 'התחנות לבריאות המשפחה (התחנות לאם ולילד) המוכרות בשם ״טיפת חלב״ התפתחו בירושלים כשיתוף פעולה בין ״הסתדרות נשים עבריות״ לבין נשים שהגיעו לארץ ישראל כחלק ממשלחת רפואית של ״הדסה״ לאחר מלחמת העולם הראשונה. השם ״טיפת חלב״ מבוסס על תרגום מצרפתית- La Goutte de Lait כשמו של ארגון שהוקם בפריס במטרה להעביר חלב סטרילי לאמהות. התחנה הראשונה הוקמה בעיר העתיקה בשנת 1921 על ידי ברטה לנדסמן ולאור הצלחתה, הוחלט להרחיב את המודל למקומות נוספים ביחד עם קופות החולים. לאחר קום המדינה, העבירה הדסה את התחנות שבניהולה להפעלת משרד הבריאות. בשנת 1957 הפעילה קופת החולים הכללית 198 תחנות לאם ולילד ו-323 מרפאות כפר שגם בהן ניתנו שירותי ״טיפת חלב״ בפיקוח משרד הבריאות. ברבות השנים, השירות גדל והתפתח לכדי שירות קהילתי אוניברסלי המתמקד בנשים הרות, אמהות טריות ופעוטות שנועד לצמצם פערים בתחום הבריאות באמצעות פעולות מניעה ויצירת סביבה בריאה ותומכת עבור המשפחה. צוות הבריאות בתחנות כולל אחיות ורופאים, בעלי הכשרה מיוחדת בבריאות הציבור. בתחנות ״טיפת חלב״ ניתנים חיסונים כנגד מחלות בקנה מידה רחב (לכ-96% מכלל האוכלוסייה) בדומה למדינות מפותחות ומעקבי התפתחות שגרתיים לתינוקות ואמהות. השירות נחשב לאחד הטובים בעולם וזכה לפרסים מטעם ארגון הבריאות העולמי. בשנים שלאחר חקיקת ״חוק ביטוח בריאות ממלכתי״ (1994) היו אמורות התחנות לעבור להפעלה על ידי קופות החולים, מהלך אשר נתקל בהתנגדויות רבות עקב חשש מהפרטת השירות וירידה באיכותו. משנת 2007 הנהיג משרד הבריאות מדיניות של צמצום מספר התחנות לבריאות המשפחה תוך ריכוז השירות לתחנות גדולות וסגירת תחנות קטנות. בשנת 2018, ניתנים שירותי טיפת חלב בכ-1030 תחנות באופן משולב על ידי משרד הבריאות, קופות החולים ורשויות מקומיות.',
