    'description': '״יד לאדם״ הוקמה בשנת 1989 על ידי ד״ר עדינה תמיר ונשרשמה כעמותה (ע״ר 580174605) ב-1990. העמותה הוקמה במטרה לסייע ולתמוך בילדי ובנשות אסירים. העמותה הפעילה מועדונים בירושלים, תל אביב, חיפה ובאר שבע בהם התקיימו פעילויות חינוכיות, שיעורי העשרה, פעילויות חברתיות וכן הפעילה משחקיות טיפוליות בבתי הכלא. בנוסף, היתה העמותה מעורבת בקידום חקיקה לשינוי מעמד נשות אסירים כך שיוכרו כחד הוריות לצורך קבלת סיוע והטבות מהמוסד לביטוח לאומי. בקשה לפירוק העמותה הוגשה ב-2011.',
