    'description': 'לאחר בג״ץ אליס מילר (1995) ובעקבות תיקון חוק שירות הביטחון בשנת 2000 והוספת סעיף 16א\' הקובע כי ״דין אישה כדין גבר״ התאפשר לחיילות לשרת שירות צבאי במגוון תפקידים. על מנת ליישם את הוראות החוק, בשנת 2001 נסגר חיל הנשים (ח״ן) ובמקומו הוקמה יחידה בשם ״יועצת הרמטכ״ל לענייני נשים״. בשנת 2016 שונה שם היחידה ל״יועצת הרמטכ״ל לענייני המגדר״, מהות השינוי היא מעבר לגוף שתכליתו קידום שוויון הזדמנויות מגדרי מהותי. מטה היוהל״ם פועל לצמצום אי השוויון המגדרי בצה״ל, לשילוב מתמשך של חיילות בתפקידים מגוונים, לניטור ומעקב אחר אלימות מינית בצה״ל ולמחקר בתחומים אלו. בשנת 2012 הוקם בתוך היוהל״ן מרכז מהו״ת- מרכז התמודדות ותמיכה שייעודו להעניק טיפול רגשי, סיוע רפואי וליווי משפטי לחיילות ולחיילים בתחומי פגיעה מינית, הריון לא מתוכנן ואלימות במשפחה.',
