    'description': 'דו״ח מבקר המדינה לשנת 2012 הצביע על ייצוג נמוך לנשים בתפקידים בכירים במשטרה ועל חוסר ייצוג בדרגים מסוימים. בעקבות פרסום הדו״ח הקים המפכ״ל דאז, יוחנן דנינו ועדה שהמליצה על מיניוי יועצת לענייני נשים בכפיפות ישירה למנכ״ל וכחברת סג הפיקוד הבכיר בממשלה. ביולי 2013 מונתה לראשונה היועצת - תנ״צ יעל אידלמן. תפקיד היועצת הוגדר כ״יצירת תנאים למיצוי היכולות ושוויון הזדמנויות של נשים המשרתות במשטרה״ וכן - עיצוב סביבה ארגונית חדשה בארגון - בטוחה והוגנת לכל. ליוהל״ן כפופות רמ״ד קידום שוויון הזדמנויות ורמ״ד למניעת הטרדות מיניות שתחתיה 256 ממונות להטרדה מינית הפועלות בכל יחידות המשטרה. נכון ל-2019 ממלאה את התפקיד תנ״צ יפעת שקלר.',
