    'description': 'לכוח לעובדים חמישה מטות: צפון, גליל, מרכז, ירושלים ודרום. הפורום נוסד בינואר 2013 על ידי שתי פעילות מרכזיות בארגון, שירה פנחס ונעה נוצני. מטרתו לחזק את השוויון המגדרי בכוח לעובדים ולהיאבק נגד האפליה של נשים בעולם העבודה. הפורום הצליח לעשות שינוי משמעותי בתוך הארגון וממשיך לפעול בנושא, ושואף להרחיב את השפעתו במקומות עבודה אותם הארגון מאגד ומעבר להם. בין ההישגים של הפורום: יצירת איזון מגדרי במערך השכיר, גידול משמעותי באחוז הנשים החברות בהנהגת הארגון, השוואת מספר המרצות למרצים בהכשרות בארגון, וכן שוויון בין מרואיינות למרואיינים בתקשורת וסיוע בהטמעה של מודל להסכם קיבוצי רגיש מגדרית שנכתב בשיתוף עם ״הקליניקה לזכויות נשים בעבודה״ באוניברסיטה העברית.',
