    'description': 'לב לעם הארגון למען העגונות ומסורבות/י הגט וילדיהם נוסד כעמותה (ע״ר 580100527) על ידי דניאלה ספיר ומסורבות גט נוספות בשנת 1983 תחת השם ״הארגון לעזרת העגונות ומעוכבי הגט״. בשנת 1996 השם הוחלף לשם הנוכחי. בעלות התפקידים יצגו את העמותה בוועדת המשנה לדיני אישות של הוועדה לקידום מעמד האשה בשנת 1998 ובוועדה לזכויות הילד בשנת 2007. פעילות העמותה תוארה באותה ועדה ב-2007: ״במסגרת הפעילות ‏שלנו אנחנו מקיימים סדנאות, סופי שבוע לאמהות וילדים. אנחנו שומעים את כל הצרות, כל הבעיות וכל מה שקורה. אנחנו ‏דואגים לכך שיהיו סדנאות לאימהות, ושבזמן הזה מישהו ישמור על הילדים וישמע אותם. הם מסיימים את הסדנה בהרגשה ‏נפלאה, שמישהו שמע אותם, טיפל בהם, הקשיב. האימהות יוצאות ממש מאושרות.״',
