    'created': {
      'by': 'יעל בר צדק',
      'on': '19/05/2018'
    },
    'description': 'עמוד הפייסבוק ״לילה טוב״ נוצר באוגוסט 2015 בעקבות פוסט פרטי שבו תוארו הטרדות מיניות במועדון לילה בתל-אביב שהוביל להתארגנות של מספר פעילות פמיניסטיות תחת השם ״דונט קיל מיי וייב״. בין המקימות היו יעל אלבז, שירה מיקין, הגר שיזף, יסמין וקס, קרן גרינבלט ומפרסמת הפוסט גילי רון. הקבוצה, בשיתוף פעולה עם ״שדולת הנשים״ ו״מרכז הסיוע לנפגעות תקיפה מינית תל אביב״ החלו לפעול ביחד עם בעלי ברים ומועדונים ובלייניות ובליינים במטרה להיאבק בהטרדות ובתקיפות מיניות בחיי הלילה וליצור מרחבים בטוחים יותר. העמותה ״לילה טוב- לבלות בלי הטרדות״ (ע״ר 580635589) הוקמה בשנת 2017. מטרות העמותה הן: ״ לפעול להטמעת חקיקה ראשית ו/או בתקנות עזר ברשויות מקומיות המחייבת בתי עסק המוגדרים כמקומות בילוי של חיי הלילה (בר/פאב/מועדון/דיסקוטק) לטפל בהטרדה ופגיעה מינית בשטח בית העסק ומחוצה לו במהירות, ביעילות ובכבוד, לקיים הכשרות עתיות לכל אנשי הצוות בבתי עסק אלו בנושא הטרדה ופגיעה מינית, ולתלות שילוט הולם במקומות הבילוי המכריז על המרחב כמוגן מהטרדה מינית ומפנה לצוות המקום המוכשר לסייע במידת הצורך. לפעול לשינוי תרבות הבילוי בחיי הלילה בישראל ולמגר מתוכה את הפגיעה וההטרדה המינית. לפעול להעלאת מודעות הציבור בישראל לתופעת הפגיעה וההטרדה המינית בחיי הלילה ולדרכים למניעתה ומיגורה. לפעול לחינוך ציבור המבלים והמבלות, עובדי ועובדות, מנהלי ומנהלות בתי העסק בחיי הלילה למניעה ומיגור של הפגיעה וההטרדה המינית, וליצירת מרחב בילוי נעים ובטוח לכל.״ פעילות העמותה כללה ניסוח אמנה וולנטרית שעליה חתמו בעלי עסקים. האמנה מחייבת כל מקום בילוי שבעליו חתמו על האמנה להעביר הכשרות ייעודיות לזיהוי וטיפול במקרים של הטרדה או פגיעה מינית, לתלות שילוט במקום ולמנות אדם אחראי לטיפול בנושא.',
