    'description': 'עמותת ״לצידך- סיוע לנשים נפגעות התמכרויות״ (ע״ר 580564680) הוקמה בשנת 2012 ביהוד-מונסון ומספקת מענה לנשים מכל הארץ. מטרות העמותה הן: ״הורדת כמות הנשים המכורות בישראל. טיפול ושיקום באשפוז בנשים מכורות. טיפול ושיקום אמבולטורי בנשים מכורות. העלאת המודעות בקרב הציבור לבעיית ההתמכרות אצל נשים בישראל. מענה מקצועי למשפחות של נשים מכורות. העלאת המודעות בקרב הציבור בקשר בין אלימות להתמכרות״. אוכלוסיית היעד של העמותה הינה נשים נפגעות התמכרויות, מגיל 18 ומעלה. העמותה מפעילה אשפוזית לגמילה מסמים לנשים בלבד שהוקמה בשיתוף פעולה עם עמותת ״מעגל נשים 911״. העמותה פועלת במספר תחומים: מתן מידע וסיוע מקצועי בסוגיות כמו גמילה, טיפול נפשי, עזרה במיצוי זכויות; יצירת מקום בטוח, מכבד ותומך לנשים נפגעות התמכרות; פעולה אל מול הרשויות לאימוץ מדיניות פרוגרסיבית בכל הנוגע לטיפול בנשים המכורות; העלאת מודעות בקרב הציבור לקשר ההדוק בין אלימות ואלימות מינית בפרט להתפתחות התמכרויות בקרב נשים.',
