    'description': '‏״לשמה - מדיניות, יזמות, קהילה״ הוקמה כפרוייקט שטח ״מעורבות״ של התנועה ״לא נבחרות, לא ‏בוחרות״ (לימים עמותת ״נבחרות״) בשנת 2013. בשנת 2016 ״מעורבות״ נרשמה כעמותת ״חרדיות ‏מעורבות״ (ע״ר 580623908) וכנראה סביב זמן זה התפצלה מ-״נבחרות״ שנרשמה כעמותה בשנת 2015. בשנת 2018 ‏החליפה עמותת ״חרדיות מעורבות״ את שמה ל-״לשמה - מדיניות, יזמות, קהילה״. מטרות ״לשמה״ הן ‏להצמיח מנהיגות נשית מתוך הקהילות החרדיות, קידום המעורבות והעשיה שלהן בתוך הקהילות ‏ובציבור הישראלי בכלל, ולקדם מדיניות וחקיקה עבור נשים חרדיות בתחומי ההשכלה, התעסוקה ‏והבריאות.‏',
