    'description': 'מונתדא אלמועילאת אלוחידאת הוקמה על ידי מייסון ריא מסח\'נין בשנת 2010 במטרה לסייע לנשים גרושות בסח\'נין. מטרות העמותה הן: ״העצמת נשים חד הוריות ושילובן בקהילה ע״י הרצאות וחשיפתן לנושאים חברתיים וחוקיים. ארגון סדנאות קבוצתיות לעודד אותן לצאת מהבית. שילוב הנשים בתעסוקה ע״י הכשרתן והקמת מוקד לחיפוש תעסוקה, תמיכה רגשית לילדי החד הוריות ושילובם בחוגים והפעלות״. הקבוצה מקיימת קבוצות וסדנאות לכ-200 נשים וילדים מדי שנה.',
    'description_ar': 'تأسست الجمعية ميسون أبو ريا من سخنين عام 2010، بهدف بهدف مساعدة الأمهات العازبات في سخنين. أهداف الجمعية هي: تمكين الامهات العازبات وإدماجهن في المجتمع من خلال المحاضرات وتعريضهن للقضايا الاجتماعية والقانونية، تنظيم ورش عمل جماعية لتشجيعهن على مغادرة المنزل، وإدماج النساء في العمل من خلال تدريبهن. وبناء مراكز للبحث عن العمل، دعم عاطفي للأطفال ودمجهن في دورات ونشاطات. تقوم المجموعات بنشطات وورشان ل 200 امرأة وطفل كل سنة.',
