    'description': 'מבוי סתום הוקמה כעמותה (ע״ר 580268183) בשנת 1995 שמטרתה ״מתן עזרה לעגונות שנמצאות במצבים קשים. סיוע כספי, משפטי וחברתי״. העמותה קמה על בסיס פעילות אישית של לאה עין גלוב בכדי להעניק סיוע משפטי וסוציאלי לנשים מסורבות גט ועגונות. באתר הארגון נכתב כי ״הכנס הראשון התקיים בביתה בשכונת רחביה בירושלים והמשרד הראשון של הארגון התנהל מתוך מטבח ביתה. עם השנים הצטרפו לארגון נשות מקצוע ומתנדבות שליוו נשים מסורבות גט לבתי הדין ובתי המשפט וצעדו עמן לאורך כל הליך הגירושין עד להשגת הגט.״ בין השנים 2009-2018 ניהלה את הארגון עו״ד בתיה כהנא-דרור. כיום ארגון מבוי סתום הוא אחד הארגונים המרכזיים הפועלים למען נשים עגונות ומסורבות גט וגישתו משלבת טיפול אישי ופרטני יחד עם מהלכים ציבוריים לשינוי חברתי רחב. ברמה האישית הארגון מציע מגוון שירותים: ייעוץ וייצוג משפטי, תמיכה נפשית, ליווי אישי ותכניות העצמה ותרפיה. ברמה הציבורית, הארגון פועל לפתרון ולמניעת בעיית סרבנות הגט, באמצעות קידום פתרונות הלכתיים ושינויי חוקי הנישואין והגירושין בישראל, יצירת לובי אזרחי, פיתוח רשת של קשרים אסטרטגיים, העלאת קמפיינים למודעות ציבורית, חתירה לשינויי דפוסי עבודה בתוך בתי הדין הרבניים, עידוד הקמת בתי דין פרטיים ופעילות למען מינוי דיינים ראויים. \'מבוי סתום\' שואף לחברה ישראלית הוגנת יותר, שבה נשים הרוצות להתגרש לא תיתקלנה עוד במבוי סתום ובאטימות לב.',
