    'description': '״מגדר״ (ISSN: 2617-4359) הוא כתב עת דיגיטלי רב תחומי ורב תרבותי שהחל לראות אור בשנת 2012. מטרתו של כתב העת לשמש במה להפצה ולהעשרה של השיח המגדרי והפמיניסטי בשפה העברית ולהעלות את המודעות לנושאים אלה. כתב העת הוא אינטרנטי מקוון, וזכויות היוצרים של המאמרים המתפרסמים בו שייכים לכותבות והכותבים. המאמרים מופיעים כקבצי PDF הניתנים להורדה. ״מגדר״ הוא יוזמה עצמאית של ד״ר יוכי שלח, מרצה בחוג לתקשורת וקולנוע במכללת סמינר הקיבוצים ושל ד״ר אורלי צרפתי, מהחוג הרב-תחומי במכללת עמק יזרעאל, ואיננו שייך לעמותה או ארגון כלשהו. קהיליית המגדר של האגודה הסוציולוגית הישראלית ושל האגודה הישראלית ללימודים פמיניסטים מעניקות לכתב העת חסות אקדמית.',
