    'description': '״מגעים״ היה העיתון ההומו-לסבי המסחרי הראשון שפעל בישראל. הוא התפרסם במתכונת של ירחון, בין השנים 1987-1995. העיתון היה בבעלותו של מרק אריאל, אשר היה גם העורך. ״מגעים״ הופץ בחנויות ובקיוסקים בתל אביב ונשלח בדואר למנויים ברחבי הארץ. העיתון כלל כתבות תוכן, סיקור של תרבות וחיי לילה הנוגעים לקהילה, דיווחים על פעילויות האגודה לשמירה על זכויות הפרט, צילומי עירום גברי אומנותי מהארץ והעולם והצהרות תמיכה בביטול החוק לאיסור משכב זכר. בשלוש השנים הראשונות לקיומו הוא הודפס בצבע, אולם החל משנת 1990 הוא הודפס בשחור-לבן וגם כמות הדפים הצטמצמה מטעמים כלכליים. ב - 1995 הפסיק הירחון להתפרסם בשל חובות של בעליו.',
