    'description': '״מדרשת נעמי״ היא דירה טיפולית בירושלים שיכולה לקלוט עד 15 צעירות. השירות החל לפעול בערך בשנת 2012 ומיועד לנערות אנגלו-סקסיות מרקע חרדי בגיל 18-23 הזקוקות לשיקום חוץ-ביתי בקהילה. מאז שנת 2014 השירות ממומן על ידי השתתפות עצמית ותמיכות ממשרד הרווחה ו״ידידות טורנטו״ ומופעל על ידי עמותת ״בתי דולב לנוער״.',
