    'description': 'מועצת הנשים בבת ים הוקמה בשנת 2002 על ידי ויקי יוחנן כפרוייקט סיום קורס מנהיגות נשים תל-אביב יפו ונרשמה כעמותה (ע״ר 580457117) ב-2006. המועצה פועלת לשיפור מעמדן וקידומן של נשים ונערות ומעודדת, להעצמתן וליצירת מפגשים ורשתות חברתיות ביניהן. בין היתר מקדמת המועצה קורסי העצמה, יזמות עסקית, שיתופי פעולה עם פרלמנט הנשים ופרלמנט הנערות שנוסד בעיר ועם המועצה הלאומית לקידום ספורט נשים ״אתנה״, הקמת טורניר ״כדורשת״ נשים, היכרויות עם מעגלי נשים בישראל ובהן נשים דרוזיות וערביות, קידום נשים בספורט ועוד. בראש המועצה מכהנת כיום (2019) ענת לבנת שהיא גם היועצת לקידום מעמד האישה בעירייה ויו״ר איגוד היועצות למעמד האישה ברשויות המקומיות.',
