    'description': 'מועצת הנשים במועצה האזורית חוף הכרמל הוקמה בשנת 2004 במטרה לעודד ולכוון לשיוויון בין המינים, מתן הזדמנות לביטוי ופעילות שווה בכל העשייה. מועצת הנשים מורכבת מנציגות מישובים שונים הפועלות בהתנדבות. בראש המועצה עומדת היועצת למעמד האישה ירדן קינן. \nמטרות מועצת הנשים: פריצת תקרת הזכוכית, שילוב נשים בוועדים ובצמתים של קבלת החלטות הנוגעות לכל התושבים במועצה, הגדלת מספר נבחרות הציבור במליאת המועצה, הגברת המודעות לחשיבות שילוב נשים במוקדי השפעה בקהילה, העלאת מעורבות הנשים בחיי הקהילה, הרחבת מאגר הידע על נשים והובלת מיזמים לקידום נשים באזור. \nהמועצה עסקה בין היתר בהעלאת מודעות נשים לסוגיות של בריאות, לקידום נשים בעסקים ותמיכה באמהות ותינוקות (פרויקט אם לאם). המועצה פועלת באמצעות אירועים, ימי עיון הרצאות וקורסים.',
