    'description': 'מועצת הנשים במועצה האיזורית לכיש הוקמה בשנת 2016. כיו״ר המועצה מכהנת היועצת לקידום מעמד האישה סופיה וקנין. חזון מועצת הנשים כפי שהוא מופיע באתר של המועצה האיזורית לכיש: \n״שוויון הזדמנויות בין המינים בכל התחומים וצמצום פערים. מועצת הנשים תפעל במטרה להעלות את המודעות לנושא ,תפעיל תוכנית , ימי עיון , קורסים ואירועים לקידום מעמד האישה ותקיים מארחים בכל היישובים . המועצה תפעל למען ייצוג הולם של נשים בעמדות מפתח המשפיעים על איכות חיינו . המועצה תעודד העצמה נשית דרך שיח וסדנאות . המועצה תטפח מנהיגות נשים .\nהמועצה תעניק אוזן קשבת לצורכי הנשים בכל יישובי המועצה״.',
