    'description': 'מועצת הנשים בטמרה החלה לפעול בשנת 2000 לאחר שחוקקה הכנסת את חוק הרשויות המקומיות (חובת מינוי יועצת למעמד האשה-2000). בשנת 2004, הוקמה עמותה בשם ״מועצת נשים טמרה״ (ע״ר 580426146) על ידי סלוא כנעאן, יועצת ראש העיר למעמד האשה. מטרות העמותה הן: ״העלאת המודעות לזכויות הנשים והשתתפות בקבלת החלטות. קידום מעמד האישה בטמרה בכל התחומים.״ העמותה ארגנה פעילויות פנאי וטיולים להכרת הארץ וגם חילקה מזון למשפחות נזקקות. מאז שנת 2013 העמותה איננה פעילה אך מועצת הנשים ממשיכה להתקיים ומציעה מגוון שירותים לנשים הכולל סדנאות, חוגים, הרצאות וכנסים בנושאים של תעסוקה ויזמות עסקית.',
    'description_ar': 'انطلق نشاط مجلس نساء طمرة في عام 2000 بالتزامن مع سن قانون المجالس المحلية (الالتزام بتعيين مستشارة لتعزيز مكانة المرأة-2000). في عام 2004 تأسست جمعية ״مجلس نساء طمرة״ (ج.م 580426146) بمبادرة من سلوى كنعان، مستشارة البلدية لمكانة المرأة. أهداف الجمعية هن: ״رفع الوعي حول حقوق النساء ومشاركتهن في اتخاد القرارات. تعزيز مكانة المرأة في طمرة على جميع الأصعدة.״ نظمت الجمعية نشاطات ترفيهية مختلفة وجولات للتعرف على البلاد بالإضافة لتوزيع طرود غذائية للعائلات المحتاجة. منذ عام 2013 توقف نشاط الجمعية الا ان مجلس المرأة في طمرة استمر في عمله ويعرض خدمات مختلفة ومتنوعة للنساء تشمل ورشات، دورات محاضرات ومؤتمرات في مواضيع التوظيف والمبادرات التجارية.',
