    'description': 'מועצת נשים יישובית ברמת השרון נוסדה בשנת 2000 ביוזמתה של ד״ר ברכה תקוה במטרה להעצים נשים ולסייע להן בבניית עסקים ובייעוץ משפטי. במועצת הנשים משתתפות למעלה מ-80 תושבות העיר. המועצה פועלת בין היתר באמצעות ארגון אירועים, קידום נשים בעסקים בין היתר באמצעות קורסים לדירקטוריות ומיזם ליצירת רשת לנשים עצמאיות וכן באמצעות שיתופי פעולה עם יחידות אחרות במועצה למשל: פרויקט ״החודש העשירי״ - ליווי נשים צעירות לאחר לידה, בשיתוף המרכז להתנדבות ושיתופי פעולה עם היחידה לטיפול בנערות וצעירות. פרויקט הדגל של המועצה נקרא ״אישה למען אישה״ והוא מתקיים בשיתוף אגף הרווחה בעירייה. מטרתו של הפרויקט: להעניק עזרה מוראלית ופיזית לנשים שחוו אלימות במשפחה. במסגרת הפרויקט, מתנדבות ממועצת הנשים עוברות הכשרה ע״י עובדות סוציאליות, על מנת שתוכלנה לתת תמיכה וסיוע.',
