    'description': 'מטה הנשים של מפלגת יש עתיד החל להתארגן בשנת 2012 לקראת הבחירות לכנסת ה-19. מאז 2014 מפעיל המטה רשת של מועצות נשים מקומיות אשר מובילות תכניות ״להעצמת נשים בקהילה המקומית, ובכלל זה אירוח ח״כים ביישוב לשם שמירת הקשר ויכולת ההשפעה במוקדי הכוח, קיום כנסים ועריכת דיונים עם מובילי דעת קהל, על מנת לקדם נושאים החשובים בעיקר לנשים.״ המועצות הנשיות של ״יש עתיד״ מונות כמה מאות חברות אשר מקיימות אירועים ארציים הנוגעים למעמד האשה: שוויון הזדמנויות, מנהיגות בתחום המדיני-בטחוני, שוויון בתעסוקה, פעילות נגד הדרת נשים, סיוע לנשים נפגעות אלימות. יו״ר מטה הנשים הן איילת השחר-סגל, אילאיל קרן.',
