    'description': 'כתב העת ״מסכת״ הוקם ב-2002 על ידי פרופ\' ישראל אומן, לימים חתן פרס נובל לכלכלה, לזכרה של רעייתו אסתר. כרך א\' של ״מסכת״ ראה אור בתשס״ב - 2002 בהוצאת מת״ן - מכון תורני לנשים והעורכת הראשונה הייתה זיוה קוסופסקי. החל מינואר 2017 יוצא כתב העת לאור במסגרת הפקולטה למדעי היהדות באוניברסיטת בר-אילן. במסכת מתפרסמים מחקרים בהיבטים שונים של חיי האישה היהודייה, מעמדה בחברה היהודית והכללית, תפיסתה העצמית והישגיה בתחומי יצירה מגוונים מאז תקופת התנ״ך.',
