    'description': 'מפלגת הנשים- למען כבוד האשה וחרותה (1992) שסימנה נ״כ הוקמה בשנת בהרצליה והתמודדה בבחירות לכנסת ה-13 כרשימה עצמאית מטעם תנועת ש.י.ן (שוויון ייצוג לנשים). רשימת המועמדות היו: רות רזניק (פעילה פמיניסטית בולטת במאבק באלימות נגד נשים), אסתר הרצוג, בלהה מרזיוף-קליין, נילי גוטין, נגיסט מנגשה, פנינה גבע ועאידה אבו-חבלה. המפלגה השתתפה במסע הבחירות והפיקה משדרי תעמולת בחירות שעסקו בנושאים הנוגעים לשוויון זכויות לנשים. המפלגה לא נבחרה לכנסת לאחר שזכתה במספר קטן מדי של קולות (כ-3,000).',
