    'description': '״מקלט אור הכרמל בעספיא״ הוקם ב-2002 על ידי ריטה צוקהרייה מקהילת היהודים המשיחיים והוא מופעל על ידי עמותת ״כנפי שחר״ (ע״R 580234086) שהוקמה אף היא על ידי הקהילה ב-1993. העמותה מוגדרת כארגון דת אשר עוסק בחינוך, צדקה ותפילה בציבור. בתחילת דרכו, קלט המקלט קלט נשים שהיו קורבנות סחר למטרות זנות ובהמשך הרחיב את פעילותו לסיוע לנשים חסרות מעמד, פליטות ומבקשות מקלט. המימון העיקרי לפעילות המקלט מגיע מתרומות והוא אינו נתמך על ידי גורמים ממשלתיים, על אף שגורמי רווחה וגם עמותות מפנים אליו נשים. מלבד קורת גג, המקלט מספק חינוך לילדים ושירותי בריאות בשיתוף פעולה עם עמותות אחרות ורופאים בהתנדבות.',
