    'description': 'מרכז אדוה (ע״ר 580157402) הוקם ב-1991 על ידי שלמה סבירסקי, ברברה סבירסקי, יוסי דהאן וויקי שירן. מרכז אדוה הוא מכון מחקר המתמחה בניטור של מגמות חברתיות וכלכליות ובניתוח המדיניות הממשלתית אל מול מגמות אלה, לפי אמות המידה של שוויון וצדק חברתי. המרכז עוסק בין היתר בסוגיות מגדריות ובניתוח מגדרי של המדיניות הכלכלית והחברתית ומוביל בישראל את רעיון הטמעת החשיבה המגדרית במדיניות ובתקציבי הממשלה והרשויות המקומיות. המרכז שותף בפרויקטים וקואליציות בנושא שוויון מגדרי ובהן: ״נשים לתקציב הוגן״, ״שוות ערך לקידום שכר שווה״, ״שותפות״ ו״שוויון מגדרי ברשויות מקומיות״. מרכז אדוה מציע הרצאות, ימי עיון וקורסים בנושאים הנגזרים מתוך תחומי המחקר, ובהם שוויון מגדרי.',
