    'description': 'המרכז האזורי הרב-תחומי לטיפול בנפגעות תקיפה מינית בראשל״צ הוא אחד מהמרכזים הרב-תחומיים הציבוריים המתמחים בטיפול בטראומה מינית. ב-1999 שירותי הרווחה בראשל״צ פנו למשרד הרווחה בבקשה להקים מרכז טיפולי לנפגעות תקיפה מינית. ב-2001 משרד הרווחה באמצעות השירות לנערות וצעירות בעיר הקימו את השירות. במרכז הוקם מוסד טיפול רב תחומי ששודרג והורחב ב-2008 בעקבות התכנית הבין-משרדית לטיפול בנפגעות תקיפה מינית. המרכז מציע מלבד טיפול יום גם דירת מעבר ופיתח מודל תעסוקה באמצעות הקרן למפעלים מיוחדים בביטוח הלאומי.',
