    'created': {
      'by': 'שרי אהרוני',
      'on': '18/11/2019'
    },
    'description': 'עמותת ״אלי״ס – אימהות למען ילדים בסיכון״ (ע״ר 580477974) הוקמה בשנת 2007 בבני ברק על ידי קבוצת אמהות, יוצאות חבר העמים שילדיהן מכורים לסמים. מטרת העמותה היא לפעול למען: ״חינוך והעצמה לאמהות של ילדים בסיכון. מניעה וטיפול בתופעת התמכרות בסמים ואלכוהול. פעילות לשינוי מדיניות של הממשלה ביחס לטיפול בקבוצות סיכון. הרחבת רשתות טיפוליות שיקומיות ותומכות באוכלוסיית סיכון. הפעלת חוג לתיאטרון ויצירת הופעות והצגות. פעילות הוצאה לאור לשם מניעת שימוש בסמים ואלכוהול. פעילות בקרב עולים חדשים לשם השתלבות בחברה הישראלית וכן פעילות למניעת שימוש בסמים ואלכוהול. הפעלת פרויקטים אמנותיים לשם מניעת שימוש בסמים ואלכוהול״. העמותה פועלת בעיקר באזור תל אביב על מנת לסייע להורים עולים שילדיהם מכורים לסמים על ידי מתן מידע נגיש בשפה הרוסית, תמיכה, סיוע וייעוץ, בין היתר באמצעות קו טלפוני. העמותה גם מפעילה מתנדבים שמציעים הסברה, עזרה וליווי למכורים ברחוב כדי לסייע להם לצאת ממעגל הסמים. בנוסף, העמותה עובדת אל מול מקבלי החלטות בכנסת ובממשלה על מנת לעורר מודעות ציבורית לבעיית הסמים בקרב צעירים דוברי רוסית ולקדם הצעות חקיקה ושינוי מדיניות בתחום הטיפול בבעיית הסמים. בראש העמותה עומדת אירינה לוית. לעמותה נמצאת בקשר שוטף עם כ-400 הורים שילדיהם מכורים לסמים.',
