    'description': 'מרכז טנ״א הוקם בבית החולים פוריה ב-2001 ביוזמת בית החולים עצמו ופועל במסגרת מחלקת הנשים. המרכז הוא אחד מחמישה מרכזים אקוטיים לטיפול בנפגעות תקיפה מינית בבתי חולים ברחבי הארץ ומצוי באחריות ובתקצוב משרד הבריאות. המרכז, אשר ממוקם במבנה נפרד, מעניק טיפול רב תחומי לנפגעות ונפגעי תקיפה מינית אשר כולל בדיקה רפואית, סיוע פסיכו-סוציאלי ותיעוד משפטי. המרכז משרת את כל אוכלוסיית הצפון, מטפל בנשים, גברים וילדות וילדים בכל הגילאים והמגזרים כולל אוכלוסיות עם צרכים מיוחדים. המרכז גם עורך פעילויות למניעת אלימות בקהילה.',
