    'description': 'ב-1986 נערכה בחסות שדולת הנשים אסיפה רבת משתתפות של נשות מקצוע בתחום בריאות הנפש ומתוכן, קבוצה של אחת-עשרה נשים, כולן פסיכולוגיות קליניות ועובדות סוציאליות קליניות, הקימו את מרכז יעוץ לאישה, במטרה לקדם בישראל את הגישה הפסיכולוגית הפמיניסטית ואת הראיה המגדרית של תחום בריאות הנפש. לאחר שנה של לימוד ורב-שיח הוקם ה\'קולקטיב\', שכלל את \'האימהות המייסדות\' של מרכז יעוץ לאישה. ה\'קולקטיב\' החל לפעול רשמית במתן שירותים קליניים מקצועיים בגישה פמיניסטית, וב-1988 נרשם כעמותה (ע״ר 580130789) בשם: ״ מרכז להתפתחות האישה״. השם שונה ל״מרכז ייעוץ לאישה״ בשנת 1989.\nמרכז יעוץ לאשה פועל לקידום נשים באמצעות פיתוח והטמעת הידע והפרקטיקה של תחום בריאות הנפש בראיה מגדרית. למרכז שני סניפים - ברמת גן ובירושלים והוא מציע שלושה סוגי פעילות: מרכז טיפול, מרכז הכשרה ומרכז העשרה ולמידה. המרכז הוקם מתוך תפיסה פמיניסטית ומציע לקהל הרחב מומחיות וידע בטיפול בנשים ובסוגיות הקשורות לעולמה הנפשי- רגשי של האשה, תוך התבוננות מעמיקה במאפייניה וצרכיה הייחודיים, עולמה הפנימי, כוחותיה ויחסיה עם המעגלים השונים בחייה. תפיסת המרכז היא ״כי קיימים הבדלים בין החוויה הנשית לחוויה הגברית, וכי נשים מושפעות באופן שונה מגורמים חברתיים, תרבותיים ופוליטיים. מתוך כך, עולה הצורך לענות לאתגרים הייחודיים העומדים בפני נשים ולהעמיק את הידע, את המודעות ואת הכלים לצרכים נפשיים שונים עמם הן מתמודדות.״ \nבמרכז מטופלות למעלה מ-300 נשים מדי שבוע בידי צוות של כ- 25 נשות מקצוע – עובדות סוציאליות קליניות, פסיכולוגיות קליניות ופסיכיאטריות – המתמחות בטיפול בנשים בכלל ובנשים עם טראומה בפרט. המרכז מספק יותר מ-10,000 שעות טיפול פרטני, קבוצתי, זוגי ומשפחתי בשנה, לצד תכניות הכשרה לנשות מקצוע ותכניות ללמידה וסדנאות בקהילה.',
