    'description': 'בשנת 1982 הוקמה באוניברסיטה העברית ה״תכנית להבדלים בין המינים״ בהובלת גליה גולן ועמיה ליבליך ובשנת 1990, בעקבות תרומה פרטית, הוקם ״מרכז לייפר ללימודי נשים ומגדר״ כתכנית רב-תחומית ומרכז אקדמי ללימוד, עיון ומחקר. במשך שנים רבות הציע מרכז לייפר חטיבה לתואר ראשון וקורסי מבוא בנושאי פמיניזם ומגדר והוא מקיים תכנית התמחות אישית רב-תחומית אישית לתואר מוסמך המאפשרת לסטודנטיות/ים לקבל תעודת התמחות במגדר לצד התואר הראשי. המרכז מקיים קולוקוויום לתלמידות מחקר, כנסים שנתיים וסדרת הרצאות לקהל הכללי ולציבור הסטודנטים ומחלק מדי שנה מלגות לסטודנטיות וסטודנטים לתארים מתקדמים. בראש מרכז לייפר עמדו חוקרות מובילות כגון: ד״ר מאירה וייס, פרופ\' תמר אלאור, פרופ\' נורית ירמיה, פרופ\' מימי אייזנשטדט, פרופ\' תמר זילבר ופרופ\' משה סלוחובסקי.',
