    'description': 'מרכז מהות הוקם ב-2004 כפרויקט במסגרת קואליציית נשים חיפה שמטרתו סיוע תעסוקתי לנשים יוצאות מקלטים. המרכז נרשם כעמותה עצמאית ב-2005 (ע״ר 580450120) ופעל עד 2013. המרכז, בניהולה של מיכל דגן, פעל כעמותה פמיניסטית לקידום שוויון מגדרי בשוק העבודה, בדגש על נשים מבוגרות מעוטות הכנסה. המרכז העניק סיוע פרטני - קורסים לנשים, השמה וליווי תעסוקתי. כמו גם עסק במרכז במחקר והפיק דוחות בנושאים: נשים בכלכלת מלחמה (בעקבות מלחמת לבנון השניה), עודות בשוק תעסוקה מעורער, גיל ותעסוקה ונשים בכלכלת פרישה. המרכז אף עסק בקידום מדיניות תעסוקה הוגנת לנשים באמצעות פרסום ניירת עמדה ופעולות סנגור. מרכז מהות נסגר בשל קשיים תקציביים בסוף שנת 2013.',
