    'description': '‏״מרכז מידע לבריאות האשה בני ברק״ (ע״ר 580453173) נוסד בשנת 2005 ביוזמתה של רו״ח שושנה גולדפינגר, מבקרת עיריית בני ברק, במטרה לסייע לנשים בקהילה בגישה למידע רפואי בסיסי. מטרות העמותה הן: ״לייסד, לנהל ולקדם מרכז מידע לבריאות האשה ברוח ישראל סבא. להפיץ מידע על בריאות האשה בקרב נשים ולעזור להן לממש את זכויותיהן בכל הקשור למוסדות בריאות״. המרכז פועל בתוך התחנה לבריאות המשפחה בבני ברק ומופעל על ידי עובדות ומתנדבות ומספק מידע רפואי כללי ופרטני לנשים חרדיות מרחבי הארץ באמצעות קו טלפון ושליחת חוברות עם מידע למתעניינות. בנוסף, המרכז מקיים ימי עיון והשתלמויות בנושא בריאות האשה לגורמים מגוונים בחברה החרדית.',
