    'description': 'מרכז מעגלים הוא רצף של מסגרות שיקומיות לעברייני מין. המרכז החליף את מרכז היום לשיקום עברייני מין בקהילה שהופעל בין 2005-2014 על ידי עמותת קש״ת במימון ופיקוח משרד הרוווחה. ב-2014 זכתה חברת ״פרויקטים שיקומיים״ (החברה היא חלק מקבוצת ״קידום פרויקטים שיקומיים בע״מ״ המקימה ומתפעלת מסגרות לנפגעי נפש ולבעלי צרכים מיוחדים ואף מספקת שירותי בריאות השן לתלמידים/ות). במכרז להפעלת המרכז ולהקמת הוסטל הפועל מאוקטובר 2015. ההפניה - באמצעות שירות המבחן למבוגרים או הרשות לשיקום האסיר. המימון והפיקוח - משרד הרווחה. רצף המסגרות כולל: הוסטל סגור ל-24 עברייני מין עם רמת מסוכנות גבוהה, מרכז יום בשלבים, קבוצות טיפוליות, מעקב וליווי פסיכיאטרי ותרופתי.',
