    'description': 'מרכז הסיוע לנפגעות תקיפה מינית ואונס, קריית-שמונה והגליל הוקם בשנת 1994 ופעל במסגרת פורום נשים קריית-שמונה. בשנת 1999 נפרד מרכז הסיוע מפורום נשים ונרשם כעמותה עצמאית. מטרת העמותה היא להעניק סיוע לנפגעות אלימות מינית ולהיאבק בתופעת האלימות המינית באמצעות העלאת מודעות ושינוי עמדות. מטרות המרכז הן להציע סיוע נפשי, תמיכה, ייעוץ וליווי לנפגעות אלימות מינית ולקרובים אליהן במשך 24 שעות ביממה. לקיים פעילות הסברתית והעלאת המודעות בקרב הציבור ולספק מידע אודות אלימות מינית. לפעול בשיתוף פעולה עם הגופים המטפלים הבאים במגע עם הנפגעת במטרה להיטיב את הבנתם לגבי הבעייתיות והייחוד של הנושא. המרכז פועל בסיוע של מתנדבות ועובדות, הוא מתקיים באמצעות תרומות ותמיכות ממשרד הרווחה.',
