    'description': 'מרכז הסיוע לנפגעות ונפגעי תקיפה מינית בשרון (ע״ר 580107993) הוקם בשנת 1984 כפרויקט של נעמ״ת ונרשם כעמותה עצמאית בשם ״קש״ב- קו שירות לאשה במצוקה״ בשנת 1987. מטרות העמותה הן ״סיוע לנפגעות/י תקיפה מינית ומניעתה.לתת סיוע נפשי ומעשי לנשים נפגעות תקיפה מינית, או במצב מצוקה מכל סיבה אחרת״. שם העמותה שונה בשנת 2002. המרכז משרת את כל אוכלוסיית השרון ב- 4 ערים, 3 מועצות אזוריות ו-10 מועצות מקומיות ומעניק סיוע 24 שעות ביממה באמצעות צוות מתנדבות. למרכז שני מוקדי פעילות עיקריים: סיוע וחינוך. בתחום הסיוע והתמיכה מפעיל המרכז קו סיוע 1202 לנשים וגברים, ליווי למשטרה, לבתי משפט ובתי חולים. במרכז מתקיימות פגישות אישיות וקבוצות תמיכה. בתחום החינוך, מקיים המרכז פעילות לצמצום התקיפה המינית בחברה באמצעות סדנאות וימי עיון בבתי ספר, מתנ״סים, מקומות עבודה, צבא ומסגרות נוספות. המרכז אף מבצע הכשרות לצוותים מקצועיים (עו״ס, פסיכולוגיים, חוקרים, רופאים, אחיות ויועצות). העמותה מתקיימת מתרומות ומתמיכה של משרד הרווחה.',
