    'description': 'מרכז הסיוע לנפגעות אונס ותקיפה מינית ירושלים ע״ש לינדה פלדמן (ע״ר) הוקם בשנת 1981 ע״י קבוצת מתנדבות במטרה לסייע לנשים אשר עברו תקיפה מינית. קבוצת המתנדבות הקימה את קו החירום אליו יכולה לפנות כל אישה באופן אנונימי, ולקבל תמיכה והקשבה, ללא כל תשלום, מתוך כבוד ויחס שוויוני ותוך שמירה קפדנית על סודיות. כיום, קו החירום שמספרו 1202 מאוייש 24 שעות ביממה ע״י מתנדבות המוכשרות להגשת סיוע בשעת משבר. המרכז מגיש סיוע נפשי ומעשי לנפגעות אונס ותקיפה מינית והקרובים להן, הכולל פגישות, ליווי לבית החולים, ליווי בהליך הפלילי כולל ליווי למשטרה והשתתפות בקבוצת תמיכה לנפגעות בילדות, לנפגעות בבגרות ולאמהות. במרכז הסיוע פועלת מחלקה חינוכית עניפה המופעלת ע״י מתנדבים ומתנדבות במטרה להביא לשינוי חברתי שיטמון בחובו הבניית שוויון בין המינים, הגברת מודעות בתחום האלימות והמיגדר והפחתת האלימות כלפי נשים והאלימות המינית בפרט. במסגרת זו עורכים המתנדבים והמתנדבות סדנאות לבני נוער בבתי ספר תיכוניים ובחטיבות הביניים לתלמידים ולמורים. במקביל למתן סיוע ולפעילות החינוכית פועל המרכז בתחום עיצוב המדיניות והעלאת נושאים בוערים לסדר היום הציבורי. העמותה מתקיימת מתרומות ומתמיכות של משרד הרווחה.',
