    'description': '״מרכז פנים״ (ע״ר 580538643) הוקם בשנת 2011 בירושלים במטרה לספק: ״ליווי ותמיכה לנשים וזוגות המתמודדים עם אתגרי פוריות, בגישה הוליסטית. לשמש כמקור מידע לזוגות העוברים אתגרי פוריות. לגרום להעלאת המודעות בציבור למצוקת האוכלוסייה העוברת טיפולי פוריות. לגרום לשינוי מעשי במערכות הבריאות במתן תמיכה לאוכלוסיה זו. לשמש לובי מול מערכות הבריאות וקובעי המדיניות.״ המרכז מקיים שיתופי פעולה עם יחידות להפרייה חוץ-גופית בתי חולים בירושלים במטרה להציע מערכת של תמיכה הוליסטית לנשים וזוגות העוברים טיפולי פוריות. שירותי המרכז כוללים פגישות ייעוץ עם עובדת סוציאלית המתמחה בתחום הפוריות, עיסוי הוליסטי ושיעורי יוגה, פעילות ספורטיבית והפניות לאנשי מקצוע. בנוסף, המרכז מעביר סדנאות והכשרות לאנשי טיפול בתחומים הנוגעים לפוריות לשם העלאת מודעות לסוגיות של אובדן ולידה שקטה.',
