    'description': 'תכנית המנטורינג לנערות וצעירות פועלת בבית הספר לעבודה סוציאלית במכללת ספיר משנת 2008 (פועלת גם באוניברסיטת תל אביב משנת 2002). התכנית פועלת כמיזם משותף עם השירות לנערות, צעירים וצעירות במשרד הרווחה. מרכז רותם הוא התרחבות של התכנית תוך פיתוח של תכניות נוספות ביחידת הפרקטיקה של המרכז, בתחומי התעסוקה, השכלה, ליווי במשברי החיים, מיצוי זכויות וסיוע משפטי ופיתוח קהילת צעירות בדרום. מלבד יחידת הפרקטיקה, קיימת יחידת מחקר והכשרות העוסקת בפיתוח ידע על נערות וצעירות במצבי סיכון ומצוקה ובשינוי מדיניות.',
