    'description': '״מרכז תמורה - המרכז לקידום השוויון״ (ע״ר 580470201), נוסד על ידי פרופ\' יפעת ביטון ועו״ד אייל שטרנברג במטרה להיאבק למען אוכלוסיות מוחלשות ומושתקות בחברה הישראלית, אשר מופלות על בסיס מרכיבי זהות כגון מגדר, מיניות ואתניות. על מנת להביא לשינוי חברתי וכלכלי, פועל המרכז בזירה המשפטית על ידי כלים כמו פנייה לערכאות, סיוע משפטי לקבוצות ולפרטים שנפגעו כתוצאה מאפליה וליווי של מהלכים המתמקדים בחקיקה מתקנת, ביצירת שינוי בתפיסה החברתית ובתודעה הציבורית בישראל. בתחום קידום השוויון המגדרי פועל המרכז באמצעות פעילות משפטית - ייצוג נשים, בעיקר נפגעות אלימות במשפחה ותקיפה מינית בתביעות נזיקיות שמטרתן רתימת מערכת המשפט לטובת תיקון עוולות שנעשו להן; פעילות חינוכית למשל בנושא של אלימות כלכלית וכן באמצעות חקיקה. עוד פועל המרכז בנושאי הדיור הציבורי, הזכות לחינוך, מאבק בגזענות, שוויון במרחב הציבורי ובקבלת שירותים ומוצרים וקידום שוויון בתעסוקה. בשנים האחרונות, מקיים המרכז שיתוף פעולה הדוק עם ביה״ס למשפטים ע״ש שטריקס של המכללה למנהל. הסטודנטיות והסטודנטים מביה״ס למשפטים פועלות ופועלים במסגרת ״הקליניקה לקידום השוויון באמצעות דיני הנזיקין״, ולוקחים/ות חלק בעבודת המרכז.',
