    'description': 'מרכז תמר הוא אחד מהמרכזים הרב-תחומיים הציבוריים המתמחים בטיפול בטראומה מינית. המרכז נותן שירות לנשים מאיזור ירושלים, הוא באחריות משרד הרווחה ומתוקצב גם על ידי משרד הבריאות. מופעל על ידי החברה העירונית ״אריאל״ - חברת בת של עיריית ירושלים. המרכז נפתח בעקבות החלטת ממשלה ב-2007 להקצות תקציבים לנפגעות תקיפה מינית וכן בעקבות וועדה בראשות מרית דנון - מנהלת הרשות למעמד האישה דאז, שיזמה הקמת מרכז ייעודי לנפגעות טראומה.',
