    'description': 'הפורום הפמיניסטי במרצ מקדם נושאי נשים ומגדר בתוך מוסדות המפלגה ומוביל מאבקים ופעילות ציבורית לקידום סדר יום פמיניסטי בישראל. הפורום יוזם פעילויות וחובר לארגוני הנשים להגברת המודעות לאי השיוויון המגדרי הקיים בישראל ולשיפור מצבן של נשים מכל חלקי החברה. מאבקים בולטים שקיים הפורום כוללים: מאבק באגודת אפרת וביטול הוועדות להפסקת הריון; חוק הבטחת הכנסה לנשים השוהות במקלטים לנשים מוכות; מאבק בהדרת נשים על כל ביטוייה; מאבק באלימות נגד נשים; שוויון מגדרי בתעסוקה. הפורום שנוהל על ידי שרון אורשלימי מהווה גלגול של פורום הנשים של מרצ שהוקם בשנת 2005 במטרה לחזק את נוכחות הנשים בתנועה. בין הפעילות הבולטות בפורום הנשים היו עדית דיאמנט, מיכל ארבל, מיכל עדן, מיכל רוזין.',
