    'description': 'מדרשת מת״ן הוקמה בירושלים בשנת 1988 על ידי הרבנית מלכה בינה ללימוד תורה גבוה לנשים, עם דגש על לימודי הלכה, תנ״ך וגמרא, והכשרות להצמיח מנהיגות נשית דתית ורוחנית בתחומים אלו ובכלל. בשנת 1989 נרשמה כעמותה (ע״ר 580140184) שמטרתה: ״ להקים מרכז תורני ללימודי קודש ולמחקר. לקדם לימודי קודש ומחקר ברמות שונות ובמסגרות שונות. מתן מילגות למתמחים בלימודי קודש ומחקר.״ לאורך השנים מת״ן פיתחה מספר תוכניות לימוד לנשים בגילאים שונים וברמות אינטנסיביות והעמקה שונות, וכן 11 סניפים ברחבי הארץ - מבאר שבע ועד זכרון יעקב.',
