    'description': '״השירות לנערות במצוקה״ הוקם במשרד הרווחה (משרד הסעד דאז) ב-1969 במקביל ליחידה לחבורות רחוב, והתמקד בתופעות כגון שוטטות וזנות. בהמשך שולבה היחידה באגף תקון במשרד. ב-1993 הורחב השירות לנערות וצעירות לגילאי 13-21. ב-2008 טווח הגילאים הורחב עוד יותר ל-13-25 וב-2010 אוחד השירות לנערות וצעירות עם השירות לנערים וצעירים תחת השם ״השירות לנוער, צעירות וצעירים״ (נוצ״ץ). השירות מופקד על איתור טיפול ושיקום כ- 40,000 נערות ונערים צעירות וצעירים בגילאי 13-25, הנמצאים בדרגות שונות של ניתוק מהחברה ומנותני השירותים ומצויים במצבי סיכון ומצוקה קשה״. באמצעות עמותות ולשכות לשירותים חברתיים ברשויות מקומיות, השירות מפעיל מקלטים, בתים חמים, הוסטלים, תכניות מנטורינג, תכניות הכשרה מקצועית והשכלה, תמיכה בנפגעות זנות וסחר, תמיכה בנפגעות תקיפה מינית, תמיכה בבני נוער וצעירים להט״בים ועוד.',
