    'description': 'עמותת נ.ט.ע (ע״ר 580456739) הוקמה בשנת 2006, ומנוהלת על ידי ד”ר יעל יצחקי. מטרותיה: לסייע לנשים לממש את שאיפותיהן המקצועיות, להתקדם ולהצליח בעולם העבודה וליצור תרבות ניהול בארגונים המקדמת הוגנות מגדרית. פעילות העמותה מתמקדת בקורסים והכשרות לארגונים ולנשים (כולל קורס לנשים ערביות), ייעוץ תעסוקתי פרטני לנשים ומחקר אודות השפעת עולם העבודה על נשים וגברים.',
