    'created': {
      'by': 'שרי אהרוני',
      'on': '05/07/2018'
    },
    'description': 'ארגון ״ניסן – מנהיגות צעירות״ (ע.ר. 580260586) היה ארגון פמיניסטי, ערבי-יהודי, שהוקם לשם לימוד, הדרכה והכשרת בני נוער בישראל למצוינות ולמעורבות אישית בחברה בדגש על עידוד השתלבותן של נשים צעירות בכל תחומי החינוך, התרבות והעשייה בישראל. פעילות העמותה התמקדה בהעצמה ופיתוח הפוטנציאל המנהיגותי אצל צעירות בישראל: ״אנו חותרות להקמת חברה צודקת, שוויונית ודימוקרטית. ולשים קץ לאפליה ושעבוד באמצעות תכנות חדשני, שיח אלטרנטיבי, אחווה ותמיכה הדדית״. העמותה הפעילה קבוצות דיאלוג ומפגשים בין נערות ונשים צעירות, יהודיות וערביות. מייסדת הארגון היתה כלנית דובר והוא נוהל במשך תקופה ארוכה בידי סנאא זריק בנצרת ובחיפה. מנהלותיו המשותפות האחרונות היו נסרין מזאווי וניצן תנעמי. לאחר המשבר הכלכלי (2008) העמותה הפסיקה לפעול ואינה מקיימת פעילות מאז שנת 2009.',
    'description_ar': 'منظمة ״نيسان- قياديات شابات״ (ج.م 580260586) كان بمثابة منظمة نسوية عربية يهودية تأسست لدراسة وتدريب وتأهيل الشباب في إسرائيل من أجل التميز والمشاركة الشخصية في المجتمع، مع التركيز على تشجيع إدماج الشابات في جميع مجالات التعليم والثقافة والممارسة في إسرائيل. ركزت أنشطة الجمعية على تمكين وتطوير القدرات القيادية للشابات في إسرائيل: ״نحن نسعى جاهدات لإقامة مجتمع عادل، منصف وديمقراطي. ووضع حد للتمييز والاستضعاف من خلال البرمجة المبتكرة، الخطاب البديل، الصداقة والدعم المتبادل ״. تدير الجمعية مجموعات حوار واجتماعات بين الفتيات الصغيرات والنساء اليهوديات والعربيات. كان مؤسس المنظمة متحدثًا باسم شقائق النعمان ولوقت طويل تديره سناء زريق في الناصرة وحيفا. وكان آخر المديرين التنفيذيين المشتركين له هو نسرين مزاوي ونيتسان تنعامي. بعد الأزمة الاقتصادية (2008)، توقفت الجمعية عن العمل ولم تنشط منذ عام 2009',
