    'description': 'הפורום הוקם ב-2011 בתמיכת קרן גנדיר, במטרה לקדם את מצבן של צעירות (בנות 18-25) במצבי מצוקה בישראל. הפורום כולל ארגונים מרכזיים המפעילים ומפתחים שירותי רווחה לצעירות בישראל, בהם: עלם, אותות, החצר הנשית, איתך - َمعَ ك משפטניות למען צדק חברתי, אל הלב, תכנית המנטורינג תל-אביב, מרכז רותם לקידום וחקר צעירות – מכללת ספיר, ילדים בסיכוי, בת עמי, הקליניקה הסוציו-משפטית - המכללה למנהל, מהפך - تغيير, نعم - נע״ם - נשים ערביות במרכז, מרכז סיוע לנפגעות תקיפה מינית בתל-אביב, עמותת ידידים.\nדרכי הפעולה כוללות למידה והתכנסויות (לעיתים יחד עם הצעירות שמביעות את קולן) ,צוותי עבודה והיגוי העוסקים בכתיבה, המשגה, שיום (naming) וניתוח נהלים קיימים בזירה של עבודה סוציאלית ונותני שירותים בתחום הרווחה, וכן במרחב שבין עבודה סוציאלית, משפטים ומדיניות ציבורית.\nשני פרויקטים שמקדם הפורום: פרויקט תעסוקה שמטרתו לבנות מודל תעסוקה רגיש מגדרית שהפורום יוכל להטמיע אותו במסגרת תכניות התעסוקה עבור צעירות במצבי סיכון. ופרויקט שמטרתו להעלות למודעות וכן, לפעול למניעה ולצמצום חובות קיימים בקרב צעירות במצבי סיכון.',
