    'description': 'נשים למען עתיד ישראל״ (ע״ר 580231207) הוקמה בשנת 1993 תחת השם ״נשים בירוק״ על ידי רות ומיכאל מטר כמחאה נגד ‏חתימת הסכם אוסלו בספטמבר של אותה השנה. מטרות העמותה הן: ״פעילות למען הקניית ערכי דת תרבות וחינוך בקהילה״ ועיקר פעילויותיה מובלות ומבוצעות על ידי נשים ‏דתיות-ציוניות. בשנות התשעים, כללה פעילות התנועה מחאות ציבוריות נגד תהליך השלום והקמת מאחזים. מאוחר יותר הפכה התנועה למובילה ״את שיח חזון הריבונות הישראלית המלאה על יהודה ושומרון״ ולצורך כך היא מתמקדת בארגון בכנסים, הרצאות ומאמצי הסברה ‏באמצעות כתב העת ״ריבונות״ וכן בארגון מתנדבים לפעולות פוליטיות ולצורך הקמת והפעלת שמורת הטבע ״עוז וגאו״ן״ לזכר גיל-‏עד, אייל ונפתלי בגוש עציון.‏',
