    'description': 'עמותת ״נשים בתמונה״ (ע״ר 580423473) הוקמה על ידי ענת שפרלינג-כהן ונעמה פריזנט-אורפז בשנת 2004 במטרה ״לייסד את הפסטיבל הראשון בישראל לנשים יוצרות (בימאיות ומפיקות) בקולנוע ובטלוויזיה. ליצור פסטיבל בינלאומי אשר יפגיש בין היוצרות הישראליות ליוצרות מחו״ל ויאפשר שיתוף פעולה פורה בין המדינות בתחום הקולנוע והטלוויזיה. לעודד ולקדם נשים יוצרות״. פעילותה המרכזית של העמותה היתה הפקת פסטיבל הקולנוע הבינלאומי לסרטי נשים ברחובות שהתקיים מדי שנה בין 2004-2013 בשיתוף עיריית רחובות. לאחר שעיריית רחובות חדלה לתמוך בפסטיבל, השתנתה פעילות העמותה ומאז 2015 מובילה העמותה בשיתוף מועצת הפיס לתרבות ולאמנות, קרן רבינוביץ\' לאמנויות, קרן גשר לקולנוע רב תרבותי ואיגוד התסריטאים פרויקט ״מענק פיתוח ליוצרות בתחום הקולנוע״ במטרה לקדם נשים יוצרות בתעשיית הקולנוע הישראלית. את העמותה המתחדשת מנהלות עטרה פריש וליאור אלפנט.',
