    'description': 'תנועת ״נשים דתיות למען קדושת החיים״ קמה בשנת 1996 בעקבות מהומות מנהרות הכותל והייתה ‏פעילה בשנת 1997 סביב קידום יציאת צה״ל מלבנון. בשנת 1998 התנועה נרשמה כעמותה. בשנים הללו תנועה קיימה כנסים בציבור הדתי והשתתפה בצעדות והפגנות יחד עם ארגונים נוספים של נשים לשלום כגון ״בת שלום״, ״אמהות ונשים למען שלום״ ו-״פורום נשים חיפה״ (כנראה לימים ״קואליציית נשים חיפה״). ממקימות ‏ומובילות התנועה נמנית צביה גרינפילד, אשה חרדית שלימים הייתה חברת ועד המנהל של ״בצלם״, ‏מהחותמים על יוזמת ז\'נבה, וחברת כנסת מטעם מרצ במשך כשלושה חודשים בסוף הכנסת ה-17 ‏בעקבות התפטרותו של יוסי ביילין. בשנת 2000 התנועה הייתה מהארגונים המקימים של ״קואליציית ‏נשים לשלום״, ולא נמצא תיעוד לפעילותה לאחר מכן.‏ בשנת 2019 נרשמה מחיקתה הסופית מפנקס העמותות.',
