    'description': '״עבודה שווה״ (חל״צ 515905180) נוסדה בשנת 2018 בעקבות יוזמה של טלי בראל-הס ומיכל מטס ליטמנוביץ שהפעילו קהילת פייסבוק משנת 2017. מטרות החל״צ הן: ״קידום שוויון מגדרי בשוק העבודה הישראלי. יצירת מדד מגדרי (״מדד עבודה שווה״), אשר בוחן חברות במשק הישראלי, עד כמה הן שוויוניות מבחינה מגדרית כלפי עובדיהן ועובדיהם.״ הפעילות של עבודה שווה עוסקת בקידום שוויון מגדרי בשוק העבודה על ידי יצירת סנטדרטים ונורמות עבודה בארגונים. העמותה מתמקדת בקידום מדיניות גיוון (diversity) באמצעות דירוג ארגונים על פי מדיניות השוויון המגדרי המיושמת בארגון. בנוסף, היא מציעה לארגונים ליווי בתהליכי מיפוי, המלצות וכתיבת תכנית עבודה להטמעת גיוון בארגון וכן מדד המאפשר השוואה בין ארגונים.',
