    'description': 'מרכז ״עדן: המקום שלך, החוויה שלך״ הוקם בשנת 2010 בירושלים על ידי ד״ר נעמי מרמון גרומט במטרה להפוך את המקווה ואת טקס הטבילה ‏לרלוונטית ומעצימה לנשים מודרניות: דתיות, מסורתיות וחילוניות. המרכז, שנרשם כעמותה (ע״ר 580521185) בשנת ‏‏2012 מפעיל מספר תוכניות: קורסי הכשרה והשתלמות לבלניות, למדריכות כלות ולמדריכי חתנים. כמו ‏כן, הוא מעביר הרצאות וקורסים באנגלית ובעברית לנשים בנושאים כגון: אינטימיות ומערכות יחסים בריאות (כולל חינוך ילדים ‏על מיניות), בעיות פוריות ואבדן הריון, התעללות מינית ופיזית ומנטלית, בריאות נשים, אירועים ממעגל ‏החיים הנשי (קבלת הווסת, נישואין, הריון, אבדן ומנופאוזה).‏',
