    'description': '‏״עלמה – אומנות נשית ברוח יהודית״ (ע״ר 580650455) נוסדה באופקים בשנת 2017 על ידי מירב חמו, אשה צעירה חרדית ‏מזרחית מקומית. מטרת העמותה ליצור מרחב והזדמנות ליצירה אמנותית של ועבור נשים בפריפריה. ‏מטרות העמותה הן: ״תיאטרון עלמה - אמנות נשית ברוח יהודית. העצמה נשית ועידוד יצירה ישראלית מקורית. קידום אומנויות ורוח על בסיס מקורות יהודיים השואבים השראה מהתנ״ך ומהמסורת היהודית. חיזוק תרבות וערכים דרך עולם הבמה, חיזוק התרבות בפריפריה, ובכל מקום אחר בארץ. נתינת במה ליוצרות ואמניות ונשות רוח. הנגשת תרבות בפריפריה ובכל רחבי הארץ. עידוד צריכה תיאטרון נשי ומענה לקהל נשי. שאיפה לחיבור ואימוץ נערות בסיכון והכשרתן לעולם הבמה. מתן מלגות לתלמידות.״',
