    'description': 'ה״עמותה לפסיכוסומטיקה במילדות וגניקולוגיה בישראל״ נוסדה ב-1996 (נרשמה כעמותה ב-1997, ע״ר 580295244) כעמותת-בת לארגון הבין-לאומי לפסיכוסומטיקה במיילדות וגינקולוגיה (International Society of Psychosomatic Obstetrics and Gynaecology), ביוזמת פרופ\' דליה מררי וד״ר יהודית אכמון, במטרה להעמיק את הבנת הקשר בין המרכיבים הפיזיולוגיים-ביולוגיים לבין המרכיבים הנפשיים בבריאות האישה, ולהפיץ את המידע בתחום זה בקרב אנשי המקצוע. העמותה רשומה באוניברסיטת תל אביב ומקדמת את מטרותיה באמצעות כנסי מדעי שנתי בבלינסון, ימי עיון, הוראת התחום, הקמת מאגר מחקרים, הפצת ניירות עמדה וניוזלטר רבעוני.',
