    'description': 'עמותת ״אוהלה״ (ע״ר 580634020) נוסדה בשנת 2016 על ידי מרוה זוהר במטרה להקים ולקדם מרחבי ידע וריפוי נשיים. מקומות של נשים עבור נשים בכל שלבי החיים. מרחבים המוקדשים להקשבה, ריפוי, לחיבור לידע נשי קדום ומחזק ולבניית קהילה. מטרות העמותה הן: ״ליצור מרחבי ריפוי נשיים - קהילתיים המאפשרים לכל נערה ואישה לחוות את עצמה כמקור הידיעה לגבי מה היא צריכה כדי ליילד ולרפא את עצמה, תוך דגש על ריפוי מאונס ומאלימות מגדרית. לחנך, להכשיר ולהפיץ ידע בקרב נשות ואנשי מקצוע, קובעי מדיניות, בני משפחה ובחברה כולה אודות דרכי טיפול פמיניסטיות - הוליסטיות בפוסט - טראומה מינית, תורת המיילדות המסורתית - קהילתית ובריאות האישה בכלל. להוות סביבת עבודה בריאה שתשמש מודל של טיפול עצמי ותקשורת מכבדת ומקרבת לעובדות, למתנדבות ולכל השותפות בעמותה.״ פרויקט הדגל של העמותה הוא פרויקט אמ״ן - אדמת מרפא נשית, כפר לטיפול ושיקום נפגעות תקיפה מינית ואונס אשר החל לפעול בשנת 2019.',
