    'description': '״עמותת אלאנואר לקידום מעמד האשה באום אלפאחם״ (ע״ר 580361962) נרשמה בשנת 2000 במטרה לפעול לשם: ״יצירת בסיס להתארגנות קהילתית לייצוג אוכלוסיית הנשים באום אל-פחם. העלאת המודעות לזכויות האשה בחברה. הקמת מרכז רב תרבותי ורב תכליתי לנשים. סיוע לאוכלוסיית הנשים החשופה להתעללות פיזית ולנערות במצוקה. סיוע לילדים בסיכון.״ לעמותה פעילות רבה המרוכזת באום אל-פאחם וכוללת הפעלת מעון יום, גני ילדים ומעון רב-תכליתי. בין השנים 2008-2017 הפעילה העמותה את המרכז להתפתחות הילד כחלק ממכרז של משרד הבריאות.',
    'description_ar': '״جمعية الانوار لتطوير مكانة المرأة في ام الفحم״ (ج. م580361962) - سجلت في عام 2000 بهدف انشاء أساس للتنظيم المجتمعي لتمثيل النساء في ام الفحم. زيادة الوعي حول حقوق المرأة في المجتمع، انشاء مركز متعدد الثقافات ومتعدد الأوجه للنساء. مساندة النساء المعرضات للإيذاء الجسدي والشابات في حالة الضائقة. ومساعدة الأطفال المعرضين للخطر. للجمعية نشاطات متعددة ومركزها في ام الفهم وتشمل تشغيل ملجأ يومي، حضانات أطفال، وملجأ متعدد الأغراض. بين أعوام 2008-2017 شغلت الجمعية مركز تطور الطفل كجزء من مناقصة وزارة الصحة.',
