    'description': 'עמותת ״ברכה - התמודדות והקטנת הסיכון של סרטן תורשתי״ (ע״ר 580508075) נוסדה בכפר סבא בשנת 2009 והינה הארגון היחיד בישראל שמטרתו העיקרית הינה הגדלת המודעות לסרטן שד ושחלות תורשתי ולתמיכה וסיוע לכל קהילת הנשאים ונשאיות המוטציה בגן BRCA וגנים נוספים. פעילותה מוקדשת להגדלת המודעות לבדיקות הגנטיות, למניעת הסרטן התורשתי וכן לשיפור חיי כל מי שנפגע/ה מסרטן זה. עמותת ברכה פועלת על בסיס התנדבותי ותרומות בלבד ומקיימת פעילות בכל מגזרי האוכלוסיה בישראל: במגזר היהודי ובמגזר הערבי, כולל ייעוץ לגברים נשאים. מטרות העמותה הן: ״שיפור איכות החיים של משפחות בהם קיים רקע מורש של סרטן שד ושחלות. הגדלת המודעות בקרב הציבור לקיומן של בדיקות גנטיות לסרטן מורש של שד ושחלות. מתן מידע על ייעוץ גנטי לסרטן שד ושחלות מורשים. מתן מידע לגבי האפשרויות השונות להקטנת הסיכון לפתח סרטן שד ושחלות״. העמותה מספקת מידע רפואי ומענה אישי דרך אתר האינטרנט, קבוצת פייסבוק והטלפון ומקיימת כנסים וארועים לנשאיות של גן BRCA.',
