    'created': {
      'by': 'יעל חסון',
      'on': '26/06/2018'
    },
    'description': 'עמותת כ״ן-כוח נשים לקידום מנהיגות נשים בישראל (WePower) (ע״ר 580357929) נוסדה בשנת 2000 על ידי מיכל יודין במטרה לקדם נשים למוקדי קבלת ההחלטות בישראל, בדגש על הזירה הציבורית והפוליטית. העמותה פועלת להגדלת מספר הנשים המתמודדות והנבחרות לכנסת ולרשויות המקומיות וכן לקידום נשים לתפקידי ניהול בכירים, לדירקטוריונים, ולשולחנות קבלת ההחלטות.\nהעמותה פועלת בפריסה ארצית בשיתוף משרדי הממשלה, מרכז השלטון המקומי, מוסדות אקדמיים, רשויות מקומיות בכל רחבי הארץ, ושותפים מהמגזר השלישי והעסקי.\nהעמותה מאמינה כי כאשר נשים יהיו בעמדות כוח הן יוכלו להשפיע על חלוקת המשאבים באופן ישיר וכך לענות באופן מיטבי על צרכיהן של נשים בחברה ולהשמיע את קולן, מתוך תפיסה כי ייצוג שוויוני בדרג מקבלי ההחלטות יעלה את מדדי השוויון המגדרי בישראל ויביא לידי שינוי חברתי בסדר העדיפויות האזרחי ובחלוקת המשאבים באופן שיענה על צרכי כלל האוכלוסיה בישראל.\nפעילות העמותה כוללת קורסים והכשרות לנשים שמטרתם עידוד נשים להיכנס למוקדי קבלת החלטות, קידום חקיקה, ארגון ימי עיון, כנסים וקמפיינים. בשנת 2016 הציגה העמותה את מדד המגדר בשלטון המקומי. נכון ל-2018 מנכ״לית העמותה היא ד״ר מזל שאול. לפניה ניהלו את כ״ן ליאל אבן-זהר ויפעת זמיר.',
