    'description': '״עמותת נשי ערה וערערה״ (ע״ר 580139616) הוקמה בשנת 1989 במטרה ״להקים, להחזיק ולנהל גני ילדים. לשפר את מצב החינוך בגני ילדים קיימים. לנהל פעילות הדרכה לגננות למען קידום חינוך הילדים״. בשנת 1993 העמותה הפעילה 6 גני טרום חובה לגילאי 3-5 המשרתים כ-150 ילדים וילדות והועסקו בהן כ-9 גננות. בשנת 2006 הוגשה בקשה למתן צו פירוק לעמותה.',
