    'description': 'העמותה (ע״ר 580250462) הוקמה בשנת 1994 ובשנת 1999 התפצלה למרכז הסיוע גליל גולן. מטרות העמותה הן: ״לפעול ולחתור למען שוויון בין המינים בחברה ולפעול נגד תופעות של אפליה על בסיס מיני ולשיתוף פעולה בין נשים מקבוצות שונות באיזורנו. להוות תמיכה לנשים בעמדות יצוג ולפעול על מנת למנוע אלימות פיזית, מינית ופסיכולוגית כלפי נשים. הפצת תרבות יהודית בקרב קבוצות שונות באוכלוסיה.״ פעילויות העמותה הן קהילתיות בעיקרן וכוללות הרצאות, קורסים וסדנאות בנושאים הנוגעים למנהיגות והעצמה, בריאות האשה, חינוך למניעת אלימות כלפי נשים. העמותה מקיימת גם סיורים ברחבי הארץ לנשים מקריית שמונה.',
