    'description': 'פאנורמה - קהילה בי ופאנסקסואלית פמיניסטית הוקמה ב-2009 על ידי שירי איזנר במטרה להעלות את הנראות והמודעות לביסקסואליות בקהילה הלהט״בית ובחברה. שנות פעילותו המרכזיות של הארגון היו 2009-2012 והוא פעל בעיקר בתל אביב. פעילויות הארגון כללו ארגון הקרנות של סרטים ביסקסואלים, מסיבות קהילתיות, יום עיון ביסקסואלי, עבודה תקשורתית ועבודה מול ארגונים קהילתיים לשיפור המענה לקהילה הביסקסואלית, מפגשים חברתיים, השתתפות בהפגנות ומצעדי גאווה ודוברות באירועים. בשנים 2013-2014, הפעילות של הארגון הלכה ודעכה, וכאשר איזנר פרשה מפעילות פוליטית, הוא התפוגג והוחלף על ידי קבוצות פעילות ביסקסואליות בפייסבוק ובארגונים קהילתיים.',
