    'description': 'הפורום לקידום מעמד האשה ברחובות פועל כמועצת נשים עירונית משנת 2007. בראשו עומדת חברת מועצה ובעלות התפקידים בפורום הן עובדות עירייה (כולל היועצת לקידום מעמד האישה) וחברות מועצה. הפורום לקידום מעמד האישה פועל להעצמת נשים, להעלאת המודעות בציבור לקידומן של נשים והכרה בשוויון בין המינים. מטרות הפורום כוללות: הובלת תהליכים עירוניים ושינוי מדיניות בנושא קידום מעמד האישה בתחומים כלכליים, תעסוקתיים ומנהיגות נשים. הפעילות העיקרית שמבצע הפורום כוללת: קורסים בתחומים תעסוקתיים-כלכליים וחברתיים להעצמת נשים ולפיתוח מנהיגות נשים וכן פרוייקטים קהילתיים כגון ירידי נשים יוצרות, קידום בריאות וספורט, הרצאות וטיולים ומניעת מניעת אלימות כלפי נשים ובכלל זה הטרדה מינית.',
