    'description': 'האזכור המוקדם ביותר של ״מועצת המקלטים״ מופיע אצל ינאי (2005) ומתיחס לפעילות משותפת של נציגות המקלטים ושדולת הנשים להגיע להסדר עם משרד הבינוי והשיכון לפיו אישה ששהתה במקלט והמצויה בהליכי גירושין זכאית לקבל סיוע בשכר דירה למשך שנה אחת, כדי לאפשר לה לחיות באופן עצמאי בקהילה, בלי שתצטרך לשוב למשק־הבית שסבלה בו בעבר. במשך השנים שינתה המועצה את שמה ל״ארגון הגג של מפעילי המקלטים״. בשנת 2015, שב הפורום לפעול תחת השם ״פורום מפעילי המקלטים״. הפורום פועל כקואליצייה המייצגת את כל המקלטים לנשים נפגעות אלימות אל מול משרדי הממשלה ופועל לקידום ענייניהן של נשים נפגעות אלימות השוהות במקלטים. ניהול תקציב הפורום עובר בין המקלטים השונים ובשנת 2018 ריכוזו מצוי בידי עמותת מעון חירום לנשים בחיפה.',
