    'description': 'בשנת 2015 המשרד לשירותי דת יזם שיתוף פעולה עם הוועדה לקידום מעמד האשה להקמת ״פורום ‏נשות הרבנים״. הפורום, הפועל בניהולו של המשרד לשירותי דת, מעניק הכשרה וליווי לנשות רבנים ‏חוצות-מגזרים לטפל בבעיית האלימות המגדרית בתוך הקהילות שלהן. הפורום מובל ע״י הרבנית ציפי ‏לאו, אשתו של הרב הראשי האשכנזי לישראל הרב דוד לאו, ויפה דרעי, אשתו של יו״ר ש״ס אריה דרעי. ‏כמו כן, הרבנית עדינה בר-שלום הייתה מעורבת בהקמת הפורום וליוויו. תוכנית ההכשרה של הרבניות ‏נבנתה על ידי עורכת הדין והטוענת הרבנית מטעם ״יד לאישה״ ורדית רוזנבלום, המכהנת גם כעמיתה ‏במכון למדיניות תורנית ״מידת״.‏',
