    'description': '״פורום נשות קריירה במזרח התיכון״ (ע״ר 58-026699-7) היה יוזמה של עמירה דותן שכללה שותפות מירדן, מצרים, מרוקו והרשות הפלסטינית, ונועדה לקדם דיאלוג בין נשים באמצעות קידום מיזמים עסקיים משותפים. היוזמה החלה ככל הנראה בעקבות מפגש הנשים שהתקיים ב״ועידת קזבלנקה״ בשנת 1994 שהיתה הועידה הכלכלית הראשונה למזרח התיכון וצפון אפריקה לאחר חתימת הצהרת העקרונות בין ישראל לאש״ף בשנת 1993. החל משנת 2000 לא נרשמה לעמותה פעילות כספית ובשנת 2003 הוגשה בקשה למחיקה מוקדמת מפנקס העמותות. ב-2011 הוגשה בקשה למחיקה מרצון.',
