    'description': '״אלמונתדא עמותת נשים למען החברה״ (ע״ר 580340230) נוסדה בשנת 1999 בכפר קאסם כ״פורום נשים מוסלמיות״ במטרה לקדם ״פעילות תרבות וחינוך לנשים ונערות. עידוד ויזמה של פעילות ספורט אצל נשים ונערות. הגברת המודעות לשמירה על איכות הסביבה.״ מדוחות העמותה לרשם העמותות עולה כי עד שנת 2010 העמותה קיימה פעילות מועטת בהתנדבות שכללה ״הרצאות במסגדים לנשים שעסקו בנושאים מגוונים והתאימו לאווירה השוררת בכפר״. החל משנת 2010, העמותה מפעילה מעון לילדים, מקיימת הרצאות בנושא קידום מעמד האשה במגזר הערבי, סדנאות לשילוב נשים באקדמיה וסיוע לנזקקים.',
    'description_ar': 'المنتدى جميعة نسائية من اجل المجتمع (ج.م 580340230). تأسس عام 1999 في كفر قاسم بأسم (منتدى المرأة المسلمة) بهدف تعزيز نشاطات اجتماعية وتربوية للنساء والشابات. تعزيز ومبادرة لنشاطات رياضية للنساء والشابات. رفع الوعي حول أهمية الحفاظ على البيئة. من تقارير الجمعية يظهر ان نشاط الجمعية حتى عام 2011 كانت قليلة من خلال التطوع لإعطاء محاضرات في المساجد للنساء في مواضيع مختلفة التي تتناسب مع الجو العام في المدينة. وابتداء من عام 2010 تركز نشاط المنتدى في تشغيل حضانات للاطفاال، محاضرات حول مكانة المرأة في المجتمع العربي، وورشات لتعزيز انخراط النساء في الأكاديمية ومساعدة المحتاجين.',
