    'description': '״פורום נשים ראש העין״ נוסד בשנת 2003 כפורום מקומי שהוקם כיוזמה קהילתית הדומה למועצת נשים. בניגוד למועצות נשים אחרות בארץ, לא עומדת בראשו חברת מועצה או היועצת למעמד האישה. יו״ר הפורום מועסקת ברבע משרה ומקבלת את שכרה מהעמותה לשירותים חברתיים. בפורום חברות נכון ל-2017 1,700 נשים תושבות העיר, הוא מפעיל אתר אינטרנט בהתנדבות שכולל בין היתר מאגר עסקי נשים מקומיים. הפורום מקבל תמיכה של כ-20 אלף ש״ח בשנה מתקציב היועצת למעמד האישה ברשות המקומית שמחציתו הולך לאירועי יום האישה והיום הבינלאומי למניעת אלימות נגד נשים. ב-2005 קיבל מבנה לפעילות מהעירייה.',
