    'description': 'פורום תקנה נוסד ב-2003 על ידי נשות ואנשי הנהגה דתית, חינוך, משפט וטיפול על מנת למנוע ולטפל בפגיעות מיניות מבעלי מרות וסמכות בחברה הדתית ולמען והעלאת המודעות הציבורית לנושא. הפורום נרשם כעמותה בשנת 2009 (ע״ר 580519692) שמטרותיה הן: ״פיתוח מודל התמודדות עם פגיעה מינית מצד בעל מרות או סמכות בתוך הקהילה הדתית, יצירת דרך נוספת עבור מתלוננות ומתלוננים לטפל בניצול או הטרדה ע״י מרות או סמכות, במקרה של סירוב להתלונן במשטרה.״ הפורום מטפל בתלונות על הטרדות או פגיעות מיניות שלא מטופלות על ידי רשויות החוק בין אם מבחירת המתלוננת או בשל הקריטריונים המשפטיים לטיפול על ידי הרשויות. הפורום מקיים שיתוף פעולה והתייעצויות עם היועץ המשפטי לממשלה.',
