    'description': 'פסיכולוגיי היה פורום מטפלים להט״בים שהוקם בשנת 2001 על ידי הבית הפתוח בירושלים. עם השנים הוא עבר לפעולה עצמאית והפעיל שלוש קבוצות עמיתים בחיפה, תל אביב וירושלים. הפורום פעל לקידום הידע, המחקר וההכשרה בטיפול פסיכותרפי ללהט״בים בישראל, ולהעלאת המודעות של מטפלים לצרכיה הייחודיים של אוכלוסיה זו. הפורום ארגן כנסים, ימי עיון והרצאות, הפעיל קבוצת קריאה וקבוצת מחקר, והפעיל גם אתר מידע ופורום אינטרנטי. בפורום היו חברים כ - 200 מטפלות ומטפלים ומאז שנת 2013 לא אותרה פעילות ציבורית מטעמו.',
