    'description': 'התכנית הוקמה בשנת 2012 במרכז הבינתחומי הרצליה על ידי שתי סטודנטיות בשם שני הראל ולינוי קצב. בשנת 2018 נרשמה כעמותה שמטרתה ״לפעול לקידום נשים בפיתוח קריירה משמעותית והשתלבות בתפקידים ניהוליים ויזמיים, החל מתחילת דרכן המקצועית ולכל אורכה. להוות פלטפורמה שתסייע באמצעות רישות מקצועי במציאת הזדמנויות לקידום מקצועי עבור ציבור הנשים, לפעול להקמת סניפים שבהם יפעלו תכניות לקידום ציבור הנשים בפיתוח קריירה במוסדות אקדמאיים בארץ ובחו״ל, לסייע לציבור הנשים במציאת עבודה או בייצור הכנסה עצמאית. לקדם, לקיים, לנהל ולבצע תכניות יזמות ומיזמים חברתיים ו/או חינוכיים ו/או טכנולוגיים, בין בעצמה ובין ביחד עם אחרים בקשר עם ולשם קידום מעמד הנשים בשוק העבודה.״ העמותה מפעילה תכניות מפגש בין סטודנטיות מאוניברסיטאות שונות בארץ ונשות עסקים וקריירה.',
