    'description': 'פרויקט קשר ישראל הינו הסניף הישראלי של הארגון היהודי הבינלאומי ״קשר״ (Kesher) שממוקם בארה״ב אשר נוסד בשנת 2008. מטרת העמותה היא ״להקים ולהפעיל רשת בין נשים עולות, לסייע להן להתחבר אחת לשנייה ולאפשר להן נגישות למשאבים להם הן זקוקות על מנת לשנות את חייהן, את הקהילה היהודית בה הן חיות ואת החברה בכלל. לעודד אקטיביזם בקרב נשים עולות לקידום שוויון מגדרי וסובלנות דתית ועדתית ברוח הציווי היהודי של תיקון עולם״. הארגון תומך בפעילות של כ-18 קבוצות נשים ברחבי הארץ. הקבוצות מקיימות מפגשים חברתיים, הכשרה מקצועית, לימוד שפה, פעילות למען קידום זכויות נשים ועוד. מייסדת העמותה היא לזלי זקס, שהיתה בעבר מנכלית שדולת הנשים, פעילה ב״נשות הכותל״ ועוד.',
