    'description': '״צאינה וראינה״ היתה קולקטיב פמיניסטי וחנות ספרים אשר נוצר מתוך פעילות ה״תנועה הפמיניסטית בתל אביב״. אסתר עילם כותבת: ״בתל אביב שכרנו בדצמבר 1976 דירה ברחוב דיזינגוף 224 שמומנה ממסי החברות ובה גרנו שנתיים. לאחר העזיבה התארגנה קבוצה מתוך חברות התנועה במטרה להקים מרכז אחר. עם נשים נוספות היא הקימה את ״צאינה וראינה״ מרכז פמיניסטי שפעל משנת 1979 עד שנת 1982.״ (עילם 2012: 120). הקבוצה מעולם לא נרשמה כעמותה, אבל יתכן שנרשמה כאגודה עותומנית.',
