    'description': 'בית ״צידה לדרך״ הוא הוסטל שיקומי לנשים (ולגברים בודדים) המתמודדות עם הפרעות אכילה, שהוקם בשנת 2009, ברמת ישי, בשיתוף פעולה בין המרכז למפעלים מיוחדים של הביטוח הלאומי, משרד הבריאות ומשרד הרווחה, עם עמותת ״תעסוקת השני״. הוא אחד משלושה בתים שיקומיים הפועלים ברחבי הארץ, ומיועד לחולות שעדיין מפגינות סימפטומים פעילים בגילאי 18-32. הבית יכול להכיל עד 14 שוהות בכל פעם, והוא נמצא באופן קבוע בתפוסה מלאה, עם רשימת המתנה. הצעירות השוהות בבית משתלבות במסגרות לימודים ותעסוקה, מקבלות ליווי סביב הארוחות וטיפול פסיכולוגי ופסיכיאטרי, נפגשות בקביעות עם דיאטנית, לומדות כישורי חיים וניהול משק בית בסיסיים, משתתפות בקבוצות תמיכה ומשתתפות בפעילויות חברתיות בבית ומחוצה לו. ב - 2012 משרד הבריאות התקשר ללא מכרז עם עמותת ״תעסוקת השני״ להמשך ניהול הבית. בשלב כלשהו, ככל הנראה מיד לאחר ההתקשרות, ניהול הבית עבר לחברה הפרטית ״שני בקהילה״, הרשומה ברשם החברות בדיוק באותה הכתובת ממנה פעלה עמותת ״תעסוקת השני״. הרישומים של משרד הבריאות בנושא מאוד בלתי ברורים, וגם היום הוא ממשיך להתקשר לכאורה, עם ״תעסוקת השני״ באופן רשמי, כשהספק הוא ״שני בקהילה״ ובחלק מהמקרים הרשומים בכלל מתיחסים ל״עמותת שני בקהילה״ שלא התקיימה מעולם. זאת למרות ש״תעסוקת השני״ כבר אינה קיימת, לאחר שב - 2014 שינתה את שמה ל״נופית יזמות חברתית״. נראה שמדובר במנהל לא תקין, אך לא ברור מה ובאיזו מידה.',
