    'description': 'QueenB נוסדה בשנת 2015 על ידי שתי סטודנטיות למדעי המחשב מהאוניברסיטה העברית, נטע מוזס ויסמין דנסקי, במטרה להעלות את מספר הנשים הבוחרות לעסוק במקצועות טכנולוגיים. בשנת 2017 היא נרשמה כעמותה בשם ״קווין בי״ (ע״ר 580644466) שמטרותיה: ״פעילות חינוכית, לימוד נערות לאחר שעות בית הספר. עידוד נערות לבחירת מגמת מחשבים במטרה להשתלב בחברות הייטק״. פעילות העמותה כוללת תכנית שבמהלכה סטודנטיות למדעי המחשב מלמדות נערות בכיתה ח\' ומקבלות מלגה. בנוסף, התכנית מכוונת ליצור רשת מקצועית לסטודנטיות למדעי המחשב והנדסה בשישה מוקדים: אוניברסיטת תל אביב, האוניברסיטה העברית, הטכניון בחיפה, אוניברסיטת בן-גוריון, בחברת Western digital בכפר סבא וב- JestHUB במזרח ירושלים.',
