    'description': 'מרכז ״קול האישה״ בחיפה הוקם בשנת 1979 על ידי מרשה פרידמן שכותבת: ״פתחתי עסק קטן ולא נושא רווחים של הזמנת ספרים דרך הדואר, ״קול האשה״ [...] מדי חודש בחודשו נסעתי לתל אביב וסרקתי את כל המדפים בחיפוש אחר ספרות פמיניסטית. [...] החלטנו לשכור דירה בקומה שנייה של בניין-אבן ישן (בחיפה) בבעולתה של אשה מבוגרת שהיתה בעבר פעילה בויצו. [...] מבלי שתכננו זאת מראש, הפך ״קול האשה״ למרכז לנשים וחנות ספרים גם יחד.״ (עמ\' 191). הפעילות ב״קול האשה״ הייתה מגוונת: הרצאות על בריאות ופוליטיקה נשית, הכרות עם מוסדות כמו מרכזי סיוע, קבוצות להעלאת תודעה וחוגים לנשים כמו תיקוני בית, ג\'אז וכדומה. המרכז שימש גם מקום מפגש לקהילה הלסבית הצפונית.',
