    'description': '״מרכז טיפול וסיוע לפליטים אפריקאים- ארטס״ (ע״ר 580520526) הוקם בשנת 2010 על ידי דידי מימון כהן, פסיכולוגית קלינית בהכשרתה אשר חקרה את ההשפעה של אונס והתעללות מינית על מבקשות מקלט מאריתראה אשר הגיעו לישראל דרך סיני. העמותה מספקת שירותים לנשים פליטות ומבקשות מקלט וילדיהן. קוצ\'ינטה - פרויקט הדגל של העמותה הוא קולקטיב של נשים הקולעות סלים. העמותה מסייעת במכירתם בירידים וחנויות וכן מכשירה נשים לקליעת סלים. בנוסף מספקת העמותה תמיכה פסיכו-סוציאלית, סיוע בבעיות רפואיות, בקשיי היום-יום וסיוע בטיפול בעצמן ובילדים. העמותה רשומה בכתובת של המייסדת בהרצליה אך הכתובת הרשמית של היא ברח\' הר ציון בדרום תל אביב. העמותה נתמכת בין היתר על ידי הכנסיה (הפטריארך הלטיני בירושלים).',
