    'description': 'פרויקט ״קידמה״ לקידום האישה בחברה הישראלית הוקם בשנת 1984 על ידי ארלט אדלר במטרה להעביר הרצאות וקורסים על מעמד האשה לנשים שלא למדו במסגרת אקדמית מסודרת על ידי הבאתן לאוניברסיטת חיפה ועל ידי הרצאות בקהילות שונות. את הפרויקט ניהלה פרופ\' מרילין ספר והוא פעל במסגרת אוניברסיטת חיפה עד שנת 2010. במשך שנות קיומו פעל הפרויקט בשיתוף פעולה עם ארגוני נשים רבים ברמה הארצית והמקומית ויזם אינספור פעילויות חינוכיות. את הפעילויות ב״קידמה״ העבירו נשות אקדמיה ונשות מקצוע. ואלה כללו קבוצות, ימי עיון וקורסים לאוכלוסיות מגוונות בקהילה ובאוניברסיטה. נושאי הפעילויות היו פורצי דרך בזמנם וכללו עיסוק בשיוויון בין המינים, זיהוי ומניעת אלימות כלפי נשים, אסרטיביות, פיתוח כישורי מנהיגות, דימוי עצמי ודימוי גוף, העצמת נשים ומנהיגות. מטרת הפעילויות נועדה ״לקדם נשים מבחינה אישית ומקצועית בקהילה ולהוביל לשינויים חברתיים״.',
