    'description': 'העמותה נרשמה ב-1983 בשם: ״בית אריאל - עמותה לטיפול בנוער במצוקה״. קודם לכן היתה אגודה עות\'מנית בשם: אגודה לטיפול בנערות בירושלים. ב-2003 שונה השם ל״קשת - קידום שירותי תקון״. המטרה העיקרית של העמותה: ״ליזום ולהפעיל תכניות לקידום, מניעה וטיפול של אוכלוסיות בסכנה, במצבי התדרדרות, שוליות חברתית, עבריינות ואלימות״. העמותה מפעילה שירותים עבור משרד הרווחה בירושלים, פ״ת, רעננה והרצליה. היא מפעילה מקלטים לקרבנות סחר (מקלט ״מעגן״ לנשים ו״אטלס״ לגברים״), הוסטל לגברים אלימים (״בית נועם״) וחלופת אשפוז לבנות 18 ומעלה, נפגעות טראומה תקיפה מינית (״בית אלה״). מנכ״לית העמותה - תמר אלון (2019) מכהנת גם כמנכ״לית עמותת אותות.',
