    'description': '״קש״ת - קשב ותמיכה״ (ע״ר 580518785) הייתה עמותה שפעלה מ - 2009. מטרות העמותה היו ״פיתוח ומתן שירותי קשב ותמיכה לצרכי קהילת הלהט״ב בסוגיות הנוגעות לקשיים על רקע נטייה מינית, זהות מגדרית ונושאים קשורים אחרים. מניעת אלימות או הסתה לאלימות נגד הקהילה או בקרב הקהילה. ריכוז מידע לרווחת חברי הקהילה. ניתוח מגמות וביצוע מחקרים בנושאי הקהילה הגאה״. העמותה סיפקה שירותי תמיכה נפשית אינטרנטית לקהילה הלהט״בית. מאחר ואין דו״חות שנתיים של העמותה ברשם העמותות, קשה לדעת מתי בדיוק היא הפסיקה את פעילותה, אין כל עדות לפעילות מאז 2012. העמותה נמצאת היום בתהליכי פירוק מרצון.',
