    'description': '״שוות - המרכז לקידום נשים בזירה הציבורית״ הוקם במכון ון ליר בירושלים בתמיכת קרן דפנה יזרעאלי בסוף שנת 2009 מתוך מחויבות לרעיון של הטמעת חשיבה מגדרית (gender mainstreaming) כאסטרטגיה כוללת לקידום מעמדן הדמוקרטי והאזרחי של נשים מקבוצות שונות. עמדה זו מבקשת להוציא את סוגיית אי-השוויון המגדרי מן ה״גטו הנשי״, ולנסחה מחדש בתור תפיסת עולם חברתית כללית, הנוגעת לגברים ולנשים כאחד ולסדרי החברה בישראל בכלל. שוות שם לו למטרה להפוך את המושגים של שוויון מגדרי לחלק בלתי נפרד מן החשיבה והפעולה של קובעי המדיניות והמתכננים בתחומי החיים השונים. שוות עוסק במחקר, מציע חשיבה אסטרטגית ויוזם פרויקטים ותוכניות בתחומים הנוגעים להטמעה של חשיבה מגדרית ולשוויון מגדרי בישראל. מקימות המרכז, פרופ\' נעמי חזן ופרופ\' חנה הרצוג, חותרות להפכו למסגרת שמפגישה בין ארגוני שטח, חוקרות/ים, מחוקקות/ים ומקבלי/ות החלטות, מתוך תקווה שהדיאלוג והחשיבה המשותפת ישמשו מקור מידע, הנחיה וניסיון מקצועי ומחקרי לכל המעוניינים בקידום שוויון וחשיבה מגדריים בישראל.',
