    'description': 'במרץ 1987 פנתה יעל רום מטעם שדולת הנשים אל המרכז לשלטון המקומי בהצעה לסייע בהקמת מועצות ציבוריות של נשים ברשויות המקומיות בדומה למודל החיפאי שהחל בשנת 1974. במכתב נאמר כי ״השירות במועצות הצבוריות עשוי לשמש בית ספר מצוין ללימוד התפקידים של נציגי צבור [...] הנשים הן הצרכניות האמיתיות של מרבית השרותים שמספקת הרשות המקומית ואת דעתן, הערכתן וצרכיהן של המשפחות הן יודעות יותר טוב מכל אחד אחר״. חודשיים מאוחר יותר פרסם מרכז השלטון המקומי חוזר מנכ״ל (מס\' 157) ובו נאמר כי ״שדולת הנשים בישראל פנתה להנהלת המרכז בבקשה להקים מועצות מקומיות של נשים ליד ראשי הרשויות המקומיות. ההנהלה המצומצמת דנה בבקשה והחליטה להמליץ בפני ראשי הרשויות להקים לידם מועצה ציבורית של נשים. המטרה היא להפעיל את הנשים בתחומי הרשות בנושאים המוניציפליים המתאימים כגון: פעולה קהילתית, נקיון העיר וכו״. בחודש יוני 1987, נערך בהרצליה כנס בנושא ״נשים בשלטון המקומי״ ובו הוקם צוות עבודה לקידום נשים בשלטון המקומי ונאספו שמות של נשים שהתעניינו בהשתלבות פעילה בפעילות הועדות של המרכז לשלטון מקומי. בינואר 1988 החלו להתקיים פעילויות הדרכה עבור מועמדות למועצות ציבוריות בשיתוף שדולת הנשים ומשרד הפנים. בשנת 1994, בתמיכה של יועצת ראש הממשלה למעמד האשה, מונתה יועצת לקידום מעמד האשה במרכז לשלטון המקומי שפעלה ביחד עם ועדה מלווה שהורכבה מנשים נבחרות ברשויות מקומיות שונות. הגדרת התפקיד המוקדמת של היועצת היתה לסייע בהקמת מועצות ציבוריות של נשים ברשויות המקומיות. מאוחר יותר, לאחר חקיקת חוק היועצות לקידום מעמד האשה ברשויות המקומיות (2000) החלה היחידה לעסוק בהכשרת יועצות, קידום נהלים ותקנות ברמה המוניציפלית ובמניעת הטרדה מינית בגופי השלטון המקומי.',
