    'description': '״התכנית ללימודי נשים בשילוב אמנויות״ נפתחה בשנת 1998 בשלוחת אוניברסיטת לסלי בישראל בנתניה. התכנית נבנתה כתכנית בין-תחומית לתואר שני עם עבודת מחקר (תזה) המבוססת על תפיסה של הוראה אקדמית בתחומי האמנות, הפמיניזם והמגדר תוך התנסות חוויתית. בתיאור התכנית נאמר כי היא ״משלבת תכנים תיאורטיים, ספרות ומחקרים עדכניים, יחד עם התנסות חווייתית בשילוב אמנויות, ככלים המאפשרים ביטוי ויצירה בתהליך ההתפתחות של האישה....יעד מרכזי בתכנית הינו הקניית ידע וכלים להתמודדות עם השינויים האישיים, המקצועיים והחברתיים הנוגעים לעולמן של נשים, והרחבת היכולת להוציא מהכוח אל הפועל תהליכים של שינוי תרבותי.״ בשנת 2010 בעקבות סגירת שלוחת אוניברסיטת לסלי בישראל, הפסיקה התכנית את פעילותה במתכונת המקורית והתמזגה לתוך התואר השני הכללי בחברה ואמנויות ב״מכללה האקדמית לחברה ואמנויות״ בנתניה.',
