    'description': 'תקציב המדינה לשנת 2003 כלל קיצוצים נרחבים בקצבאות הביטוח הלאומי ובמיוחד בקצבאות הילדים, הבטחת הכנסה ודמי מזונות. כתוצאה, פרצה מחאה חברתית אותה הובילו אמהות חד הוריות. הלחץ הציבורי שנוצר הביא לגיבוש ״התכנית לשילוב הורים יחידים בשוק העבודה״ אשר קיבלה תוקף של החלטת ממשלה (החלטה 757 מיום 28.8.2003). התכנית יועדה לאמהות חד הוריות שקיבלו הבטחת הכנסה או דמי מזונות וכללה בין היתר מענק כספי לאמהות חד הוריות שיגדילו את הכנסתן, הטבות למעסיקים, החזרי נסיעות וזכאות לקורסים ומסלולי הכשרה תוך כדי עבודה. התכנית הופעלה במתכונת ניסיונית עד 2005 והחל מ-2006 הופעלה כתכנית ארוכת טווח שכללה גם מרכיבים כגון: סבסוד מסגרות טיפול בילדים, מסגרות הכשרה מקצועית, עידוד יזמות, סיוע בניידות ומלגות לימודים. התכנית הופעלה על ידי משרד התמ״ת וכללה מספר תכניות משנה אשר אוחדו תחת מסגרת תקציבית אחת ״עידוד תעסוקת חד-הוריות״. ביקורת שערך מבקר המדינה על התכנית העלתה כי היא לא השיגה את יעדיה והתקציבים שהוקצו לה לא נוצלו.',
