    'description': '״תמך - קידום תעסוקה ומסחר בישראל״ נוסדה בשנת 2005 במטרה להכשיר וללוות נשים חרדיות למען קידומן בתעסוקה ובעסקים עצמאיים. מנכ״לית ״תמך״ משנת 2008 היא שייני באב״ד. בראיון היא מתארת שהיא הצטרפה לתמך ״כדי לשנות כיוון ולעסוק בתעסוקת נשים חרדיות.״ הארגון הוא ״גוף פילנתרופי״ בכך שאופן הפעולה של ‏הארגון הוא לא שירות השמה פרטינית עבור נשים, אלא יצירת שיתופי פעולה עם עסקים, הבנת הצרכים ‏שלהם מבחינת כ״א, ואז בניית קורסי הכשרה עבור נשים חרדיות בליווי צמוד של העסק ע״מ להכניס את ‏אותן הנשים לתוכו.‏',
